import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Nav from "../components/nav/topNav";
import { Grid, StylesProvider } from "@material-ui/core";
import SubHeaderTutor from "../components/nav/SubHeaderTutor";
import { makeStyles } from "@material-ui/core/styles";
import Calendar from "react-calendar";
import axios from "axios";
import "react-calendar/dist/Calendar.css";
import { GET_AVAILABILITY } from "../Constants/sagaActions";
import dateFormat from "dateformat";
import CircularProgress from "@material-ui/core/CircularProgress";
import TransparentSearchBar from "../components/SearchBar/TransparentSearchBar";
import LightStrokeIcon from "../assets/images/lightStrokeIcon.png";
import SideButtons from "../components/TutorAvailability/SideButtons";
import { Box } from "@material-ui/core";
import { schedule } from "../Data/Data";
import Footer from "../components/footer/footer";
import AvaibilityEdit from "./AvaibilityModel";
import baseUrl from "../baseurl";

const useStyles = makeStyles((theme) => ({
  messagesHeaderActive: {
    marginBottom: 0,
    color: "#FFB600",
    fontWeight: "bold",
    marginLeft: 20,
    paddingTop: 10,
    width: 40,
    cursor: "pointer",
    height: "100%",
    borderBottom: "4px solid #FFB600",
  },
  messagesHeader: {
    marginBottom: 0,
    cursor: "pointer",
    marginLeft: 20,
    paddingTop: 10,
    // fontWeight: "bold",
    color: "gray",
    width: 40,
    height: "100%",
  },
  messagePic: {
    width: 45,
    height: 45,
    borderRadius: 25,
  },

  button: {
    background: "#FFB600",
    border: "none",
    color: "white",
    padding: 10,
    marginTop: 20,
    width: "80%",
    borderRadius: 2,
  },
  arrowButton: {
    backgroundColor: "white",
    width: 30,
    height: 30,
    borderRadius: 2,
    border: "1px solid gray",
    fontWeight: "bolder",
  },
  disabledButton: {
    padding: 5,
    border: "1px solid #D4D8DB",
    borderRadius: 3,
    color: "#D4D8DB",
    marginBottom: 0,
    width: 150,
  },
  yellowBorder: {
    borderBottom: "1px solid black",
    marginBottom: 0,
    width: 150,
    cursor: "pointer",
  },
  yellowBorderActive: {
    borderBottom: "3px solid #FFB600",
    marginBottom: 0,
    cursor: "pointer",
    width: 150,
  },
  scheduleBox: {
    borderRight: "1px solid #F2F2F2",
    height: 30,
    paddingTop: 10,
  },
  scheduleBoxFilled: {
    borderRight: "1px solid #F2F2F2",
    background: "#FFB600",
    paddingBottom: "0.3rem",
    paddingTop: "0.3rem",
    marginTop: 20,
  },
}));
let daysArray = [
  "Monday",
  "Tuesday",
  "Wednesday",
  "Thursday",
  "Friday",
  "Saturday",
  "Sunday",
];
export default function TutorAvailability() {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [activeMessage, setActiveMessage] = React.useState("All");
  const [date, setDate] = React.useState(new Date());
  const [avaibility, setAvaibility] = React.useState([]);
  const [warningText, setWarningText] = React.useState("");
  const [showMessage, setShowMessage] = React.useState(false);
  const [showModel, setShowModel] = React.useState(false);
  const onChange = (date) => setDate(date);
  console.log(date.getDate());
  const dayNames = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];
  var weekDays = [];
  var one = 1;

  const gettingAvailability = useSelector(
    (state) => state.tutorAvailability.gettingAvailability
  );
  const availability = useSelector(
    (state) => state.tutorAvailability.availability
  );

  const getAvaibility = async () => {
    let token = await localStorage.getItem("yellowToken");
    const config = {
      headers: {
        Authorization: token,
      },
    };

    const bodyParameters = {
      key: "value",
    };
    let getSlots = [];

    axios
      .get(`${baseUrl}/api/tutors/get-availability`, config)
      .then((data) => {
        if (data.data.data.length === 0) {
          setWarningText("No data");
        }

        let newData = data.data.data.availabilty.map((data) => {
          data.slots.map((slot) => {
            if (data.day === "Monday") {
              slot.sol = [true, false, false, false, false, false, false];
              slot.day = "Monday";
            }
            if (data.day === "Tuesday") {
              slot.sol = [false, true, false, false, false, false, false];
              slot.day = "Tuesday";
            }
            if (data.day === "Wednesday") {
              slot.sol = [false, false, true, false, false, false, false];
              slot.day = "Wednesday";
            }
            if (data.day === "Thursday") {
              slot.sol = [false, false, false, true, false, false, false];
              slot.day = "Thursday";
            }
            if (data.day === "Friday") {
              slot.sol = [false, false, false, false, true, false, false];
              slot.day = "Friday";
            }
            if (data.day === "Saturday") {
              slot.sol = [false, false, false, false, false, true, false];
              slot.day = "Saturday";
            }
            if (data.day === "Sunday") {
              slot.sol = [false, false, false, false, false, false, true];
              slot.day = "Sunday";
            }
            getSlots = [...getSlots, slot];
            return slot;
          });
          return data;
        });
        console.log("This is all new res data slotsss ::: ", getSlots);
        setAvaibility(getSlots);
      })
      .catch((e) => {
        console.log("This is an error :: ", e);
      })
      .catch((e) => {
        setWarningText("No data");
      });
  };

  useEffect(() => {
    getAvaibility();
    dispatch({ type: GET_AVAILABILITY });
  }, []);

  const changeShowModel = () => {
    setShowModel(!showModel);
  };

  return (
    <div>
      <Nav></Nav>
      <AvaibilityEdit
        show={showModel}
        changeShow={changeShowModel}
        getAllAvaibilities={getAvaibility}
      />
      <SubHeaderTutor for="TutorAvailability"></SubHeaderTutor>
      <Grid container style={{ height: "100vh", background: "#EDEFF0" }}>
        <Grid
          item
          md={3}
          lg={3}
          xl={3}
          sm={3}
          xs={3}
          style={{ borderRight: "1px solid #F2F2F2", marginTop: 15 }}
        >
          <Grid container direction="column" alignItems="center">
            <button
              style={{
                textAlign: "left",
                width: "60%",
                paddingLeft: 10,
                marginBottom: "1rem",
                border: "1px solid #F2F2F2",
                background: "#FFB600",
                color: "white",
                textAlign: "center",
                height: 50,
                fontSize: 18,
              }}
              onClick={() => changeShowModel()}
            >
              Update Availability
            </button>
            <SideButtons for="Availability"></SideButtons>
          </Grid>
        </Grid>
        <Grid item md={9} lg={9} xl={9} sm={9} xs={9} style={{ marginTop: 15 }}>
          {/* <Grid
            container
            direction='row'
            alignItems='center'
            style={{
              borderBottom: "1px solid #F2F2F2",
              background: "#FFB600",
              color: "white",
              height: 50,
              fontSize: 20,
              paddingLeft: 15,
            }}>
            Update Time Slots
          </Grid> */}
          <Grid
            container
            direction="row"
            alignItems="center"
            style={{
              borderBottom: "1px solid #F2F2F2",
              background: "white",
              height: 50,
              fontSize: 20,
              paddingLeft: 15,
            }}
          >
            Availability Time Slots
          </Grid>

          <Grid
            container
            direction="row"
            alignItems="center"
            style={{
              borderBottom: "1px solid #F2F2F2",
              background: "white",
              height: 50,
            }}
          >
            <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
            <Grid item md={12} lg={12} xl={12} sm={12} xs={12}>
              <Box
                display="flex"
                flexDirection="row"
                alignItems="center"
                style={{
                  borderBottom: "1px solid #F2F2F2",
                  background: "white",
                  height: 50,
                }}
              >
                {dayNames.map((day) => {
                  return <Box flex={1}>{day}</Box>;
                })}
              </Box>
            </Grid>
            <Grid item md={12} lg={12} xl={12} sm={12} xs={12}>
              <Box
                display="flex"
                flexDirection="row"
                alignItems="center"
                style={{
                  backgroundColor: "white",
                  padding: "1rem",
                  overflow: "scroll",
                  height: "80vh",
                }}
              >
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Monday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Tuesday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Wednesday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Thursday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Friday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Saturday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
                <Box flex={1}>
                  {avaibility.map((time) => {
                    if (time.day === "Sunday") {
                      return (
                        <div className={classes.scheduleBoxFilled}>
                          <span style={{ color: "white" }}>
                            {time.startTime.substring(0, 5)}
                            {time.startTime.substring(8, 12)} -{" "}
                            {time.endTime.substring(0, 5)}
                            {time.endTime.substring(8, 12)}
                          </span>
                        </div>
                      );
                    } else {
                      return;
                    }
                  })}
                </Box>
              </Box>
            </Grid>
          </Grid>

          {avaibility.length === 0 && warningText === "" ? (
            <CircularProgress style={{ color: "#FFB600" }} />
          ) : null}
          {avaibility.length === 0 && warningText !== "" ? (
            <Grid
              container
              direction="row"
              alignItems="center"
              style={{
                borderBottom: "1px solid #F2F2F2",
                background: "white",
                height: 90,
              }}
            >
              <Grid item md={1} lg={1} xl={1} sm={1} xs={1}>
                <p>{warningText}</p>
              </Grid>
            </Grid>
          ) : null}
        </Grid>
      </Grid>

      <div>
        <Footer />
      </div>
    </div>
  );
}
