import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Grid, Box } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import NavBar from "../../components/nav/topNav";
import axios from "axios";
import Footer from "../../components/footer/footer";
import GoogleSquare from "../../assets/images/googleSquare.png";
import { LOGIN } from "../../Constants/sagaActions";
import { GoogleLogin } from "react-google-login";
import FacebookSquare from "../../assets/images/facebookSquare.png";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import baseUrl from "../../baseurl";
import { Link } from "react-router-dom";
import { loginPage } from "./translation";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

const Login = ({ history }) => {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);
  const [email, setEmail] = React.useState("");
  const dispatch = useDispatch();
  const [values, setValues] = useState({ email: "", password: "" });
  const [error, setError] = useState(null);
  const [showError, setShowError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [emailLoading, setEmailLoading] = useState(false);

  const loggingIn = useSelector((state) => state.auth.loggingIn);
  const user = useSelector((state) => state.auth.user);
  const language = useSelector((state) => state.languageReducer.language);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id="simple-modal-title">Reset Password</h2>
      <p style={{ textAlign: "left" }}>Email*</p>
      <input
        style={{ width: "100%", height: 45, padding: 10 }}
        value={email}
        placeholder="Enter email"
        onChange={(e) => setEmail(e.target.value)}
        type="text"
      ></input>
      <Box display="flex">
        <Box flex={1}></Box>
        <Button
          onClick={() => handleResetPassword()}
          style={{
            color: "white",
            backgroundColor: "#ffcd02",
            marginTop: "1rem",
          }}
        >
          {emailLoading ? <CircularProgress size={25} /> : "Get Link"}
        </Button>
      </Box>
    </div>
  );

  const handleResetPassword = () => {
    setShowError(false);
    setEmailLoading(true);
    axios({
      method: "post",
      url: `${baseUrl}/api/auth/emailforgotpassword`,
      data: { email: email },
    })
      .then((da) => {
        setShowSuccessMessage(true);
        setSuccessMessage("Password reset link send to your email.");
        setEmailLoading(false);
        setOpen(false);
      })
      .catch((e) => {
        setErrorMessage("Password not updated");
        setOpen(false);
        showError(true);
        setEmailLoading(false);
      });
  };

  const login = (e) => {
    e.preventDefault();
    setShowError(false);
    setLoading(true);
    const { email, password } = values;
    if (email === "" || password === "") {
      setErrorMessage(loginPage[6][language]);
      setShowError(true);
      setLoading(false);
      return;
    }
    // 13.112.227.238
    axios({
      method: "post",
      url: `${baseUrl}/api/auth/common`,
      data: { email: email, password: password },
    })
      .then(async (result) => {
        console.log("resultsssssss :: ", result);
        if (result.data.isConfirm == true) {
          if (result.data.user.isBlocked === true) {
            handleOpen();
            setLoading(false);
            return;
          } else {
            //handle success
            await localStorage.setItem(
              "yellowToken",
              result.data.Authorization
            );
            await localStorage.setItem("role", result.data.user.role);

            if (result.data.user.role === "student") {
              await localStorage.setItem(
                "yellowStudent",
                JSON.stringify(result.data.user)
              );
              history.push("/requestTutorHome");
            }
            if (result.data.user.role === "tutor") {
              await localStorage.setItem(
                "yellowTutor",
                JSON.stringify(result.data.user)
              );
              history.push("/dashboard");
            }
          }
        } else {
          setErrorMessage("Please confirm your email first");
          setShowError(true);
          setLoading(false);
        }
      })
      .catch(function (err) {
        //handle error
        console.log("This is eerroorr loggiinn :: ", err);
        setErrorMessage("Email/Password incorrect");
        setShowError(true);
        setLoading(false);
      });
  };

  const loginFacebook = (emailF, passwordF) => {
    setShowError(false);
    setLoading(true);
    // 13.112.227.238
    axios({
      method: "post",
      url: `${baseUrl}/api/auth/common`,
      data: { email: emailF, password: passwordF },
    })
      .then(async (result) => {
        console.log("resultsssssss :: ", result);
        //handle success
        await localStorage.setItem("yellowToken", result.data.Authorization);
        await localStorage.setItem("role", result.data.user.role);

        if (result.data.user.role === "student") {
          await localStorage.setItem(
            "yellowStudent",
            JSON.stringify(result.data.user)
          );
          history.push("/requestTutorHome");
        }
        if (result.data.user.role === "tutor") {
          await localStorage.setItem(
            "yellowTutor",
            JSON.stringify(result.data.user)
          );
          history.push("/dashboard");
        }
        console.log("llooggiinnnnn :: ", result);
      })
      .catch(function (response) {
        //handle error
        setErrorMessage("Email/Password incorrect");
        setShowError(true);
        setLoading(false);
      });
  };

  const loginGoogle = (emailg, passwordg) => {
    setShowError(false);
    setLoading(true);

    axios({
      method: "post",
      url: `${baseUrl}/api/tutors/login`,
      data: { email: emailg, password: passwordg },
    })
      .then(async (result) => {
        //handle success
        await localStorage.setItem("yellowToken", result.data.Authorization);
        await localStorage.setItem(
          "yellowTutor",
          JSON.stringify(result.data.user)
        );
        console.log("Thsis iskjahdskjas user name :: ", result.data.user.role);
        // history.push("/dashboard");
      })
      .catch(function (response) {
        //handle error
        setErrorMessage("Email associated with the account is not registered");
        setShowError(true);
        setLoading(false);
      });
  };

  const responseFacebook = (response) => {
    console.log("Response facebookk apii :: ", response);
    axios({
      method: "post",
      url: `${baseUrl}/api/auth/facebookauth`,
      data: { accessToken: response.accessToken, userID: response.userID },
    })
      .then(async (res) => {
        await localStorage.setItem("yellowToken", res.data.Authorization);
        await localStorage.setItem("role", "student");
        await localStorage.setItem(
          "yellowStudent",
          JSON.stringify(res.data.user)
        );

        history.push("/requestTutorHome");
      })
      .catch((err) => console.log("Error gogole login :: ", err));
  };

  const onChangeValues = (e) => {
    const { value, name } = e.target;
    const newValues = { ...values };
    newValues[name] = name === "password" ? value : value.toLowerCase();
    setValues(newValues);
  };

  const responseGoogle = (response) => {
    console.log("This is response of googleee :: ", response.tokenId);
    axios({
      method: "post",
      url: `${baseUrl}/api/auth/googleauth`,
      data: { token: response.tokenId },
    })
      .then(async (response) => {
        await localStorage.setItem("yellowToken", response.data.Authorization);
        await localStorage.setItem("role", "student");
        await localStorage.setItem(
          "yellowStudent",
          JSON.stringify(response.data.user)
        );
        console.log("This is response google login :: ", response.data);
        history.push("/requestTutorHome");
      })
      .catch((err) => console.log("Error gogole login :: ", err));
    // loginFacebook(response.wt.cu, "123456");
  };

  const responseGoogleError = (response) => {
    console.log("This is google eror ::", response);
  };

  useEffect(() => {
    if (JSON.parse(localStorage.getItem("yellowTutor"))) {
      history.push("/dashboard");
    }
  }, []);

  return (
    <div>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
      <NavBar />
      <div style={{ height: "100vh" }}>
        <Grid container style={{ marginTop: "20px" }}>
          <Grid item md={4} lg={4} xl={4} sm={2} xs={2}></Grid>
          <Grid item md={4} lg={4} xl={4} sm={8} xs={8}>
            {showError ? <p style={{ color: "red" }}>{errorMessage}</p> : null}
            {showSuccessMessage ? (
              <p style={{ color: "green" }}>{successMessage}</p>
            ) : null}
            <form onSubmit={login} style={{ marginBottom: "1rem" }}>
              <p style={{ textAlign: "left" }}>
                {/* Email address* */}
                {loginPage[0][language]}*
              </p>
              <input
                style={{ width: "100%", height: 45, padding: 10 }}
                value={values["email"]}
                name="email"
                onChange={onChangeValues}
                type="text"
              ></input>
              <p style={{ textAlign: "left" }}>
                {/* Password* */}
                {loginPage[1][language]}*
              </p>
              <input
                style={{ width: "100%", height: 45, padding: 10 }}
                name="password"
                value={values["password"]}
                onChange={onChangeValues}
                type="password"
              ></input>
              <Grid
                container
                direction="row"
                alignItems="center"
                justify="center"
                style={{ background: "#FFB600", marginTop: 15 }}
              >
                <Button
                  type="submit"
                  style={{
                    height: "100%",
                    width: "100%",
                    padding: "10px",
                    background: "none",
                    border: "none",
                  }}
                >
                  {loading ? (
                    <CircularProgress size={25} />
                  ) : (
                    loginPage[2][language]
                  )}
                </Button>
              </Grid>
              <div style={{ display: "flex", justifyContent: "flex-end" }}>
                <Link to="#" onClick={() => setOpen(true)}>
                  {/* Forgot Password */}
                  {loginPage[3][language]}
                </Link>
              </div>
            </form>

            {/* <FacebookLogin
              appId="225838649197070"
              fields="name,email"
              autoLoad={false}
              callback={responseFacebook}
              render={(renderProps) => (
                <Grid
                  onClick={() => renderProps.onClick()}
                  container
                  direction="row"
                  alignItems="center"
                  style={{
                    background: "#3D5188",
                    cursor: "pointer",
                    marginBottom: "1rem",
                  }}
                >
                  <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                    <Grid container direction="column" alignItems="flex-start">
                      <img style={{ margin: 10 }} src={FacebookSquare}></img>
                    </Grid>
                  </Grid>
                  <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                    <Grid container direction="column" alignItems="flex-start">
                      <span
                        style={{
                          color: "white",
                        }}
                      >
                        Sign in with Facebook
                        {loginPage[4][language]}
                      </span>
                    </Grid>
                  </Grid>
                </Grid>
              )}
            /> */}

            {/* <GoogleLogin
              clientId="757414594228-5rd7q8mroi7d1sv1jchmsnspjs6piagm.apps.googleusercontent.com"
              autoLoad={false}
              render={(renderProps) => (
                <Grid
                  container
                  direction="row"
                  alignItems="center"
                  style={{ background: "#4285F4", cursor: "pointer" }}
                  onClick={renderProps.onClick}
                >
                  <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                    <Grid container direction="column" alignItems="flex-start">
                      <img style={{ margin: 10 }} src={GoogleSquare}></img>
                    </Grid>
                  </Grid>
                  <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                    <Grid container direction="column" alignItems="flex-start">
                      <span
                        style={{
                          color: "white",
                        }}
                      >
                    Sign in with Google 
                        {loginPage[5][language]}
                      </span>
                    </Grid>
                  </Grid>
                </Grid>
              )}
              buttonText="Login"
              onSuccess={responseGoogle}
              onFailure={responseGoogleError}
              cookiePolicy={"single_host_origin"}
            /> */}
          </Grid>
          <Grid item md={4} lg={4} xl={4} sm={2} xs={2}></Grid>
        </Grid>
      </div>
      <div style={{}}>
        <Footer />
      </div>
    </div>
  );
};

export default Login;
