import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import Nav from "../../components/nav/topNavStudent";
import { Grid } from "@material-ui/core";
import SubHeaderTutor from "../../components/nav/SubHeaderStudent";
import AccountCircle from "@material-ui/icons/AccountCircle";
import BusIcon from "../../assets/images/busCircle.png";
import ArchiveIcon from "@material-ui/icons/Archive";
import { makeStyles } from "@material-ui/core/styles";
import MessageCircle from "../../assets/images/messageCircle.png";
import ListCircle from "../../assets/images/listCircle.png";
import LogoutCircle from "../../assets/images/logoutCircle.png";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import CircularProgress from "@material-ui/core/CircularProgress";
import Footer from "../../components/footer/footer";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import TutorLessonTable from "./studentLessonTable";
import { myLessTrans } from "./translation";
import axios from "axios";
import baseUrl from "../../baseurl";

const useStyles = makeStyles((theme) => ({
  name: {
    fontSize: 14,
    marginLeft: 15,
  },
  tableHeader: {
    fontSize: 12,
  },
  button: {
    background: "#FFB600",
    border: "none",
    color: "white",
    padding: 5,
    paddingLeft: 10,
    width: 150,
    marginLeft: 25,
    borderRadius: 2,
  },
  iconsImages: {
    marginLeft: "5%",
  },
}));

export default function TutorMyLessonsPage() {
  const classes = useStyles();
  const dispatch = useDispatch();
  const [studentLessons, setStudentLessons] = useState([]);
  const [loading, setLoading] = useState(true);
  const [noData, setNodata] = useState(false);
  const language = useSelector((state) => state.languageReducer.language);

  const getData = async () => {
    let token = await localStorage.getItem("yellowToken");
    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowStudent");
    let newStu = await JSON.parse(stu);
    axios
      .get(
        `${baseUrl}/api/lessonRequest/lessonByStudentId/${newStu._id}`,
        config
      )
      .then((data) => {
        console.log("ajhsfdgjhasgdsajh 12:: ", data);
        setStudentLessons(data.data.data);

        setLoading(false);
      })
      .catch((e) => {
        setLoading(false);
      });
  };
  useEffect(() => {
    getData();
  }, []);

  return (
    <div>
      <Nav></Nav>
      <SubHeaderTutor for="My Lessons"></SubHeaderTutor>
      <Grid
        container
        style={{
          background: "#EDEFF0",
          paddingBottom: "2rem",
        }}
      >
        <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
          {/* <Grid
            container
            direction="row"
            alignItems="center"
            style={{
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
              height: 70,
              background: "white",
              borderRadius: 2,
              marginTop: 30,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            <Grid item md={7} lg={7} xl={7} sm={7} xs={7}>
              <Grid container direction="row" alignItems="flex-start">
                <span>Next lesson: Wednesday, 27 May at 11:00 with</span>
                <AccountCircle
                  style={{ color: "#E9E6E3", marginLeft: 15 }}
                ></AccountCircle>
                <span style={{ marginLeft: 10 }}>TNDRS A.</span>
              </Grid>
            </Grid>
            <Grid item md={5} lg={5} xl={5} sm={5} xs={5}>
              <Grid
                container
                direction="row"
                justify="flex-end"
                alignItems="center"
              >
                <img src={BusIcon} style={{ width: 45, height: 45 }}></img>
                <span style={{ color: "#FFB600" }}>Enter Classroom</span>
              </Grid>
            </Grid>
            <div style={{ width: "35%" }}> </div>
          </Grid> */}
          <div
            style={{
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
              background: "white",
            }}
          >
            <Grid
              container
              direction="row"
              alignItems="center"
              style={{
                height: 60,
                borderRadius: 2,
                marginTop: 30,
                paddingLeft: 20,
                borderBottom: "1px solid #AAB5BC",
                paddingRight: 20,
              }}
            >
              <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                <Grid container direction="column" alignItems="flex-start">
                  <span style={{ fontSize: 18 }}>
                    {/* My lessons */}
                    {myLessTrans[0][language]}
                  </span>
                </Grid>
              </Grid>
              <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                <Grid
                  container
                  direction="row"
                  alignItems="center"
                  justify="flex-end"
                >
                  {/* <ArchiveIcon style={{ color: "gray" }}></ArchiveIcon>
                  <span>Archived lessons</span> */}
                </Grid>
              </Grid>
            </Grid>

            {studentLessons.length > 0 ? (
              <TutorLessonTable
                lessons={studentLessons}
                noData={noData}
                getData={getData}
              />
            ) : null}
            {loading ? (
              <CircularProgress
                size={25}
                style={{ marginTop: "10px", color: "#ffcd02" }}
              />
            ) : loading === false && studentLessons.length == 0 ? (
              <p>Lessons not found</p>
            ) : null}
          </div>
        </Grid>
        <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
      </Grid>
      {loading && <div style={{ height: "50vh", background: "#EDEFF0" }}></div>}
      <div>
        <Footer />
      </div>
    </div>
  );
}
