import React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Globe from '../../assets/images/globe.png';
import WireGlobe from '../../assets/images/wireGlobe.png';
import Medal from '../../assets/images/medalIcon.png';
import CertifiedIcon from '../../assets/images/certifiedIcon.png';
import ProfileIcon from '../../assets/images/profileIcon.png';
import CircleIcon from '../../assets/images/circleIcon.png';
import ScholarCapIcon from '../../assets/images/scholarCapIcon.png';
import BookmarkIcon from '../../assets/images/bookmarkIcon.png';
import LocationIcon from '../../assets/images/locationIcon.png';
import ClockIcon from '../../assets/images/clockIcon.png';
import LightStrokeIcon from '../../assets/images/lightStrokeIcon.png';
import WhiteClockIcon from '../../assets/images/whiteClockIcon.png';
import { Link, useHistory } from 'react-router-dom';
import { landingPageFocusSkill } from '../../Translation/index';
import { landingComfortZone } from './translation';
import { useSelector } from 'react-redux';
const useStyles = makeStyles((theme) => ({
  centralCard: {
    marginTop: 50,
    background: 'white',
    borderRadius: 5,
  },
  globeImage: {
    width: 55,
    height: 55,
    marginTop: -20,
  },
  boldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 'bold',
    [theme.breakpoints.down('sm')]: {
      fontSize: 20,
      marginTop: -1,
    },
    [theme.breakpoints.down('xs')]: {
      fontSize: 16,
    },
  },
  semiBoldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 500,
    [theme.breakpoints.down('sm')]: {
      fontSize: 20,
      marginTop: -1,
    },
    [theme.breakpoints.down('xs')]: {
      fontSize: 16,
    },
  },
  wireGlobeImage: {
    width: '80%',
  },
  focusSkillsUpline: {
    fontSize: 14,
    [theme.breakpoints.down('sm')]: {
      fontSize: 12,
    },
  },
  focusSkillsDownline: {
    fontSize: 13,
    marginTop: -5,
    [theme.breakpoints.down('sm')]: {
      fontSize: 10,
    },
  },
  focusSkillsImages: {
    width: 65,
    height: 65,
    marginTop: 1,
    marginLeft: 50,
    [theme.breakpoints.down('sm')]: {
      width: 45,
      height: 45,
      marginLeft: 30,
    },
  },
  yellowButton: {
    padding: 10,
    cursor: 'pointer',
    color: 'white',
    background: '#FFB600',
    borderRadius: 5,
  },
  simpleText: {
    marginTop: -20,
    [theme.breakpoints.down('xs')]: {
      fontSize: 11,
    },
  },
  textUnderGlobe: {
    [theme.breakpoints.down('sm')]: {
      fontSize: 13,
    },
  },
  textUnderGlobeDown: {
    padding: '1rem',
    [theme.breakpoints.down('sm')]: {
      fontSize: 10,
    },
  },
}));

export default function LanguagesCard() {
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);
  return (
    <div style={{ background: '#EDEFF0' }}>
      <Grid container>
        <Grid item md={1} lg={1} xl={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={12}
          xs={13}
          style={{ margin: '1rem' }}
        >
          <div className={classes.centralCard}>
            <img className={classes.globeImage} src={Globe}></img>
            <p className={classes.boldText}>
              {/* Make the world your comfort zone */}
              {landingComfortZone[0][language]}
            </p>
            <p className={classes.simpleText}>
              {/* Speak naturally with professional online tutors from all over the
              world. */}
              {landingComfortZone[1][language]}
            </p>
            <img className={classes.wireGlobeImage} src={WireGlobe}></img>
            <Grid container style={{ marginTop: 25 }}>
              <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                <img src={Medal}></img>
                <p className={classes.textUnderGlobe}>
                  {/* Expert tutors */}
                  {landingComfortZone[2][language]}
                </p>
                <p className={classes.textUnderGlobeDown}>
                  {/* Find native speakers and certified private tutors */}
                  {landingComfortZone[3][language]}
                </p>
              </Grid>
              <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                <img src={CertifiedIcon}></img>
                <p className={classes.textUnderGlobe}>
                  {/* Verified profiles */}
                  {landingComfortZone[4][language]}
                </p>
                <p className={classes.textUnderGlobeDown}>
                  {/* We carefully check and confirm each tutor's profile */}
                  {landingComfortZone[7][language]}
                </p>
              </Grid>
              <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                <img src={ProfileIcon}></img>
                <p className={classes.textUnderGlobe}>
                  {/* Learn anytime */}
                  {landingComfortZone[5][language]}
                </p>
                <p className={classes.textUnderGlobeDown}>
                  {/* Take online lessons at the perfect time for your busy
                  schedule. */}
                  {landingComfortZone[6][language]}
                </p>
              </Grid>
              <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                <img src={CircleIcon}></img>
                <p className={classes.textUnderGlobe}>
                  {/* Affordable prices */}
                  {landingComfortZone[8][language]}
                </p>
                <p className={classes.textUnderGlobeDown}>
                  {/* Choose an experienced tutor that fits your budget */}
                  {landingComfortZone[9][language]}
                </p>
              </Grid>
            </Grid>
            {/* <img style={{ marginTop: 25 }} src={ScholarCapIcon}></img> */}
            <p className={classes.semiBoldText}>
              {/* Focus on the skills you need */}
              {/* {landingPageFocusSkill[0][language]} */}
            </p>
            <p className={classes.simpleText}>
              {/* Prepare to achieve your goals with private tutors */}
              {/* {landingPageFocusSkill[1][language]} */}
            </p>
            <Grid container style={{ marginTop: 25 }}>
              <Grid item md={6} lg={6} xl={6}>
                <Grid
                  container
                  direction='row'
                  style={{ marginLeft: 15, marginTop: 10 }}
                >
                  {/* <img
                    className={classes.focusSkillsImages}
                    src={BookmarkIcon}
                  ></img> */}
                  <div style={{ textAlign: 'left', marginLeft: 10 }}>
                    <p className={classes.focusSkillsUpline}>
                      {/* Immerse yourself in a new culture */}
                      {/* {landingPageFocusSkill[2][language]} */}
                    </p>
                    <p className={classes.focusSkillsDownline}>
                      {/* Connect with language experts from around the world */}
                      {/* {landingPageFocusSkill[3][language]} */}
                    </p>
                  </div>
                </Grid>
              </Grid>
              <Grid item md={6} lg={6} xl={6}>
                <Grid
                  container
                  direction='row'
                  style={{ marginLeft: 15, marginTop: 10 }}
                >
                  {/* <img
                    className={classes.focusSkillsImages}
                    src={LightStrokeIcon}
                  ></img> */}
                  <div style={{ textAlign: 'left', marginLeft: 10 }}>
                    <p className={classes.focusSkillsUpline}>
                      {/* Immerse yourself in a new culture */}
                      {/* {landingPageFocusSkill[2][language]} */}
                    </p>
                    <p className={classes.focusSkillsDownline}>
                      {/* Connect with language experts from around the world */}
                      {/* {landingPageFocusSkill[3][language]} */}
                    </p>
                  </div>
                </Grid>
              </Grid>
              <Grid item md={6} lg={6} xl={6}>
                <Grid
                  container
                  direction='row'
                  style={{ marginLeft: 15, marginTop: 10 }}
                >
                  {/* <img
                    className={classes.focusSkillsImages}
                    src={ClockIcon}
                  ></img> */}
                  <div style={{ textAlign: 'left', marginLeft: 10 }}>
                    <p className={classes.focusSkillsUpline}>
                      {/* Immerse yourself in a new culture */}
                      {/* {landingPageFocusSkill[2][language]} */}
                    </p>
                    <p className={classes.focusSkillsDownline}>
                      {/* Connect with language experts from around the world */}
                      {/* {landingPageFocusSkill[3][language]} */}
                    </p>
                  </div>
                </Grid>
              </Grid>
              <Grid item md={6} lg={6} xl={6}>
                <Grid
                  container
                  direction='row'
                  style={{ marginLeft: 15, marginTop: 10 }}
                >
                  {/* <img
                    className={classes.focusSkillsImages}
                    src={LocationIcon}
                  ></img> */}
                  <div style={{ textAlign: 'left', marginLeft: 10 }}>
                    <p className={classes.focusSkillsUpline}>
                      {/* Immerse yourself in a new culture */}
                      {/* {landingPageFocusSkill[2][language]} */}
                    </p>
                    <p className={classes.focusSkillsDownline}>
                      {/* Connect with language experts from around the world */}
                      {/* {landingPageFocusSkill[3][language]} */}
                    </p>
                  </div>
                </Grid>
              </Grid>
            </Grid>
            <img src={WhiteClockIcon}></img>
            <p className={classes.semiBoldText}>
              {/* Request a private tutor */}
              {landingPageFocusSkill[4][language]}
            </p>
            <p style={{ marginBottom: 30 }}>
              {/* Post your requirements and let tutors find you */}
              {landingPageFocusSkill[5][language]}
            </p>

            <span className={classes.yellowButton}>
              <Link to='/postrequest' style={{ color: 'white' }}>
                {' '}
                {/* Post request */}
                {landingPageFocusSkill[6][language]}
              </Link>
            </span>
            <p style={{ height: 30 }}></p>
          </div>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
}
