import React, { useState } from "react";
import NavBar from "../components/nav/topNav";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Footer from "../components/footer/footer";
import SideButtons from "../components/Settings/SideButtons";

const useStyles = makeStyles((theme) => ({
  TextLabelLarge: {
    fontSize: 35,
  },
  card: {
    background: "white",
    marginBottom: 20,
    borderRadius: 5,
    minHeight: 300,
  },
  links: {
    marginBottom: 0,
    marginLeft: 10,
    color: "white",
    cursor: "pointer",
  },
  buttons: {
    border: "none",
    borderRadius: 2,
    padding: 10,
    width: 130,
    textAlign: "left",
  },
  saveButton: {
    background: "#FFB600",
    color: "white",
    border: "none",
    borderRadius: 2,
    padding: 10,
    width: 130,
  },
  deletButton: {
    border: "1px solid #F8463D",
    borderRadius: 2,
    color: "#F8463D",
    marginLeft: 20,
    cursor: "pointer",
    padding: 10,
    width: 130,
  },
  buttonsFilled: {
    background: "rgba(0,0,0,0.2)",
    border: "none",
    borderRadius: 2,
    textAlign: "left",
    padding: 10,
    width: 130,
  },
  textField: {
    width: "90%",
    borderRadius: 2,
    height: 40,
    border: "1px solid gray",
    marginRight: 20,
  },
  dataFieldContainer: {
    marginTop: 10,
  },
  textLeft: {
    textAlign: "left",
    paddingLeft: 20,
  },
  yellowTextButton: { color: "#FFB600", cursor: "pointer" },
}));

export default function Settings() {
  const classes = useStyles();
  const [avatar, setAvatar] = useState(null);

  return (
    <div>
      <NavBar></NavBar>
      <div style={{ background: "#384047" }}>
        <Grid
          container
          direction="row"
          style={{ height: 50 }}
          alignItems="center"
          justify="flex-start"
        >
          <p className={classes.links}>My Requests</p>
          <p className={classes.links}>My Lessons</p>
          <p className={classes.links}>Settings</p>
        </Grid>
      </div>
      <div style={{ background: "#EDEFF0" }}>
        <Grid container>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
          <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
            <Grid container style={{ marginTop: 20, marginBottom: 20 }}>
              <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
                <SideButtons for="Autoconfirmation"></SideButtons>
              </Grid>
              <Grid item md={9} lg={9} xl={9} sm={9} xs={9}>
                <div className={classes.card}>
                  <Grid container style={{ borderBottom: "1px solid #F2F2F2" }}>
                    <p
                      style={{
                        textAlign: "left",
                        padding: 10,
                        fontSize: 22,
                      }}
                    >
                      Lesson autoconfirmation
                    </p>
                  </Grid>
                  <Grid container>
                    <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
                      <p
                        style={{
                          textAlign: "left",
                          marginLeft: 20,
                          fontSize: 25,
                        }}
                      >
                        Save a card
                      </p>
                    </Grid>
                    <Grid item md={6} lg={6} xl={6} sm={12} xs={12}></Grid>
                  </Grid>
                  <Grid container>
                    <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
                      <p
                        style={{
                          textAlign: "left",
                          marginLeft: 20,
                          marginRight: 10,
                          fontSize: 16,
                        }}
                      >
                        Lessons will be confirmed automatically 72 hours after
                        the scheduled end time unless you report an issue.When a
                        lesson is confirmed, we release payment to your tutor.
                      </p>
                    </Grid>
                    <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
                      <p
                        style={{
                          textAlign: "justify",
                          marginRight: 20,
                          fontSize: 16,
                        }}
                      >
                        Autoconfirmation for lessons in the Yellow School Space
                        Autoconfirm tracks the time you spend connected with
                        your tutor in the Yellow School Space. 15 minutes after
                        a lesson is completed, it is confirmed automatically and
                        we release your tutor's payment. If there are any
                        issues, please contact us right after the lesson and
                        we'll help you find a solution.
                      </p>
                    </Grid>
                  </Grid>
                </div>
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
        </Grid>
      </div>
      <Footer></Footer>
    </div>
  );
}
