import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Master from '../../assets/m2.png'
import American from '../../assets/a2.png'
import Visa from '../../assets/v2.png'
import Discover from '../../assets/d2.png'
import { Grid, Box, TextField, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    card: {
        background: "white",
        margin: '1rem',
        marginTop: 0,
        borderRadius: 5,
      },
      pass: {
          color: '#384047',
          fontSize: 35,
          textAlign: 'left',
          padding: '1rem',
          paddingLeft: '2rem',
          borderBottomStyle: 'solid',
          borderBottomWidth: 1,
          borderBottomColor: '#DADFE1'
      },
      stc: {
        color: '#384047',
        fontSize: 23,
        textAlign: 'left',
    },
    stcp: {
color: '#6F757B',
fontSize: 17,
textAlign: 'left',
    },
    logos: {
        textAlign: 'left',
    },
    button: {
        backgroundColor: '#FFB600',
        color: 'white',
    },
    updateBill: {
        color: '#FFB600',
        fontSize: 17,
        padding: '2rem',
        paddingRight: '2rem',
        paddingBottom: '1rem',
        borderBottomStyle: 'solid',
        borderBottomWidth: 1,
        borderBottomColor: '#DADFE1'
    },
    row: {
        borderBottomStyle: 'solid',
        borderBottomWidth: 1,
        borderBottomColor: '#DADFE1'
    },
    boxPadding: {
        padding: '2rem',
        paddingTop: "0.3rem",
        paddingBottom: "0.3rem",
        color: '#384047',
        fontSize: 15
    },
    boxPaddingDownloade: {
        padding: '2rem',
        paddingTop: "0.3rem",
        paddingBottom: "0.3rem",
        color: '#FFB600',
        fontSize: 15
    },
    boxPaddingDate: {
        paddingRight: '2rem',
        paddingTop: "0.3rem",
        paddingBottom: "0.3rem",
        color: '#384047',
        fontSize: 15
    },
    
    findPadding: {
        padding: '2rem',
        paddingTop: "1rem",
        paddingBottom: "0.2rem",
        color: '#FFB600',
        fontSize: 17
    },
}))

export default function PaymentMethod() {
    const classes = useStyles();
    return (
        <div className={classes.card}>
            <Box display="flex">
                <Box flex={1}>
                <p className={classes.pass}>Payment history</p>
                </Box>
                <p className={classes.updateBill}>Update billing info</p>
            </Box>
       

           <div  className={classes.row}>
               <Box display='flex' style={{ marginLeft: '2rem' }}>
                    <Box  className={classes.boxPaddingDate}>
                     <p >DATE</p>
                    </Box>
                    <Box className={classes.boxPadding}>
                  <p >HOURS</p>
                    </Box>
                    <Box  className={classes.boxPadding}>
                    <p > SUBJECT</p>
                    </Box>
                    <Box  flex={1} className={classes.boxPaddingDownloade}>
                 <p style={{    textAlign: 'right'}}>DOWNLOAD ALL</p>
                    </Box>
               </Box>
           </div>
           <div  className={classes.row}>
           <Box >
                    <Box  className={classes.findPadding}>
                     <p >Find your first tutor</p>
                    </Box>
               </Box>
           </div>
          <Box display='flex' style={{ marginTop: '20rem'}}></Box>
        </div>
    )
}
