import React from 'react';
import { Grid } from '@material-ui/core';
import { fade, makeStyles } from '@material-ui/core/styles';
import Man from '../../assets/teacher.jpeg';
import BusCircle from '../../assets/images/busCircle.png';
import { Link, useHistory } from 'react-router-dom';
import { landingPageRequestPrivateTutor } from '../../Translation/index';
import { useSelector } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  languagesCard: {
    marginTop: 30,
    background: 'white',
    borderRadius: 5,
  },
  semiBoldText: {
    fontFamily: 'Roboto',
    fontSize: 18,
  },
  yellowButton: {
    padding: 10,
    color: 'white',
    borderRadius: 5,
    width: 250,
    background: '#FFB600',
  },
  navLinks: {
    color: 'white',
    // marginLeft: 20,
    fontFamily: 'Roboto',
    cursor: 'pointer',
  },
}));

export default function SpeakConfidently() {
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);

  return (
    <div style={{ background: '#EDEFF0' }}>
      <Grid container>
        <Grid item md={1} lg={1} xl={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={12}
          xs={12}
          style={{ margin: '1rem' }}
        >
          <div className={classes.languagesCard}>
            <img
              style={{
                width: '100%',
                borderRadius: 5,
                objectFit: 'cover',
                marginTop: '1rem',
              }}
              src={Man}
            ></img>
            <img style={{ marginTop: -40 }} src={BusCircle}></img>
            <Grid
              container
              direction='column'
              alignItems='center'
              style={{ width: '100%' }}
            >
              <p className={classes.semiBoldText} style={{ width: 325 }}>
                {/* Our goal is to have */}
                {landingPageRequestPrivateTutor[17][language]}
                {/* <strong style={{ fontWeight: 600, fontSize: 22 }}>
                10 000 
                </strong>{" "}
                registered users till May 30th, 2021. */}
              </p>
              <p style={{ width: 325, fontSize: 13 }}>
                {/* Get fast results with professional online tutors. Prepare to
                achieve your goals today. */}
                {landingPageRequestPrivateTutor[18][language]}
              </p>
              <p className={classes.yellowButton}>
                {' '}
                <Link class={classes.navLinks} to='/becomeStudent'>
                  {/* Start learning */}
                  {landingPageRequestPrivateTutor[19][language]}
                </Link>
              </p>
            </Grid>
          </div>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
}
