import React, { useState, useEffect } from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import 'video-react/dist/video-react.css';
import { Player } from 'video-react';
import HoverVideoPlayer from 'react-hover-video-player';

const useStyles = makeStyles((theme) => ({
  Card: {
    background: 'white',
    borderRadius: 5,
    width: '95%',
    marginBottom: 20,
  },
  dayBox: {
    border: '1px solid #F2F2F2',
    height: 30,
    fontSize: 12,
  },
  dayBoxLarge: {
    border: '1px solid #F2F2F2',
    height: 40,
    fontSize: 12,
  },
  dayBoxLargeGreen: {
    border: '1px solid #F2F2F2',
    background: '#50BF16',
    height: 40,
    fontSize: 12,
  },
}));

export default function TutorCard(props) {
  const days = [1, 2, 3, 4, 5, 6, 7];
  const classes = useStyles();
  const [showVideo, setShowVideo] = useState(false);
  const [video, setVideo] = useState(
    'https://yellow-school.s3.us-east-2.amazonaws.com/Adobe+XD+2020-10-05+11-36-29.mp4'
  );

  useEffect(() => {
    console.log('This is videoooooooooo uriiiii :; ', props.videoUrl);
    let videoUrlConvert = props.videoUrl;
    if (videoUrlConvert) {
      let result = videoUrlConvert.replace('watch?v=', 'embed/');
      let resultContain = result.includes('yellow-school');
      if (resultContain) {
        setShowVideo(true);
      } else {
        setShowVideo(false);
      }
      setVideo(result);
    } else {
      setShowVideo(false);
      setVideo(
        'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQnDqmBuDHpTQlegVXItuMtR9SogRVRrkIV5w&usqp=CAU'
      );
    }
  }, [props.videoUrl]);

  return (
    <div className={classes.Card}>
      {/* <iframe
        style={{ marginTop: 10, border: "none", borderRadius: 5 }}
        src="https://yellow-school.s3.us-east-2.amazonaws.com/Adobe+XD+2020-10-05+11-36-29.mp4"
      ></iframe> */}
      {/* <Player autoPlay>
      <source src={`${video}`} />
    </Player> */}
      {/* <HoverVideoPlayer
      videoSrc="https://yellow-school.s3.us-east-2.amazonaws.com/Adobe+XD+2020-10-05+11-36-29.mp4"
    /> */}
      {/* <video width="400" >
  <source src="mov_bbb.mp4">
  Your browser does not support HTML video.
</video> */}
      {showVideo ? (
        <video
          controls
          src={video}
          autoPlay
          width='400'
          height='200'
          style={{}}
        ></video>
      ) : (
        <iframe width='400' height='200' src={video}></iframe>
      )}

      {/* <Grid container>
        <Grid
          md={5}
          lg={5}
          xl={5}
          sm={5}
          xs={5}
          className={classes.dayBox}
        ></Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Mon
        </Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Tue
        </Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Wed
        </Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Thu
        </Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Fri
        </Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Sat
        </Grid>
        <Grid md={1} lg={1} xl={1} sm={1} xs={1} className={classes.dayBox}>
          Sun
        </Grid>
      </Grid>
      <Grid container>
        <Grid
          md={5}
          lg={5}
          xl={5}
          sm={5}
          xs={5}
          className={classes.dayBoxLarge}
        >
          <p>Morning</p>
          <p style={{ marginTop: -20 }}>06:00-12:00</p>
        </Grid>
        {days.map((day) => {
          return (
            <Grid
              md={1}
              lg={1}
              xl={1}
              sm={1}
              xs={1}
              className={
                day % 2 === 0 ? classes.dayBoxLarge : classes.dayBoxLargeGreen
              }
            ></Grid>
          );
        })}
      </Grid>
      <Grid container>
        <Grid
          md={5}
          lg={5}
          xl={5}
          sm={5}
          xs={5}
          className={classes.dayBoxLarge}
        >
          <p>Noon</p>
          <p style={{ marginTop: -20 }}>06:00-12:00</p>
        </Grid>
        {days.map((day) => {
          return (
            <Grid
              md={1}
              lg={1}
              xl={1}
              sm={1}
              xs={1}
              className={classes.dayBoxLarge}
            ></Grid>
          );
        })}
      </Grid>
      <Grid container>
        <Grid
          md={5}
          lg={5}
          xl={5}
          sm={5}
          xs={5}
          className={classes.dayBoxLarge}
        >
          <p>Evening</p>
          <p style={{ marginTop: -20 }}>06:00-12:00</p>
        </Grid>
        {days.map((day) => {
          return (
            <Grid
              md={1}
              lg={1}
              xl={1}
              sm={1}
              xs={1}
              className={
                day >= 5 ? classes.dayBoxLarge : classes.dayBoxLargeGreen
              }
            ></Grid>
          );
        })}
      </Grid>
      <Grid container direction="column" alignItems="center">
        <p
          style={{
            padding: 5,
            marginTop: 20,
            borderRadius: 2,
            color: "#FFB600",
            fontSize: 17,
            border: "1px solid #FFB600",
            width: "96%",
          }}
          onClick={() => console.log("Ttthhiiiiasd :: ", props.videoUrl)}
        >
          View full schedule
        </p>
      </Grid> */}
    </div>
  );
}
