import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import dateFormat from 'dateformat';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import { Grid } from '@material-ui/core';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

export default function OutlinedCard(props) {
  const classes = useStyles();
  const [tutorRating, setTutorRating] = useState([]);
  useEffect(() => {
    console.log('This is propsssss :: ', props.tutorData.ratings);
    if (props.tutorData.ratings) {
      setTutorRating(props.tutorData.ratings);
    }
  }, [props]);
  return (
    <Card className={classes.root} variant='outlined'>
      {tutorRating.length > 0 ? (
        tutorRating.map((data) => (
          <CardContent>
            <Grid container style={{ textAlign: 'left' }}>
              <Grid item md={2} lg={2} xl={2} sm={2} xs={2}>
                <img
                  src={data && data.publisher ? data.publisher.imageUrl : ''}
                  style={{
                    borderRadius: '10px',
                    height: '70px',
                    width: '70px',
                    paddingTop: '10px',
                    textAlign: 'center',
                    marginTop: '1rem',
                  }}
                />
              </Grid>
              <Grid
                item
                md={10}
                lg={10}
                xl={10}
                sm={10}
                xs={10}
                style={{ borderBottom: '1px solid #DADFE1', paddingBottom: 30 }}
              >
                <Typography
                  variant='h5'
                  component='h3'
                  color='textSecondary'
                  gutterBottom
                >
                  {data && data.publisher ? data.publisher.fullName : ''}
                </Typography>
                <Typography variant='body2' className='mb-1'>
                  {dateFormat(
                    data ? data.createdAt : '',
                    'dddd, mmmm dS, yyyy'
                  )}
                </Typography>
                <Typography variant='body2' component='p'>
                  {data ? data.comment : ''}
                </Typography>
              </Grid>
            </Grid>
          </CardContent>
        ))
      ) : (
        <p>No rating yet</p>
      )}
    </Card>
  );
}
