import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Modal from "@material-ui/core/Modal";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Input from "@material-ui/core/Input";
import Footer from "../../components/footer/footer";
import CircularProgress from "@material-ui/core/CircularProgress";
import MailOutlineIcon from "@material-ui/icons/MailOutline";
import { Button } from "@material-ui/core";
import Icon from "@material-ui/core/Icon";
import Nav from "../../components/nav/topNav";
import axios from "axios";
import baseUrl from "../../baseurl";

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

export default function Invite({ history }) {
  const useStyles = makeStyles((theme) => ({
    root: {
      paddingTop: "35px",
      backgroundColor: "#EDEFF0",
    },
    root2: {
      paddingTop: "55px",
      backgroundColor: "#EDEFF0",
    },
    paper: {
      position: "absolute",
      width: 400,
      backgroundColor: theme.palette.background.paper,
      border: "2px solid #000",
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),
    },
    linkText: {
      color: theme.palette.text.secondary,
    },
    buttonColor: {
      color: "white",
      backgroundColor: "#FFB600",
      marginLeft: "8px",
    },
    shareButton: {
      color: "white",
      backgroundColor: theme.palette.text.primary,
      marginLeft: "8px",
    },
  }));

  const classes = useStyles();
  const [userData, setUserData] = useState(null);
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);
  const [userEmail, setUserEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [errorMessage, setErrorMessage] = useState(false);

  useEffect(() => {
    getUserData();
  }, []);

  const getUserData = async () => {
    let tutor = await localStorage.getItem("yellowTutor");
    let student = await localStorage.getItem("yellowStudent");
    if (tutor) {
      let t = JSON.parse(tutor);
      setUserData(t);
    }
    if (student) {
      let s = JSON.parse(student);
      setUserData(s);
    }
  };

  const onSendInvitation = async () => {
    setError(false);
    setLoading(true);
    let re =
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (re.test(userEmail)) {
      axios({
        method: "post",
        url: `${baseUrl}/api/auth/reffer`,
        data: { email: userEmail, name: userData.name },
      })
        .then((da) => {
          setLoading(false);
          setOpen(true);
        })
        .catch((e) => {
          setLoading(false);
          setError(true);
          setErrorMessage("Email already registered");
        });
    } else {
      setLoading(false);
      setError(true);
      setErrorMessage("Email is not correct");
    }
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id='simple-modal-title'>Invitation Sent</h2>

      <p id='simple-modal-description'>
        We have send an invitation link to {userEmail}
      </p>

      <Button
        variant='contained'
        style={{
          color: "white",
          backgroundColor: "green",
          marginRight: "2rem",
        }}
        onClick={() => history.push("/login")}>
        Ok
      </Button>
    </div>
  );

  return (
    <div>
      <Nav />
      <Grid container className={classes.root}>
        <Grid item md={1} lg={2} xl={2} sm={1}></Grid>
        <Grid item md={5} lg={4} xl={4} sm={10}>
          <img
            src='https://www.buyandship.com.sg/contents/uploads/2019/10/FriendReferral-500x261.png'
            width='350px'
            height='300px'
          />
        </Grid>

        <Grid
          item
          md={5}
          lg={4}
          xl={4}
          sm={10}
          style={{
            alignItems: "left",
            textAlign: "left",
            paddingRight: "10px",
          }}>
          {/* <h3>Give a discount to your friends and pay less</h3>
          <p>
            Give a 30% discount for a first lesson to your friends and get 5$
            from their next payment.
          </p>
         
          <div style={{ display: "flex", width: "80%" }}>
            <TextField
              id="outlined-secondary"
              label="Referral link"
              variant="outlined"
              color="primary"
              fullWidth
              size="small"
            />
            <Button
              size="small"
              variant="contained"
              className={classes.buttonColor}
              color="primary"
            >
              Copy
            </Button>
          </div>

          <Button className={classes.paper}>
            <MailOutlineIcon style={{ color: "red" }} className="mr-2" />
            Choose emails from your Gmail
          </Button> */}
          <h3>Send a discount to your friend via email</h3>
          {error && <p style={{ color: "red" }}>{errorMessage} </p>}
          <div style={{ display: "flex", width: "80%" }}>
            <TextField
              id='outlined-secondary'
              label='Enter emails'
              variant='outlined'
              color='primary'
              fullWidth
              size='small'
              onChange={(e) => setUserEmail(e.target.value)}
            />
            <Button
              size='small'
              variant='contained'
              className={classes.buttonColor}
              onClick={onSendInvitation}
              disabled={loading}
              color='primary'>
              {loading ? <CircularProgress /> : "Send"}
            </Button>
          </div>
          <p className='mt-2'>
            Enter a few emails separated by comma and we'll send your friends a
            letter with 30% discount and your referral link.
          </p>
        </Grid>
        <Grid item md={1} lg={2} xl={2} sm={1}></Grid>
      </Grid>
      <Grid container className={classes.root2}>
        <Grid item md={1} lg={2} xl={2} sm={1}></Grid>
        {/* <Grid
          item
          md={6}
          lg={4}
          xl={4}
          sm={10}
          style={{
            padding: "20px",

            backgroundColor: "#F5F5F3",
            textAlign: "left",
          }}
        >
          <h5>Share your referral link</h5>
          <Button
            size="small"
            variant="contained"
            style={{ width: "100px" }}
            className={classes.shareButton}
            color="primary"
          >
            VK
          </Button>
          <Button
            size="small"
            style={{ width: "100px" }}
            variant="contained"
            className={classes.shareButton}
            color="primary"
          >
            Facebook
          </Button>
          <Button
            size="small"
            variant="contained"
            style={{ width: "100px" }}
            className={classes.shareButton}
            color="primary"
          >
            Twitter
          </Button>
          <br />
          <Button
            style={{ marginTop: "10px", width: "100px" }}
            size="small"
            variant="contained"
            className={classes.shareButton}
            color="primary"
          >
            Whatsapp
          </Button>
          <Button
            style={{ marginTop: "10px", width: "100px" }}
            size="small"
            variant="contained"
            className={classes.shareButton}
            color="primary"
          >
            Instagram
          </Button>
          <Button
            size="small"
            style={{ marginTop: "10px", width: "100px" }}
            variant="contained"
            className={classes.shareButton}
            color="primary"
          >
            Messanger
          </Button>
        </Grid> */}
        <Grid
          item
          md={4}
          lg={4}
          xl={4}
          sm={10}
          style={{
            padding: "20px",
            marginLeft: "10px",
            backgroundColor: "#EEEEE8",
            textAlign: "left",
          }}>
          {/* <h6>Send a discount to your friend via email</h6>
          <div style={{ display: "flex", width: "80%" }}>
            <TextField
              id="outlined-secondary"
              label="Enter emails"
              variant="outlined"
              color="primary"
              fullWidth
              size="small"
            />
            <Button
              size="small"
              variant="contained"
              className={classes.buttonColor}
              color="primary"
            >
              Send
            </Button>
          </div>
          <p className="mt-2">
            Enter a few emails separated by comma and we'll send your friends a
            letter with 30% discount and your referral link.
          </p> */}
        </Grid>
      </Grid>
      <Grid container className={classes.root2}>
        <Grid md={12} lg={12} xl={12} sm={12} style={{ textAlign: "center" }}>
          <h3>How it works?</h3>
        </Grid>
      </Grid>
      <Grid container className={classes.root2}>
        <br />
        <Grid item md={1} lg={2} xl={2} sm={1}></Grid>
        <Grid item md={6} lg={3} xl={3} sm={10}>
          <h5>You give a discount to your friends</h5>
          <p>
            You can invite friends with your referral link. They get 30%
            discount for the first payment
          </p>
        </Grid>
        <Grid item md={6} lg={3} xl={3} sm={10}>
          <h5>Your friends use discount</h5>
          <p>Your friends choose a tutor and pay less with your discount</p>
        </Grid>
        <Grid item md={6} lg={3} xl={3} sm={10}>
          <h5>You get money</h5>
          <p>
            As soon as your friend used a discount you'll get 5$ of their deal
            to your account
          </p>
        </Grid>
        <Grid item md={1} lg={2} xl={2} sm={1}></Grid>
      </Grid>
      <Footer></Footer>
      <Modal
        open={open}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'>
        {body}
      </Modal>
    </div>
  );
}
