export default function TutorLessonTable({
  lessons,
  scheduleLesson,
  lessonScheduling,
}) {
  const classes = useRowStyles();

  useEffect(() => {
    console.log("Tutorrrr lessons :: ", lessons);
  }, [lessons]);

  const TABLE_HEADER = [
    { label: "STUDENTS" },
    { label: "PREPAID" },
    { label: "PRICE PER HOUR" },
    { label: "Status" },
    { label: "Date" },
    { label: "Start Time" },
    { label: "End Time" },
    { label: "" },
    { label: "" },
  ];
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableBody>
          <TableRow>
            <TableCell />
            {TABLE_HEADER.map(({ label }, i) => (
              <TableCell
                className={classes.tableHeader}
                style={{ textAlign: "left" }}
                key={i}
              >
                {label}
              </TableCell>
            ))}
          </TableRow>
          {lessons.map((lesson) => (
            <Row
              key={lesson._id}
              lesson={lesson}
              scheduleLesson={scheduleLesson}
              lessonScheduling={lessonScheduling}
            />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
