import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Nav from "../components/nav/topNav";
import { Grid } from "@material-ui/core";
import SubHeaderTutor from "../components/nav/SubHeaderTutor";
import AccountCircle from "@material-ui/icons/AccountCircle";
import BusIcon from "../assets/images/busCircle.png";
import ArchiveIcon from "@material-ui/icons/Archive";
import { makeStyles } from "@material-ui/core/styles";
import MessageCircle from "../assets/images/messageCircle.png";
import ListCircle from "../assets/images/listCircle.png";
import LogoutCircle from "../assets/images/logoutCircle.png";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import { MyLessons } from "../Data/Data";
import MenuItem from "@material-ui/core/MenuItem";
import Footer from "../components/footer/footer";
import Menu from "@material-ui/core/Menu";
import axios from "axios";
import baseUrl from "../baseurl";
import { GET_TUTOR_LESSON } from "../Constants/sagaActions";

const useStyles = makeStyles((theme) => ({
  name: {
    fontSize: 14,
    marginLeft: 15,
  },
  tableHeader: {
    fontSize: 12,
  },
  button: {
    background: "#FFB600",
    border: "none",
    color: "white",
    padding: 5,
    paddingLeft: 10,
    width: 150,
    marginLeft: 25,
    borderRadius: 2,
  },
  iconsImages: {
    marginLeft: "5%",
  },
}));

export default function TutorMyLessonsPage() {
  const classes = useStyles();
  const dispatch = useDispatch();

  const [anchorEl, setAnchorEl] = React.useState(null);
  const isMenuOpen = Boolean(anchorEl);

  const tutorLessons = useSelector((state) => state.tutorLesson.tutorLessons);
  const gettingTutorLessons = useSelector(
    (state) => state.tutorLesson.gettingTutorLessons
  );

  useEffect(() => {
    getAllLessons();
  }, []);

  const getAllLessons = async () => {
    let token = await localStorage.getItem("yellowToken");

    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowTutor");
    let newStu = await JSON.parse(stu);
    console.log("Lessongs foundd :: ", newStu);
    axios
      .get(`${baseUrl}/api/lessonByTutorId/${newStu._id}`, config)
      .then((data) => {
        console.log("These are all my lessons :: ", data);
      })
      .catch((e) => console.log("This is error :: ", e));
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };
  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  useEffect(() => {
    dispatch({ type: GET_TUTOR_LESSON });
  }, []);

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Student</MenuItem>
      <MenuItem onClick={handleMenuClose}>Student</MenuItem>
    </Menu>
  );
  return (
    <div>
      <Nav></Nav>
      <SubHeaderTutor for="My Lessons"></SubHeaderTutor>
      <div style={{ height: "100%" }}>
        <Grid container style={{ background: "#EDEFF0", height: "90vh" }}>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
          <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
            <Grid
              container
              direction="row"
              alignItems="center"
              style={{
                boxShadow:
                  "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
                height: 70,
                background: "white",
                borderRadius: 2,
                marginTop: 30,
                paddingLeft: 20,
                paddingRight: 20,
              }}
            >
              <Grid item md={7} lg={7} xl={7} sm={7} xs={7}>
                <Grid container direction="row" alignItems="flex-start">
                  <span>Next lesson: Wednesday, 29 May at 11:00 with</span>
                  <AccountCircle
                    style={{
                      color: "#E9E6E3",
                      marginLeft: 15,
                    }}
                  ></AccountCircle>
                  <span style={{ marginLeft: 10 }}>TNDRS A.</span>
                </Grid>
              </Grid>
              <Grid item md={5} lg={5} xl={5} sm={5} xs={5}>
                <Grid
                  container
                  direction="row"
                  justify="flex-end"
                  alignItems="center"
                >
                  <img src={BusIcon} style={{ width: 45, height: 45 }}></img>
                  <span style={{ color: "#FFB600" }}>Enter Classroom of</span>
                </Grid>
              </Grid>
              <div style={{ width: "35%" }}> </div>
            </Grid>
            <div
              style={{
                boxShadow:
                  "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
                background: "white",
              }}
            >
              <Grid
                container
                direction="row"
                alignItems="center"
                style={{
                  height: 60,
                  borderRadius: 2,
                  marginTop: 30,
                  paddingLeft: 20,
                  borderBottom: "1px solid #AAB5BC",
                  paddingRight: 20,
                }}
              >
                <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                  <Grid container direction="column" alignItems="flex-start">
                    <span style={{ fontSize: 18 }}>Active lessons</span>
                  </Grid>
                </Grid>
                <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                  <Grid
                    container
                    direction="row"
                    alignItems="center"
                    justify="flex-end"
                  >
                    <ArchiveIcon style={{ color: "gray" }}></ArchiveIcon>
                    <span>Archived lessons</span>
                  </Grid>
                </Grid>
              </Grid>
              <Grid
                container
                direction="row"
                alignItems="center"
                style={{
                  height: 60,
                  borderBottom: "1px solid #AAB5BC",
                  borderRadius: 2,
                  paddingLeft: 20,
                  paddingRight: 20,
                }}
              >
                <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                  <Grid container direction="row">
                    <Grid item md={7} lg={7} xl={7} sm={7} xs={7}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <span className={classes.tableHeader}>STUDENTS</span>
                      </Grid>
                    </Grid>
                    <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <span className={classes.tableHeader}>PREPAID</span>
                      </Grid>
                    </Grid>
                    <Grid item md={2} lg={2} xl={2} sm={2} xs={2}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <span
                          className={classes.tableHeader}
                          style={{ textAlign: "left" }}
                        >
                          PRICE PER HOUR
                        </span>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                  <Grid
                    container
                    direction="row"
                    alignItems="center"
                    justify="flex-end"
                  >
                    <span className={classes.tableHeader}>Actions</span>
                  </Grid>
                </Grid>
              </Grid>
              {tutorLessons &&
                tutorLessons.map((lesson) => {
                  return (
                    <Grid
                      key={lesson._id}
                      container
                      direction="row"
                      alignItems="center"
                      style={{
                        height: 60,
                        borderBottom: "1px solid #AAB5BC",
                        borderRadius: 2,
                        paddingLeft: 20,
                        paddingRight: 20,
                      }}
                    >
                      <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                        <Grid container direction="row">
                          <Grid item md={4} lg={4} xl={4} sm={4} xs={12}>
                            <Grid
                              container
                              direction="row"
                              justify="flex-start"
                            >
                              <AccountCircle
                                style={{
                                  color: "#E9E6E3",
                                  marginLeft: 15,
                                }}
                              ></AccountCircle>
                              <span className={classes.name}>
                                {lesson.studentId?.name}
                              </span>
                            </Grid>
                          </Grid>
                          <Grid item md={4} lg={4} xl={4} sm={4} xs={12}>
                            <Grid
                              container
                              direction="column"
                              alignItems="flex-start"
                            >
                              <span className={classes.tableHeader}>
                                {lesson.totalHours} hours
                              </span>
                            </Grid>
                          </Grid>
                          <Grid item md={4} lg={4} xl={4} sm={4} xs={12}>
                            <Grid
                              container
                              direction="column"
                              alignItems="flex-start"
                            >
                              <span className={classes.tableHeader}>
                                {lesson.pricePerHour}
                              </span>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
                        <Grid container direction="row" alignItems="center">
                          <button className={classes.button}>Schedule</button>
                          <img
                            src={MessageCircle}
                            className={classes.iconsImages}
                          ></img>
                          <img
                            src={ListCircle}
                            className={classes.iconsImages}
                          ></img>
                          <img
                            src={LogoutCircle}
                            className={classes.iconsImages}
                          ></img>
                          <MoreVertIcon
                            onClick={handleMenuOpen}
                            className={classes.iconsImages}
                          ></MoreVertIcon>
                        </Grid>
                      </Grid>
                    </Grid>
                  );
                })}
              {renderMenu}
            </div>
          </Grid>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
        </Grid>
      </div>

      <div>
        <Footer />
      </div>
    </div>
  );
}
