import { call, takeLatest, put } from "redux-saga/effects";
import { logggingIn, loginSuccesss, loginError } from "../slices/auth.slice";
import { LOGIN } from "../../Constants/sagaActions";
import { loginApi } from "../../API/authApi";

export function* loginSaga(action) {
  try {
    const { email, password, history } = action.payload;
    yield put(logggingIn());
    const result = yield call(() => loginApi({ email, password }));
    console.log("REsult", result);
    localStorage.setItem("yellowToken", result.data.Authorization);
    localStorage.setItem("yellowTutor", JSON.stringify(result.data.user));

    yield put(loginSuccesss(result.data));
    history.push("/dashboard");
  } catch (e) {
    yield put(loginError(e));
  }
}

export default function* watchLoginSaga() {
  yield takeLatest(LOGIN, loginSaga);
}
