import React, { useEffect, useState } from 'react';
import Navbar from './components/nav/topNav';
import SignUp from './components/signUp';
import Login from './Routes/Login/login';
import ResetPassword from './Routes/Login/resetPassword';
import ConfirmEmail from './Routes/Login/confirmEmail';
import ConfirmEmailTutor from './Routes/Login/confirmTutorEmail';
import TutorDashboard from './Routes/Dashboard/tutorDashboard';
import InviteFriends from './Routes/InviteFriends/inviteFriends';
import LandingPage from './Pages/LandingPage';
import BlogPage from './Pages/BlogPage';
import AboutPage from './Pages/AboutPage';
import RequestTutor from './Pages/RequestTutor';
import RequestTutorHome from './Pages/RequestTutorHome';
import SingleTutor from './Pages/SingleTutor';
import BecomeTutorPage from './Pages/BecomeTutorPage';
import BecomeStudentPage from './Pages/BecomeStudentPage';
import MyInvites from './Pages/MyInvites';
import PostRequest from './components/RequestTutor/RequestCard';
import Settings from './Pages/Settings';
import PaymentHistory from './Pages/PaymentHistory';
import AutoConfirmation from './Pages/AutoConfirmation';
import PaymentMethod from './Pages/PaymentMethods';
import Passwords from './Pages/Passwords';
import Calendar from './Pages/Calendar';
import Notification from './Pages/Notifications';
import TutorMyLessons from './Routes/TutorLesson/tutorLessonsList';
import TutorMyMessages from './Pages/TutorMyMessages';
import PrivacyPolicy from './Pages/PrivacyPolicy';
import StudentMyMessages from './Pages/StudentMyMessages';
import TutorCalendar from './Pages/TutorCalendar';
import TutorAvailabilityPage from './Pages/TutorAvailabilityPage';
import LearningPlanLibarary from './Routes/LearningPlanLibrary/learningPlanLibarary';
import YelloHelpCenter from './Pages/HelpCenter';
import StudentIndex from './components/Student/index';
import { Button } from '@material-ui/core';
import { transitions, positions, Provider as AlertProvider } from 'react-alert';
import AlertTemplate from 'react-alert-template-basic';

// Student Pages
import LessonStudentPage from './Routes/TutorLesson/studentLessonsList';

import { Switch, Route, BrowserRouter, Redirect } from 'react-router-dom';
import './App.css';

const options = {
  // you can also just use 'bottom center'
  position: positions.BOTTOM_CENTER,
  timeout: 5000,
  offset: '30px',
  // you can also just use 'scale'
  transition: transitions.SCALE,
};

const ProtectedRoute = ({ component: Component, user, ...rest }) => {
  return (
    <Route
      {...rest}
      render={(props) => {
        if (localStorage.getItem('yellowToken')) {
          return <Component {...rest} {...props} />;
        } else {
          return (
            <Redirect
              to={{
                pathname: '/login',
                state: {
                  from: props.location,
                },
              }}
            />
          );
        }
      }}
    />
  );
};

function App() {
  return (
    <AlertProvider template={AlertTemplate} {...options}>
      <div className='App'>
        <BrowserRouter>
          <Switch>
            <ProtectedRoute
              exact
              path='/dashboard'
              component={TutorDashboard}
            />
            <Route exact path='/' component={LandingPage} />
            <Route exact path='/privacypolicy' component={PrivacyPolicy} />
            <Route exact path='/resetpassword/:id' component={ResetPassword} />
            <Route exact path='/confirmemail/:id' component={ConfirmEmail} />
            <Route
              exact
              path='/confirmemailtutor/:id/:tutorId'
              component={ConfirmEmailTutor}
            />
            <Route exact path='/blog' component={BlogPage} />
            <Route exact path='/about' component={AboutPage} />
            <Route exact path='/requestTutor' component={RequestTutor} />
            <Route
              exact
              path='/requestTutorHome'
              component={RequestTutorHome}
            />
            <Route exact path='/singleTutor' component={SingleTutor} />
            <Route exact path='/becomeTutor' component={BecomeTutorPage} />
            <Route exact path='/becomeStudent' component={BecomeStudentPage} />
            <Route exact path='/myInvites' component={MyInvites} />
            <ProtectedRoute exact path='/settings' component={Settings} />
            <Route exact path='/paymentHistory' component={PaymentHistory} />
            <Route
              exact
              path='/autoConfirmation'
              component={AutoConfirmation}
            />
            <Route exact path='/paymentMethod' component={PaymentMethod} />
            <Route exact path='/postrequest' component={PostRequest} />
            <Route exact path='/passwords' component={Passwords} />
            <Route exact path='/calendar' component={Calendar} />
            <Route exact path='/signup' component={SignUp} />
            <Route exact path='/login' component={Login} />
            <Route exact path='/helpcenter' component={YelloHelpCenter} />
            <Route exact path='/notifications' component={Notification} />
            <ProtectedRoute
              exact
              path='/tutorMyLessons'
              component={TutorMyLessons}
            />
            <Route
              exact
              path='/studentMyLessons'
              component={LessonStudentPage}
            />
            <Route exact path='/tutorMyMessages' component={TutorMyMessages} />
            <Route
              exact
              path='/studentMyMessages'
              component={StudentMyMessages}
            />
            <Route exact path='/tutorCalendar' component={TutorCalendar} />
            <Route exact path='/inviteFriends' component={InviteFriends} />
            <Route exact path='/studentpage' component={StudentIndex} />
            <Route exact path='/tutor' component={Notification} />
            <ProtectedRoute
              exact
              path='/learningPlans'
              component={LearningPlanLibarary}
            />
            <Route
              exact
              path='/tutorAvailability'
              component={TutorAvailabilityPage}
            />
          </Switch>
        </BrowserRouter>
        {/* <Navbar />
      <SignUp /> */}
      </div>
    </AlertProvider>
  );
}

export default App;
