import React, { useState, useEffect } from 'react';
// import Slider from 'infinite-react-carousel';
import { makeStyles } from '@material-ui/core/styles';
import { Container, Grid } from '@material-ui/core';
// import { Comments } from "../../Data/Data";
import { Button } from '@material-ui/core';
import Carousel, { consts } from 'react-elastic-carousel';
import { landingPageFocusSkill } from '../../Translation/index';
import { useSelector } from 'react-redux';
import CommentPic from '../../assets/images/commentPic.png';
import CommentPics1 from '../../assets/s1.png';
import CommentPics2 from '../../assets/s2.png';
import CommentPics3 from '../../assets/s3.png';
import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';

const useStyles = makeStyles((theme) => ({
  centralCard: {
    marginTop: 50,
    background: 'white',
    borderRadius: 5,
    [theme.breakpoints.down('xs')]: {
      // height: 280,
    },
    overflow: 'hidden',
  },
  boldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 'bold',
  },
  semiBoldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 500,
  },
  CommentPic: {
    // height: '30vw',
    width: '50%',
    [theme.breakpoints.down('xs')]: {
      width: '100%',
    },
    borderRadius: 5,
  },
  commentPicSmall: {
    width: 50,
    height: 50,
    borderRadius: 25,
    [theme.breakpoints.down('sm')]: {
      width: 20,
      height: 20,
    },
  },
  commentDescription: {
    fontSize: 25,
    [theme.breakpoints.down('md')]: {
      fontSize: 15,
    },
    [theme.breakpoints.down('sm')]: {
      fontSize: 12,
    },
    [theme.breakpoints.down('xs')]: {
      fontSize: 11,
    },
  },
  sideGrid: {
    marginTop: 20,
    marginLeft: 25,
    [theme.breakpoints.down('sm')]: {
      marginTop: 20,
      marginLeft: 5,
    },
    [theme.breakpoints.down('xs')]: {
      marginTop: 10,
      marginLeft: 5,
    },
  },
  commentText: {
    width: 400,
    textAlign: 'justify',
    fontSize: 22,
    [theme.breakpoints.down('md')]: {
      width: 280,
      textAlign: 'justify',
      fontSize: 15,
    },
    [theme.breakpoints.down('sm')]: {
      width: 190,
      fontSize: 12,
    },
    [theme.breakpoints.down('xs')]: {
      width: 245,
      fontSize: 11,
    },
    fontFamily: 'Ka',
  },
}));

export default function CommentSlider() {
  const classes = useStyles();
  const [activeSlide, setActiveSlide] = useState(0);
  const language = useSelector((state) => state.languageReducer.language);
  useEffect(() => {
    console.log('This is the width of screen .... :: ', window.innerWidth);
  }, []);

  function myArrow({ type, onClick, isEdge }) {
    const pointer = type === consts.PREV ? '<' : '>';
    return (
      <Button onClick={onClick} disabled={isEdge}>
        {' '}
        <h1 style={{ fontSize: 60, color: isEdge ? 'gray' : 'black' }}>
          {pointer}
        </h1>
      </Button>
    );
  }

  const Comments = [
    {
      pic: CommentPics1,
      commentBy: 'Philip',
      description: '',
      // comment:
      //   "Talk to Me was an English school that was created in 2014 when Nazar created a study group of twenty willing students from KNEU University in Kyiv with a proper English tutor.",
      comment: `${landingPageFocusSkill[9][language]}`,
      language: 'English',
    },
    {
      pic: CommentPics2,
      commentBy: 'Tulip',
      description: '',
      // comment:
      //   "After being kicked out of the study rooms at the University, Nazar didnt give up on his dreams. Lessons moved to a rented office space that had multiple rooms for lessons. The group now had about fifty students and three tutors. It was difficult and after a drastic rent increase, they ended up moving- the first of several.",
      comment: `${landingPageFocusSkill[10][language]} ${landingPageFocusSkill[12][language]} ${landingPageFocusSkill[13][language]} ${landingPageFocusSkill[14][language]}`,
      language: 'French',
    },
    {
      pic: CommentPics3,
      commentBy: 'Tulip',
      description: '',
      // comment:
      //   "Two years later, Talk to Me was renamed Yellow School and they finally settled down into the perfect space. Yellow School now had seven tutors for four different languages: English, Polish, German, Chinese. After setting up the school, Nazar moved to Shanghai, China to learn a new culture, and later, with new mindset and knowledge - continue developing Yellow School.",
      comment: `${landingPageFocusSkill[11][language]} ${landingPageFocusSkill[15][language]} ${landingPageFocusSkill[16][language]}`,
      language: 'French',
    },
  ];

  var settings = {
    dots: true,
    autoplay: true,
    arrows: false,
  };

  return (
    <div style={{ background: '#EDEFF0', borderRadius: 5 }}>
      <p className={classes.semiBoldText}>
        {/* Read the story of Yellow School */}
        {landingPageFocusSkill[7][language]}
      </p>
      <p>
        {/* Yellow school tutors have helped thousands of people prepare to speak
        confidently */}
        {landingPageFocusSkill[8][language]}
      </p>
      <Grid container>
        <Grid item md={1} lg={1} xl={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={12}
          xs={12}
          style={{ margin: '1rem' }}
        >
          <Slider>
            {Comments.map((Comment) => {
              return (
                <div>
                  <div className={classes.centralCard}>
                    <Grid container direction='row'>
                      <img
                        className={classes.CommentPic}
                        src={Comment.pic}
                      ></img>
                      <Grid item>
                        <Grid
                          container
                          direction='row'
                          className={classes.sideGrid}
                        >
                          <Grid item md={12} lg={12} xl={12} sm={12} xs={12}>
                            <div
                              style={{
                                display: 'flex',
                                alignItems: 'center',
                              }}
                            >
                              <img
                                src={Comment.pic}
                                className={classes.commentPicSmall}
                              ></img>{' '}
                              <span style={{ marginLeft: '10px' }}>
                                {Comment.commentBy}
                              </span>
                            </div>
                          </Grid>

                          <Grid item>
                            <Grid
                              style={{ marginRight: '1rem' }}
                              container
                              direction='column'
                            >
                              <span className={classes.commentDescription}>
                                {Comment.description}
                              </span>
                              <p className={classes.commentText}>
                                <i>"{Comment.comment}"</i>
                              </p>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                  </div>
                </div>
              );
            })}
          </Slider>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
      {/* <Carousel {...settings}>
        {Comments.map((Comment) => {
          return (
            <div>
              <div className={classes.centralCard}>
                <Grid container direction='row'>
                  <img className={classes.CommentPic} src={Comment.pic}></img>
                  <Grid item>
                    <Grid
                      container
                      direction='row'
                      className={classes.sideGrid}
                    >
                      <img
                            src={Comment.pic}
                            className={classes.commentPicSmall}
                          ></img>{" "}
                      <Grid item>
                        <Grid
                          style={{ marginRight: '1rem' }}
                          container
                          direction='column'
                        > */}
      {/* <span>{Comment.commentBy}</span> */}
      {/* <span className={classes.commentDescription}>
                            {Comment.description}
                          </span>
                          <p className={classes.commentText}>
                            <i>"{Comment.comment}"</i>
                          </p>
                        </Grid>
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </div>
            </div>
          );
        })}
      </Carousel> */}
    </div>
  );
}
