const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  iconColor: {
    color: '#8A959E',
  },
  iconColor2: {
    color: 'black',
  },
  leftText: {
    marginTop: 10,
    marginLeft: 10,
    fontSize: 14,
    cursor: 'pointer',
  },
  leftTextLabel: {
    fontSize: 12,
    fontWeight: 'bold',
    marginLeft: 18,
    marginTop: 5,
    // textAlign: 'left',
  },
  slider: {
    marginLeft: 18,
    marginTop: 5,
    width: '10rem',
    color: '#ffcd02',
  },
}));

const BootstrapInput = withStyles((theme) => ({
  input: {
    // position: 'relative',
    fontSize: 12,
    // padding: '10px 26px 10px 12px',
    paddingLeft: 18,
    paddingTop: 0,
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:focus': {},
  },
}))(InputBase);
function valuetext(value) {
  return `${value}C`;
}

