import React, { useState } from 'react';
import Modal from '@material-ui/core/Modal';
import { Grid, Box, TextField, Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import LandingPageNav from '../nav/landingPageNav';
import Footer from '../footer/footer';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';

function rand() {
  return Math.round(Math.random() * 40) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  Card: {
    background: 'white',
    borderRadius: 2,
    width: '80%',
    marginTop: 20,
    marginBottom: 20,
    padding: '2vh 3vw',
    boxShadow: ' 0px 1px 32px 5px rgba(179,179,179,0.3)',
    borderRadius: '5px',
  },
  leftText: {
    fontSize: 22,
    fontWeight: '400',
    textAlign: 'left',
  },
  leftTextLabel: {
    color: '#6F757B',
    marginBottom: '1px',
    fontWeight: 'lighter',
    textAlign: 'left',
  },
  leftTextLabelSmall: {
    fontSize: 10,
    fontWeight: 'lighter',
    textAlign: 'left',
    width: '100%',
  },
  input: {
    width: '100%',
    height: 36,
    borderRadius: 4,
    border: '1px solid #E4E8E9',
    paddingLeft: 5,
    marginBottom: 20,
  },
  Buttons: {
    padding: 10,
    color: '#FFB600',
    cursor: 'pointer',
    border: '1px solid #FFB600',
  },
  ButtonsFilled: {
    padding: '10px 10px',
    align: 'center',
    cursor: 'pointer',
    color: 'white',
    border: '1px solid #FFB600',
    background: '#FFB600',
  },
  priceInput: {
    width: 80,
    fontSize: 20,
    border: '1px solid #FFB600',
    color: '#9D9D9D',
  },
}));

const AirbnbSlider = withStyles({
  root: {
    color: '#FFB600',
    height: 3,
    width: '95%',
    marginTop: 10,
    padding: '13px 0',
  },
  thumb: {
    height: 23,
    width: 23,
    backgroundColor: '#FFB600',
    border: '1px solid #FFB600',
    marginTop: -12,
    marginLeft: -13,
    // boxShadow: "#ebebeb 0 2px 2px",
    // "&:focus, &:hover, &$active": {
    //   boxShadow: "#ccc 0 2px 3px 1px",
    // },
    // "& .bar": {
    //   // display: inline-block !important;
    //   height: 9,
    //   width: 1,
    //   backgroundColor: "currentColor",
    //   marginLeft: 1,
    //   marginRight: 1,
    // },
  },
  active: {},
  track: {
    height: 3,
  },
  rail: {
    color: '#FFB600',
    opacity: 1,
    height: 3,
  },
})(Slider);

function AirbnbThumbComponent(props) {
  return (
    <span {...props}>
      <span className='bar' />
      <span className='bar' />
      <span className='bar' />
    </span>
  );
}

export default function RainBowCard(props) {
  const classes = useStyles();
  const history = useHistory();
  const [online, setOnline] = useState(true);
  const [showModel, setShowModel] = useState(false);
  const [minRate, setMinRate] = useState(0);
  const [modalStyle] = React.useState(getModalStyle);
  const [maxRate, setMaxRate] = useState(100);
  const updateValue = (event, newValue) => {
    setMinRate(newValue[0]);
    setMaxRate(newValue[1]);
  };
  const [showWarning, setShowWarning] = useState(false);
  const [tutorSubject, setTutorSubject] = useState('');
  const [headline, setHeadline] = useState('');
  const [requirements, setRequirements] = useState('');
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [phone, setPhone] = useState('');

  const onSubmit = () => {
    setShowWarning(false);
    if (
      !tutorSubject ||
      !headline ||
      !requirements ||
      !name ||
      !email ||
      !phone
    ) {
      setShowWarning(true);
    } else {
      setShowModel(true);
    }
  };
  return (
    <div>
      <Modal
        open={showModel}
        onClose={() => setShowModel(false)}
        // aria-labelledby='simple-modal-title'
        // aria-describedby='simple-modal-description'
      >
        <div
          style={{
            marginTop: '25%',
            width: '20%',
            marginLeft: '40%',
            backgroundColor: 'white',
            padding: '1rem',
          }}
          className={classes.paper}
        >
          <h2 id='simple-modal-title' style={{ color: 'green' }}>
            Success!
          </h2>
          <p id='simple-modal-description'>Your request has been submitted.</p>
          <Box display='flex'>
            <Box flex={1}></Box>
            <Button
              variant='contained'
              className={classes.button}
              disableElevation
              onClick={() => history.push('/')}
            >
              Ok
            </Button>
          </Box>
        </div>
      </Modal>
      <LandingPageNav></LandingPageNav>
      <Grid container style={{ marginTop: '4vh', marginBottom: '2vh' }}>
        <Grid item md={3} xs={1}></Grid>
        <Grid
          item
          md={6}
          xs={10}
          align='center'
          alignItems='center'
          justify='center'
        >
          <div className={classes.Card}>
            <p style={{ fontSize: 28 }}>Request</p>
            <p className={classes.leftText}>Describe Your Needs</p>
            {showWarning && (
              <p
                className={classes.leftTextLabel}
                style={{ color: 'red', fontWeight: 'bold' }}
              >
                Please fill all fields
              </p>
            )}
            <p className={classes.leftTextLabel}>Tutoring Subject</p>
            <Grid container justify='center'>
              <input
                placeholder='Arabic Language'
                className={classes.input}
                type='text'
                onChange={(e) => setTutorSubject(e.target.value)}
              ></input>
            </Grid>
            <p className={classes.leftTextLabel}>Headline</p>
            <Grid container justify='center'>
              <input
                placeholder='Enter Here'
                className={classes.input}
                type='text'
                onChange={(e) => setHeadline(e.target.value)}
              ></input>
            </Grid>
            <p className={classes.leftTextLabel}>Requirements</p>
            <Grid container justify='center'>
              <input
                placeholder='Describe Your Needs'
                className={classes.input}
                type='text'
                onChange={(e) => setRequirements(e.target.value)}
              ></input>
            </Grid>
            <p className={classes.leftTextLabel}>Lesson Type</p>
            <Grid container justify='flex-start'>
              <p
                className={online ? classes.ButtonsFilled : classes.Buttons}
                onClick={() => setOnline(true)}
              >
                Online Lessons
              </p>
              <p
                className={online ? classes.Buttons : classes.ButtonsFilled}
                onClick={() => setOnline(false)}
              >
                Local Lessons
              </p>
            </Grid>
            <p className={classes.leftTextLabel}>Price Range</p>
            <Grid container justify='flex-start'>
              <input
                className={classes.priceInput}
                value={minRate}
                type='text'
              ></input>
              <p
                style={{
                  lineHeight: '2px',
                  fontSize: 24,
                  marginLeft: 2,
                  marginRight: 2,
                  marginTop: 18,
                }}
              >
                -
              </p>
              <input
                className={classes.priceInput}
                value={maxRate}
                type='text'
              ></input>
              <p
                style={{
                  color: '#9D9D9D',
                  fontSize: 20,
                  lineHeight: '2px',
                  marginLeft: 10,
                  marginTop: 18,
                }}
              >
                USD
              </p>
            </Grid>
            <AirbnbSlider
              onChange={updateValue}
              ThumbComponent={AirbnbThumbComponent}
              getAriaLabel={(index) =>
                index === 0 ? 'Minimum price' : 'Maximum price'
              }
              defaultValue={[minRate, maxRate]}
            />
            <p className={classes.leftTextLabel}>
              {' '}
              {/* <b>453</b> Tutors Fit Your Needs */}
            </p>
            <p
              className={classes.leftTextLabel}
              style={{
                textDecoration: 'underline dotted #FFB600',
                marginTop: '2vh',
                marginBottom: '5vh',
              }}
            >
              Specify The Age, Level And Specialty Of The Student{' '}
            </p>
            <p className={classes.leftText}>Contact Information</p>

            <p className={classes.leftTextLabel}>Your Name</p>
            <Grid container justify='center'>
              <input
                placeholder='Enter here'
                className={classes.input}
                type='text'
                onChange={(e) => setName(e.target.value)}
              ></input>
            </Grid>
            <p className={classes.leftTextLabel}>Email</p>
            <Grid container justify='center'>
              <input
                placeholder='example@gmail.com'
                className={classes.input}
                type='email'
                onChange={(e) => setEmail(e.target.value)}
              ></input>
            </Grid>
            <p className={classes.leftTextLabel}>Phone Number</p>
            <Grid container justify='center'>
              <input
                placeholder='+298928921 62'
                className={classes.input}
                type='text'
                onChange={(e) => setPhone(e.target.value)}
              ></input>
            </Grid>
            <p className={classes.leftTextLabelSmall}>
              We Will Text You In Approximately One Hour After Somebody Responds
              To Your Request.
            </p>
            <div style={{ textAlign: 'left' }}>
              <p
                className={classes.ButtonsFilled}
                style={{ width: '160px', borderRadius: 2 }}
                onClick={() => onSubmit()}
              >
                Save and continue
              </p>
            </div>
            <p
              className={classes.leftTextLabel}
              style={{ fontSize: 16, fontWeight: 'normal', width: '100%' }}
            >
              By Clicking Save And Continue, You Agree To Yellow Schools Space
              Terms Of Service And Privacy Policy
            </p>
          </div>
        </Grid>
        <Grid item md={3} xs={1}></Grid>
      </Grid>
      <Footer></Footer>
    </div>
  );
}
