import React from "react";
import Header from "../components/nav/topNav";
import { Grid } from "@material-ui/core";
import Footer from "../components/footer/footer";
import RequestCard from "../components/RequestTutor/RequestCard";

export default function AboutPage() {
  return (
    <div>
      <Header></Header>
      <Grid container style={{ background: "#EDEFF0" }}>
        <Grid item md={3} lg={3} xl={3} sm={3} xs={3}></Grid>
        <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
          <Grid container alignItems="center" justify="center">
            <RequestCard></RequestCard>
          </Grid>
        </Grid>
        <Grid item md={3} lg={3} xl={3} sm={3} xs={3}></Grid>
      </Grid>
      <Footer></Footer>
    </div>
  );
}
