import React, { Fragment, useState, useEffect } from "react";

import { Grid } from "@material-ui/core";
import Pagination from "@material-ui/lab/Pagination";
import { makeStyles } from "@material-ui/core/styles";
import Nav from "../components/RequestTutorHome/Nav";
import LoginToolBar from "../components/RequestTutorHome/LoginToolBar";
import TutorReview from "../components/ReviewCard/TutorReview";
import TutorCard from "../components/RequestTutorHome/SingleTutorCard";
import TutorAbout from "../components/RequestTutorHome/SingleTutorAbout";
import RightVideoCard from "../components/RequestTutorHome/SingleTutorVideoCard";
import ScheduleCard from "../components/TutorSchedule/tutorScheduleCalender";
import TutorGirl from "../assets/images/tutorGirl.png";
import { Lessons, LessonCity } from "../Data/Data";
import Footer from "../components/footer/footer";
import Login from "../Routes/Login/login";
import axios from "axios";
import baseUrl from "../baseurl";

const useStyles = makeStyles((theme) => ({
  leftTextLabelLarge: {
    fontSize: 20,
    fontWeight: "lighter",
    marginBottom: 20,
    marginTop: 10,
    textAlign: "left",
  },
  leftTextLabelSmall: {
    fontSize: 12,
    marginTop: -20,
    fontWeight: "lighter",
    textAlign: "left",
  },
  transparentButton: {
    border: "1px solid #FFB600",
    padding: 10,
    color: "#FFB600",
    marginRight: 10,
    borderRadius: 5,
    cursor: "pointer",
    fontSize: 12,
  },
  justifiedParagraph: { textAlign: "left", fontSize: 12, textAlign: "justify" },
  justifiedParagraphHeading: {
    textAlign: "left",
    fontSize: 17,
  },
}));
const SingleTutor = (props) => {
  const classes = useStyles();
  const [tutorData, setTutorData] = useState({});
  const [showVideo, setShowVideo] = useState(false);
  const [tutorDetailData, setTutorDetailData] = useState({
    preferredStudentAge: [],
    preferredStudentLevel: [],
  });
  const [schedule, setSchedule] = useState(null);
  const [show, setShow] = useState(false);

  const getSchedule = async (id) => {
    let token = await localStorage.getItem("yellowToken");
    const config = {
      headers: {
        Authorization: token,
      },
    };

    axios
      .get(`${baseUrl}/api/tutors/getAvailabilty/${id}`, config)
      .then((data) => {
        setSchedule(data.data.data.availabilty);
        setShow(true);
      })
      .catch((e) => {
        setShow(true);
        console.log("suuuuuccessss ERRRR ttttttt :: ", e);
      });
  };
  const getTutorDetails = async () => {
    if (props.location.state) {
      let token = await localStorage.getItem("yellowToken");
      const config = {
        headers: {
          Authorization: token,
        },
      };

      axios
        .get(`${baseUrl}/api/tutors/${props.location.state.tutor._id}`, config)
        .then((data) => {
          console.log("Thisis 22uuuddaataa :: ", data.data.data);
          getSchedule(data.data.data.userId);
          setTutorDetailData(data.data.data);
          setShowVideo(true);
        })
        .catch((e) => {
          console.log("suuuuuccessss ERRRR ttttttt :: ", e);
        });
    }
  };
  useEffect(() => {
    getTutorDetails();
    if (props.location.state) {
      setTutorData(props.location.state.tutor);
    }
  }, []);
  return (
    <>
      <Fragment>
        <LoginToolBar />
        <div style={{ height: "100%", background: "#EDEFF0" }}>
          <Grid container style={{ background: "white", height: "500px" }}>
            <Grid md={2} lg={2} xl={2} sm={1} xs={1}></Grid>
            <Grid md={6} lg={6} xl={6} sm={10} xs={10}>
              <TutorCard
                pic={tutorData.imageUrl ? tutorData.imageUrl : TutorGirl}
                tutorName={
                  tutorData.firstName ? tutorData.firstName : "Matthew T."
                }
                Language={
                  tutorData.subjectTaught ? tutorData.subjectTaught : "English"
                }
                Native={
                  tutorData.nativeLanguage ? tutorData.nativeLanguage : "Native"
                }
                experience="IELTS preparation teacher,15 years experience. Expert in teaching"
                activeStudents={tutorData.lessons ? tutorData.lessons : 0}
                lessons={tutorData.lessons ? tutorData.lessons : 0}
                stars={tutorData.rating_average ? tutorData.rating_average : 4}
                hourlyRate={tutorData.hourlyRate ? tutorData.hourlyRate : 20}
                reviewCount={89}
                tutor={tutorData}
                tutorDetail={tutorDetailData}
              ></TutorCard>
              <TutorAbout tutorDetail={tutorDetailData} />
              {show ? (
                <ScheduleCard
                  tutorDetail={tutorDetailData}
                  schedule={schedule}
                />
              ) : null}
              <div style={{ marginTop: "2rem" }}>
                <div style={{ display: "flex" }}>
                  <h3 style={{ marginBottom: "1rem" }}>Reviews</h3>
                </div>

                <TutorReview tutorData={tutorData} />
              </div>
            </Grid>
            <Grid md={3} lg={3} xl={3} sm={4} xs={4}>
              <Grid container direction="column" alignItems="center">
                {showVideo ? (
                  <RightVideoCard
                    tutorDetail={tutorDetailData}
                  ></RightVideoCard>
                ) : null}
              </Grid>
            </Grid>
            <Grid md={1} lg={1} xl={1} sm={0} xs={0}></Grid>
            <Grid
              md={12}
              lg={12}
              xl={12}
              sm={12}
              xs={12}
              style={{ marginTop: "3rem" }}
            >
              <Footer />
            </Grid>
          </Grid>
        </div>
      </Fragment>
    </>
  );
};

export default SingleTutor;
