import React, { useState, useEffect } from "react";
import Header from "../components/nav/topNav";
import ScheduleCard from "../components/BlogPage/ScheduleCard";
import { Grid } from "@material-ui/core";
import BlogsCard2 from "../components/BlogPage/BlogCard2";
import Footer from "../components/footer/footer";
import baseUrl from "../baseurl";
import axios from "axios";

export default function BlogPage() {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    getAllPosts();
  }, []);

  const getAllPosts = () => {
    axios.get(`${baseUrl}/api/posts/allposts`).then((data) => {
      console.log("mnbjkbjkn :: ", data.data.data);
      setPosts(data.data.data);
    });
  };

  return (
    <div>
      <Header></Header>
      <div style={{ background: "#EDEFF0" }}>
        <Grid container>
          <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
          <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
            <ScheduleCard></ScheduleCard>
          </Grid>
          <Grid item md={7} lg={7} xl={7} sm={12} xs={12}>
            <Grid container direction='column' style={{ padding: "1rem" }}>
              <h1
                style={{
                  textAlign: "left",
                  color: "#FFB600",
                }}>
                Nazar's Blog
              </h1>
              {/* <BlogsCard></BlogsCard> */}
              {posts &&
                posts.map((data, index) => {
                  return <BlogsCard2 post={data} index={index}></BlogsCard2>;
                })}
              {/* <BlogsCard3></BlogsCard3>
              <BlogsCard4></BlogsCard4>
              <BlogsCard5></BlogsCard5> */}
            </Grid>
          </Grid>
          <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
        </Grid>
        <Footer></Footer>
      </div>
    </div>
  );
}
