import React, { useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import CircularProgress from '@material-ui/core/CircularProgress';
import Box from '@material-ui/core/Box';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import baseUrl from '../../../baseurl';

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: '#FFFFFF',
    padding: '2rem',
    borderRadius: 5,
    marginTop: '3rem',
  },
  heading1: {
    color: '#6F757B',
    fontSize: 30,
  },
  buttonNext: {
    marginTop: theme.spacing(5),
    backgroundColor: '#FFB600',
    color: 'white',
  },
  buttonBack: {
    marginTop: theme.spacing(5),
    marginRight: theme.spacing(2),
    backgroundColor: '#F8F8F8',
    color: '#FFB600',
    borderColor: '#FFB600',
    borderWidth: 1,
    borderStyle: 'solid',
  },
  buttonUpload: {
    marginTop: theme.spacing(5),
    backgroundColor: 'rgba(255, 182, 0, 0.3)',
    color: 'white',
    borderWidth: 0,
    padding: '0.5rem',
  },
  p1: {
    fontSize: 25,
    padding: 0,
    margin: 0,
    color: '#6F757B',
  },
  p2: {
    fontSize: 18,
    padding: 0,
    margin: 0,
    color: '#6F757B',
  },
  p3: {
    fontSize: 18,
    padding: 0,
    margin: 0,
    color: '#384047',
    marginLeft: theme.spacing(1),
  },
  p4: {
    fontSize: 18,
    padding: 0,
    margin: 0,
    color: '#384047',
    marginLeft: theme.spacing(4),
  },
  p5: {
    fontSize: 18,
    padding: 0,
    margin: 0,
    color: '#5EC429',
    marginLeft: theme.spacing(1),
  },
  icon: {
    color: '#FFB600',
  },
  icon2: {
    color: '#5EC429',
  },
}));
export default function Verification(props) {
  const classes = useStyles();
  const [file, setFile] = useState(null);
  const [error, setError] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [selectedImage, setSelectedImage] = React.useState(null);
  const [isUploaded, setisUploaded] = React.useState(false);
  const handleFile = (e) => {
    var file = e.target.files[0];
    let reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      setSelectedImage(reader.result);
    };
    setFile(file);
  };

  const onSubmit = async () => {
    setError(false);
    setLoading(true);

    if (file === null) {
      setError(true);
      setLoading(false);
    } else {
      var bodyFormData = new FormData();
      bodyFormData.set('image', file);

      axios
        .put(baseUrl + '/api/tutors/addDocument/' + props.tutorId, bodyFormData)
        .then(async (res) => {
          setLoading(false);
          setisUploaded(true);
          props.handleNext();
        })
        .catch((error) => {
          setLoading(false);
          setError(true);
          console.log('Error uploading image :: ', error);
        });
    }
  };

  return (
    <div
      style={{
        paddingBottom: '3rem',
        backgroundColor: '#EDEFF0',
      }}
    >
      <Container maxWidth='md' className={classes.container} boxShadow={3}>
        <Typography
          className={classes.heading1}
          variant='subtitle1'
          align='left'
          gutterBottom
        >
          Profile verification
        </Typography>
        {error ? (
          <Grid container alignItems='left'>
            <p style={{ fontSize: 13, color: 'red' }}>Please upload file</p>
          </Grid>
        ) : null}
        {isUploaded ? (
          <Grid container alignItems='left'>
            <p style={{ fontSize: 13, color: 'green' }}>
              File uploaded successfully
            </p>
          </Grid>
        ) : null}
        <p align='left' className={classes.p1}>
          Confirm your identity
        </p>

        <div
          style={{
            backgroundColor: 'rgba(255, 182, 0, 0.3)',
            padding: '2rem',
          }}
        >
          <Box display='flex' flexDirection='row'>
            <CheckCircleIcon className={classes.icon} />
            <p align='left' className={classes.p3}>
              Become a verified tutor
            </p>
          </Box>{' '}
          <p align='left' className={classes.p4}>
            Please upload a photo of you holding your passport. Make sure your
            passport's photo page is open, with your name and face clearly
            visible.
          </p>
        </div>
        <Box
          display='flex'
          justifyContent='flex-start'
          style={{ marginTop: '1rem' }}
        >
          {selectedImage && (
            <img
              src={selectedImage}
              style={{ width: '5rem', height: '5rem' }}
            />
          )}
        </Box>
        <Box display='flex' justifyContent='flex-start'>
          <div class='upload-btn-wrapper'>
            <Button variant='contained' className={classes.buttonNext}>
              {' '}
              Upload documents
            </Button>
            <input type='file' onChange={handleFile} name='myfile' />
          </div>
          {/* <Button
            variant='contained'
            className={classes.buttonUpload}
            onClick={() => props.handleNext()}>
            Upload documents
          </Button> */}
        </Box>

        <div
          style={{
            padding: '2rem',
          }}
        >
          <Box display='flex' flexDirection='row'>
            <LockOpenIcon className={classes.icon2} />
            <p align='left' className={classes.p5}>
              Safe
            </p>
          </Box>{' '}
          <p align='left' className={classes.p4}>
            We do not store your documents on our servers after verification is
            complete
          </p>
        </div>

        <Box display='flex' justifyContent='flex-start'>
          <Button variant='contained' className={classes.buttonBack}>
            back
          </Button>

          <Button
            variant='contained'
            disabled={loading}
            className={classes.buttonNext}
            onClick={() => onSubmit()}
          >
            {loading === false ? (
              'Complete Registration'
            ) : (
              <CircularProgress size={25} />
            )}
          </Button>
        </Box>
      </Container>
    </div>
  );
}
