import React, { useEffect, useState } from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { findTutorDetailPage2 } from '../../Translation/index';
import PrivacyPolicy from '../../assets/PrivacyPolicy.docx';
import { footerPageTop } from './footerJSON';
const useStyles = makeStyles((theme) => ({
  footerText: {
    color: 'white',
    textDecoration: 'none',
    textAlign: 'left',
    fontSize: 18,
    fontWeight: 'bold',
  },
  subHeadingText: {
    color: 'white',
    textDecoration: 'none',
    textAlign: 'left',
    marginTop: 5,
  },
}));
export default function Footer() {
  const classes = useStyles();
  const [userData, setUserData] = useState(null);
  const language = useSelector((state) => state.languageReducer.language);
  useEffect(() => {
    getUserData();
  }, []);

  const getUserData = async () => {
    let tutor = await localStorage.getItem('yellowTutor');
    let student = await localStorage.getItem('yellowStudent');
    if (tutor) {
      setUserData(tutor);
    }
    if (student) {
      setUserData(student);
    }
  };

  return (
    <div style={{ background: '#262626' }}>
      <Grid
        container
        direction='row'
        alignItems='flex-start'
        justify='flex-start'
      >
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={10}
          xs={10}
          style={{ borderBottom: '1px solid #DADFE1', paddingBottom: 30 }}
        >
          <Grid container style={{ marginTop: 30 }}>
            <Grid
              style={{ textAlign: 'left', marginTop: 20 }}
              item
              md={4}
              lg={4}
              xl={4}
              sm={12}
              xs={12}
            >
              <p className={classes.footerText}>
                {/* Quick Links */}
                {findTutorDetailPage2[31][language]}
              </p>
              <Grid container direction='column' style={{ marginTop: 20 }}>
                {/* <Link to='/about' className={classes.subHeadingText}>
           
                  {findTutorDetailPage2[32][language]}
                </Link> */}
                {/* <Link to='/helpcenter' className={classes.subHeadingText}> */}
                {/* Help Center  */}
                {/* {findTutorDetailPage2[33][language]}
                </Link> */}
                <Link to='/requestTutorHome' className={classes.subHeadingText}>
                  {/* Find a tutor */}
                  {footerPageTop[1][language]}
                </Link>
                <Link to='/blog' className={classes.subHeadingText}>
                  {/* Nazar's blog */}
                  {findTutorDetailPage2[34][language]}
                </Link>
              </Grid>
            </Grid>
            {/* <Grid
              style={{ textAlign: "left", marginTop: 20 }}
              item
              md={3}
              lg={3}
              xl={3}
              sm={12}
              xs={12}>
             
              <Grid container direction='column' style={{ marginTop: 20 }}>
                

                <Link to='/inviteFriends' className={classes.subHeadingText}>
     
                  {userData && findTutorDetailPage2[36][language]}
                </Link>
              </Grid>
            </Grid> */}
            <Grid
              style={{ textAlign: 'left', marginTop: 20 }}
              item
              md={4}
              lg={4}
              xl={4}
              sm={12}
              xs={12}
            >
              <p className={classes.footerText}>
                {/* My Account */}
                {findTutorDetailPage2[39][language]}
              </p>
              <Grid container direction='column' style={{ marginTop: 20 }}>
                <Link to='/privacypolicy' className={classes.subHeadingText}>
                  {/* Privacy Policy */}
                  {findTutorDetailPage2[41][language]}
                </Link>
              </Grid>
              <Grid container direction='column' style={{ marginTop: 20 }}>
                <Link href='#' className={classes.subHeadingText}>
                  {/* Terms & Conditions */}
                  {/* {findTutorDetailPage2[40][language]} */}
                </Link>
              </Grid>

              <Grid container direction='column' style={{ marginTop: 20 }}>
                <Link to='/inviteFriends' className={classes.subHeadingText}>
                  {/* Referral program */}
                  {/* {userData && findTutorDetailPage2[36][language]} */}
                </Link>
              </Grid>
            </Grid>
            <Grid
              style={{ textAlign: 'left', marginTop: 20 }}
              item
              md={4}
              lg={4}
              xl={4}
              sm={12}
              xs={12}
            >
              <p className={classes.footerText}>
                {/* Support */}
                {footerPageTop[2][language]}
              </p>
              <Grid container direction='column' style={{ marginTop: 20 }}>
                <Link
                  href='#'
                  className={classes.subHeadingText}
                  onClick={() => {
                    window.open(
                      'mailto:support@yellowschool.world?subject=Subject&body=Body%20goes%20here'
                    );
                  }}
                >
                  support@yellowschool.world
                </Link>
                {/* {findTutorDetailPage2[38][language]} */}

                {/* <Link href="#" className={classes.subHeadingText}>
                  Tutoring jobs
                </Link> */}
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
      <Grid
        container
        direction='row'
        alignItems='flex-start'
        justify='flex-start'
      >
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={10}
          xs={10}
          style={{ paddingBottom: 30 }}
        >
          <Grid container style={{ marginTop: 30 }}>
            <Grid
              style={{ textAlign: 'left', marginTop: 20 }}
              item
              md={3}
              lg={3}
              xl={3}
              sm={12}
              xs={12}
            >
              {/* <p className={classes.footerText}>Support</p>
              <Grid container direction='column' style={{ marginTop: 20 }}>
                <p className={classes.subHeadingText}>Need any help?</p>
                <Link href='#' className={classes.subHeadingText}>
                  support@Yellowschool.world
                </Link>
              </Grid> */}
            </Grid>
            {/* <Grid
              style={{ textAlign: "left", marginTop: 20 }}
              item
              md={3}
              lg={3}
              xl={3}
              sm={12}
              xs={12}
            >
              <p className={classes.footerText}>
                Address
              </p>
              <Grid container direction="column" style={{ marginTop: 20 }}>
                <p className={classes.subHeadingText}>
                  USA
                </p>
                <p className={classes.subHeadingText}>
                  1371 Beacon Street, Suite 301, Brookline, MA, 02446
                </p>
              </Grid>
            </Grid> */}
            <Grid
              style={{ textAlign: 'left' }}
              item
              md={3}
              lg={3}
              xl={3}
              sm={12}
              xs={12}
            ></Grid>
          </Grid>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
}
