import React, { useState } from "react";
import { Grid } from "@material-ui/core";
import CommentPic from "../../assets/images/commentPic.png";
import Facebook from "../../assets/images/facebook.png";
import Twitter from "../../assets/images/twitter.png";
import Reddit from "../../assets/images/reddit.png";
import Linkedin from "../../assets/images/linkedin.png";
import Mail from "../../assets/images/mail.png";
import Collapse from "@material-ui/core/Collapse";

import { makeStyles } from "@material-ui/core/styles";
import { LanguagesData } from "../../Data/Data";

const useStyles = makeStyles((theme) => ({
  Card: {
    background: "white",
    borderRadius: 10,
    width: "100%",
    marginTop: "2rem",
    padding: "1rem",
    paddingLeft: "2rem",
  },
  yellowText: {
    color: "#FFB600",
    textAlign: "left",
    marginBottom: 5,
  },
  circlePic: {
    width: 25,
    height: 25,
    borderRadius: 15,
  },
  blogDetailText: {
    marginLeft: 5,
    fontSize: 12,
  },
  blogText: {
    fontSize: 14,
    textAlign: "justify",
    width: "95%",
  },
  socialIcons: {
    marginRight: 25,
  },
}));

export default function BlogsCard() {
  const [showDetail, setShowDetail] = useState(false);
  const classes = useStyles();
  return (
    <div >
       <p style={{ fontSize: 20 }} onClick={() => setShowDetail(!showDetail)}>
          We are currently updating the questions
          </p>
      <Collapse in={showDetail} timeout='auto' unmountOnExit>
        <p className={classes.blogText}>
          Answers are shown here .....
        </p>
      </Collapse>
    </div>
  );
}
