import React from "react";
import { Grid } from "@material-ui/core";

import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  Card: {
    background: "white",
    borderRadius: 10,
    width: "80%",
    height: 250,
    marginTop: 20,
    paddingLeft: 10,
    "&$after": {
      background:
        "linear-gradient(to right,#bcbcbc 25%,#ffcd02 25%,#ffcd02 50%,#e84f47 50%,#e84f47 75%,#65c1ac 75%)",
      position: "absolute",
      content: "",
      height: "4px",
      right: 0,
      left: 0,
      top: 0,
    },
    borderTop:
      "1px solid linear-gradient(to right,#bcbcbc 25%,#ffcd02 25%,#ffcd02 50%,#e84f47 50%,#e84f47 75%,#65c1ac 75%)",
  },
  image: {
    marginTop: 25,
    width: 60,
  },
  heading: {
    marginTop: 0,
    fontSize: 25,
    fontWeight: "lighter",
  },
  detail: {
    textAlign: "justify",
    width: "80%",
    fontWeight: "lighter",
    paddingLeft: 25,
    fontSize: 18,
  },
  transparentButton: {
    border: "1px solid gray",
    width: "65%",
    padding: 10,
    borderRadius: 5,
    marginBottom: 30,
  },
}));

export default function RainBowCard(props) {
  const classes = useStyles();
  return (
    <div className="mulitColor">
      <img className={classes.image} src={props.icon}></img>
      <p className={classes.heading}>{props.Heading}</p>
      <p className={classes.detail}>{props.Detail}</p>
      <Grid
        style={{ width: "100%" }}
        container
        aligItems="center"
        justify="center"
      >
        <span className={classes.transparentButton}>{props.buttonText}</span>
      </Grid>
    </div>
  );
}
