import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Master from "../../assets/m2.png";
import American from "../../assets/a2.png";
import Visa from "../../assets/v2.png";
import Discover from "../../assets/d2.png";
import { Grid, Box, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  card: {
    background: "white",
    margin: "1rem",
    marginTop: 0,
    borderRadius: 5,
  },
  pass: {
    color: "#6F757B",
    fontSize: 26,
    textAlign: "left",
    padding: "1rem",
    paddingLeft: "2rem",
    borderBottomStyle: "solid",
    borderBottomWidth: 1,
    borderBottomColor: "#DADFE1",
  },
  stc: {
    color: "#384047",
    fontSize: 23,
    textAlign: "left",
  },
  stcp: {
    color: "#6F757B",
    fontSize: 17,
    textAlign: "left",
  },
  logos: {
    textAlign: "left",
  },
  button: {
    backgroundColor: "#FFB600",
    color: "white",
  },
  pp: {
    color: "#6F757B",
    fontSize: 17,
    marginLeft: "2rem",
    marginRight: "1rem",
    textAlign: "left",
  },
}));

export default function PaymentMethod() {
  const classes = useStyles();
  return (
    <div className={classes.card}>
      <div>
        <p className={classes.pass}>Lesson autoconfirmation</p>
      </div>
      <div style={{ marginTop: "2rem", marginLeft: "2rem" }}>
        <p className={classes.stc}>Save a card</p>
      </div>

      <div>
        <Grid container>
          <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
            <p className={classes.pp}>
              Lessons will be confirmed automatically 72 hours after the
              scheduled end time unless you report an issue.When a lesson is
              confirmed, we release payment to your tutor.
            </p>
          </Grid>
          <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
            <p className={classes.pp}>
              Autoconfirmation for lessons in the Yellow School Space
              Autoconfirm tracks the time you spend connected with your tutor in
              the Yellow School Space. 15 minutes after a lesson is completed,
              it is confirmed automatically and we release your tutor's payment.
              If there are any issues, please contact us right after the lesson
              and we'll help you find a solution.
            </p>
          </Grid>
        </Grid>
      </div>

      <Box display='flex' style={{ marginTop: "2rem" }}></Box>
    </div>
  );
}
