import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Master from "../../assets/m2.png";
import American from "../../assets/a2.png";
import Visa from "../../assets/v2.png";
import Google from "../../assets/g2.png";
import { Grid, Box, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  card: {
    background: "white",
    margin: "1rem",
    marginTop: 0,
    borderRadius: 5,
  },
  pass: {
    color: "#6F757B",
    fontSize: 26,
    textAlign: "left",
    padding: "1rem",
    paddingLeft: "2rem",
    borderBottomStyle: "solid",
    borderBottomWidth: 1,
    borderBottomColor: "#DADFE1",
  },
  stc: {
    color: "#384047",
    fontSize: 23,
    textAlign: "left",
  },
  stcp: {
    color: "#6F757B",
    fontSize: 17,
    textAlign: "left",
  },
  logos: {
    textAlign: "left",
  },
  button: {
    backgroundColor: "#FFB600",
    color: "white",
    width: "80%",
    fontSize: 17,
    marginTop: "1rem",
  },
  pp: {
    color: "#6F757B",
    fontSize: 17,
    marginLeft: "2rem",
    marginRight: "1rem",
    textAlign: "left",
  },
  gp: {
    color: "#384047",
    fontSize: 17,
    textAlign: "left",
  },
}));

export default function PaymentMethod() {
  const classes = useStyles();
  return (
    <div className={classes.card}>
      <div>
        <p className={classes.pass}>Google Calendar</p>
      </div>

      <div
        style={{ marginLeft: "2rem", marginTop: "3rem", marginRight: "2rem" }}>
        <Grid container>
          <Grid item md={2} lg={2} xl={2} sm={12} xs={12}>
            <img src={Google} style={{ margin: "1rem" }} />
          </Grid>

          <Grid item md={5} lg={5} xl={5} sm={12} xs={12}>
            <p className={classes.gp}>
              Connect your Google Calendar and synchronize all your Yellow
              Schools Space lessons with your personal schedule
            </p>
          </Grid>
          <Grid item md={5} lg={5} xl={5} sm={12} xs={12}>
            <Button
              variant='contained'
              className={classes.button}
              disableElevation>
              Connect Google Calendar
            </Button>
          </Grid>
        </Grid>
      </div>

      <Box display='flex' style={{ marginTop: "2rem" }}></Box>
    </div>
  );
}
