import React, { Component } from "react";

import { Grid } from "@material-ui/core";
import moment from "moment";
import SingleCard from "./singleCard";

export default class cards extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstDay: this.props.firstDay,
    };
  }
  render() {
    let someDate = new Date(this.state.firstDay);
    someDate.setDate(someDate.getDate());

    const CARD_OPTIONS = [
      {
        day: moment(someDate.setDate(someDate.getDate())).format("ddd"),
        style: { backgroundColor: "yellow" },
        dayStyle: { backgroundColor: "orange", borderRadius: "45%" },
      },
      {
        day: moment(someDate.setDate(someDate.getDate() + 1)).format("ddd"),
      },
      {
        day: moment(someDate.setDate(someDate.getDate() + 1)).format("ddd"),
      },
      {
        day: moment(someDate.setDate(someDate.getDate() + 1)).format("ddd"),
      },
      {
        day: moment(someDate.setDate(someDate.getDate() + 1)).format("ddd"),
      },
      {
        day: moment(someDate.setDate(someDate.getDate() + 1)).format("ddd"),
      },
      {
        day: moment(someDate.setDate(someDate.getDate() + 1)).format("ddd"),
      },
    ];

    return (
      <Grid container justify="space-around">
        {CARD_OPTIONS.map(({ day, style, dayStyle }, index) => (
          <Grid
            key={index}
            item
            xs={3}
            sm={3}
            md={"auto"}
            lg={"auto"}
            xl="auto"
          >
            <SingleCard
              day={day}
              date={moment(someDate.setDate(someDate.getDate())).format("DD")}
              cardStyle={style}
              dayStyle={dayStyle}
            />
          </Grid>
        ))}
      </Grid>
    );
  }
}
