import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Button from '@material-ui/core/Button';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import dateFormat from 'dateformat';
import CloseIcon from '@material-ui/icons/Close';
import ReactStars from 'react-rating-stars-component';
import CircularProgress from '@material-ui/core/CircularProgress';
import StripeContainer from './stripeContainer';
import BaloonImage from '../../assets/popups/baloon.png';
import TeacherImage from '../../assets/popups/teacher.png';
import baseUrl from '../../baseurl';
import { allowSelection } from '@fullcalendar/react';
import { WorkRounded } from '@material-ui/icons';

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper1: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  paper: {
    position: 'absolute',
    width: 700,
    height: 742,
    backgroundColor: theme.palette.background.paper,
    top: '10%',
    left: '30%',
    border: 'none',
    paddingLeft: '4rem',
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TransactionModal(props) {
  const classes = useStyles();
  const history = useHistory();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);
  const [startTime, setStartTime] = React.useState('');
  const [showConfirmLesson, setShowConfirmLesson] = useState(false);
  const [showHowLesson, setShowHowLesson] = useState(false);
  const [showRateLesson, setShowRateLesson] = useState(false);
  const [showCompleteLesson, setShowCompleteLesson] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const bookLesson = async () => {
    setOpen(true);
    let token = await localStorage.getItem('yellowToken');
    let stu = await localStorage.getItem('yellowStudent');

    const config = {
      headers: {
        Authorization: token,
      },
    };
    let newStu = await JSON.parse(stu);
    let data = props.lessonData;
    console.log('Dataaaa :: ', data);
    data.email = newStu.email;
    data.studentId = newStu._id;
    console.log('ajhsgdjhasgdjashgd posted success:: aa', data);
    axios
      .post(`${baseUrl}/api/lessonRequest`, data, config)
      .then((data) => {
        console.log('ajhsgdjhasgdjashgd posted success:: ', data);
        setOpen(false);
        setShowConfirmLesson(true);
      })
      .catch((e) => {
        setOpen(false);
        // props.closeModel();
        console.log('ajhsgdjhasgdjashgd posted error:: ', e);
      });
  };
  const calculateCurrency = () => {
    let curr = '';
    if (props.tutorDetail.currency === 'Ukrainian') {
      curr = 'UAH';
    } else if (props.tutorDetail.currency === 'Yuan') {
      curr = 'CN';
    } else if (props.tutorDetail.currency === 'Euro') {
      curr = 'EU';
    } else {
      curr = 'USD';
    }

    return curr;
  };
  const body = (
    <div style={modalStyle} className={classes.paper1}>
      <h2 id='simple-modal-title'>Book Your Lesson</h2>
      <h4 id='simple-modal-title'>
        Total Price : {props.tutorDetail.hourlyRate} {calculateCurrency()}
      </h4>
      <p id='simple-modal-description'>
        {/* {dateFormat(props.lessonData.startTime, "h:MM:ss TT")} -{" "}
        {dateFormat(props.lessonData.endTime, "h:MM:ss TT")} */}
        {props.lessonData.startTime} - {props.lessonData.endTime}
      </p>
      <StripeContainer
        bookLesson={bookLesson}
        rate={props.tutorDetail.hourlyRate}
      />
      <Button
        variant='contained'
        style={{
          color: 'white',
          backgroundColor: '#ffcd02',
          marginRight: '2rem',
        }}
        onClick={() => props.closeModel()}
      >
        Cancel
      </Button>
      {/* <Button
        variant='contained'
        style={{ color: "white", backgroundColor: "#ffcd02" }}
        disabled={true}>
        {open ? (
          <CircularProgress size='1.5rem' color='white' />
        ) : (
          "Book Lesson"
        )}
      </Button> */}
    </div>
  );

  return (
    <div>
      <Modal
        open={props.show}
        onClose={props.closeModel}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        {body}
      </Modal>
      <Modal open={showConfirmLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
            }}
          >
            <CloseIcon
              style={{ cursor: 'pointer' }}
              onClick={() => {
                setShowConfirmLesson(false);
              }}
            />
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              marginTop: '1rem',
            }}
          >
            <img src={BaloonImage} style={{ width: 363, height: 327 }} />
          </div>
          <h2
            id='simple-modal-title'
            style={{ fontSize: 35, marginTop: '2rem' }}
          >
            Lesson confirmed
          </h2>
          <p
            id='simple-modal-description'
            style={{ fontSize: 25, marginTop: '3rem' }}
          >
            Every lesson is a step to fluency
          </p>

          <Button
            variant='contained'
            style={{
              background: '#FFB600',
              color: 'white',
              fontSize: 22,
              marginTop: '3rem',
            }}
            onClick={() => {
              history.push('/studentMyLessons');
            }}
          >
            Continue
          </Button>
        </div>
      </Modal>

      <Modal open={showHowLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
            }}
          >
            <CloseIcon style={{ cursor: 'pointer' }} />
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-start',
              marginTop: '3rem',
            }}
          >
            <img
              src={TeacherImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id='simple-modal-title'
            style={{ fontSize: 35, marginTop: '2rem' }}
          >
            How was your lesson
          </h2>
          <p
            id='simple-modal-description'
            style={{ fontSize: 25, marginTop: '3rem' }}
          >
            Rating is anonymous and helps you track <br /> you progress
          </p>

          <div style={{ marginTop: '2rem' }}>
            <ReactStars count={5} size={52} activeColor='#ffd700' />
          </div>

          <Button
            variant='contained'
            style={{
              background: '#FFB600',
              color: 'white',
              fontSize: 22,
              marginTop: '3rem',
              width: 168,
              height: 60,
            }}
            onClick={() => {
              setShowConfirmLesson(false);
              setShowHowLesson(false);
              setShowRateLesson(true);
              setShowCompleteLesson(false);
            }}
          >
            Continue
          </Button>
        </div>
      </Modal>

      <Modal open={showRateLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
            }}
          >
            <CloseIcon style={{ cursor: 'pointer' }} />
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-start',
              marginTop: '1rem',
            }}
          >
            <img
              src={TeacherImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id='simple-modal-title'
            style={{ fontSize: 35, marginTop: '4rem' }}
          >
            Lesson rated! Ready to learn <br />
            from Nazar
          </h2>
          <p
            id='simple-modal-description'
            style={{ fontSize: 25, marginTop: '4rem' }}
          >
            Keep up the good work! Reserve your favorite <br />
            lesson times to continue your progress
          </p>

          <Button
            variant='contained'
            style={{
              background: '#FFB600',
              color: 'white',
              fontSize: 22,
              marginTop: '4rem',
              width: 168,
              height: 60,
            }}
            onClick={() => {
              setShowConfirmLesson(false);
              setShowHowLesson(false);
              setShowRateLesson(false);
              setShowCompleteLesson(false);
            }}
          >
            Buy Hours
          </Button>
        </div>
      </Modal>

      <Modal open={showCompleteLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-end',
            }}
          >
            <CloseIcon style={{ cursor: 'pointer' }} />
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-start',
              marginTop: '1rem',
            }}
          >
            <img
              src={TeacherImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id='simple-modal-title'
            style={{ fontSize: 35, marginTop: '4rem' }}
          >
            Did you complete your lessons <br />
            with Nazar?
          </h2>
          <p
            id='simple-modal-description'
            style={{ fontSize: 25, marginTop: '4rem' }}
          >
            Monday, May 17 at 17:30 1 hour English language <br />
            lesson
          </p>

          <Button
            variant='contained'
            style={{
              background: '#FFB600',
              color: 'white',
              fontSize: 22,
              marginTop: '4rem',
            }}
            onClick={() => {
              setShowConfirmLesson(false);
              setShowHowLesson(true);
              setShowRateLesson(false);
              setShowCompleteLesson(false);
            }}
          >
            Yes, Confirm Lessons
          </Button>
          <br />
          <Button
            variant='contained'
            style={{
              background: '#F8E0A4',
              color: '#DFA003',
              fontSize: 22,
              marginTop: '1rem',
            }}
          >
            Report issue
          </Button>
        </div>
      </Modal>
    </div>
  );
}
