import React, { useState, useEffect } from 'react';
import NavBar from '../components/nav/topNav';
import { useSelector, useDispatch } from 'react-redux';
import Modal from '@material-ui/core/Modal';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import FacebookSquare from '../assets/images/facebookSquare.png';
import logo from '../assets/logo.svg';
import GoogleSquare from '../assets/images/googleSquare.png';
import Button from '@material-ui/core/Button';
import ManStanding from '../assets/teacher.jpeg';
import LightStrokeIcon from '../assets/images/whiteLieght.png';
import UserBook from '../assets/images/userBook.png';
import CalendarFill from '../assets/images/calendarFill.png';
import SearchBig from '../assets/images/searchBig.png';
import MoneyBig from '../assets/images/moneyBig.png';
import Clock from '../assets/images/Clock.png';
import Security from '../assets/images/security.png';
import { useHistory } from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Comments } from '../Data/Data';
import Footer from '../components/footer/footer';
import { Link } from 'react-router-dom';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import { GoogleLogin } from 'react-google-login';
import { loginPage } from '../Routes/Login/translation';
import baseUrl from '../baseurl';
import axios from 'axios';

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  TextLabelLarge: {
    fontSize: 35,
  },
  TextLabelMedium: {
    fontSize: 35,
  },
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  threeRowsColOne: {
    fontSize: 28,
    width: '50%',
    height: '100%',
    textAlign: 'left',
  },
  smallTextLeft: { fontSize: 13, width: '60%', textAlign: 'left' },
}));

function BecomeStudentPage(props) {
  let history = useHistory();
  const classes = useStyles();
  const [email, setEmail] = useState('');
  const [name, setName] = useState('');
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);
  const [confirmPassword, setConfirmPassword] = useState('');
  const dispatch = useDispatch();
  const [error, setError] = useState(null);
  const [showError, setShowError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [errorMessage, setErrorMessage] = useState('asdfsdfsd');
  const language = useSelector((state) => state.languageReducer.language);
  const loggingIn = useSelector((state) => state.auth.loggingIn);
  const user = useSelector((state) => state.auth.user);
  const [password, setPassword] = useState('');
  const login = (e) => {
    setShowError(false);
    setLoading(true);
    if (confirmPassword !== password) {
      setErrorMessage('Password and Confirm Password not matched');
      setShowError(true);
      setLoading(false);
      return;
    }

    if (email === '' || password === '' || name === '') {
      setErrorMessage('Please fill all fields');
      setShowError(true);
      setLoading(false);
      return;
    }

    axios({
      method: 'post',
      url: `${baseUrl}/api/users`,
      data: { email: email, password: password, name: name },
    })
      .then(async (result) => {
        setLoading(false);
        setOpen(true);
      })
      .catch(function (response) {
        //handle error
        console.log('This is response eerroorrr :: ', response);
        setErrorMessage('Email already registered');
        setShowError(true);
        setLoading(false);
      });
  };

  const loginFacebookGoogle = (nameC, emailC, passwordC) => {
    setShowError(false);
    setLoading(true);

    axios({
      method: 'post',
      url: `${baseUrl}/api/users`,
      data: { email: emailC, password: passwordC, name: nameC },
    })
      .then(async (result) => {
        setLoading(false);
        console.log('Thsis iskjahdskjas user name :: ', result.data);
        history.push('/login');
      })
      .catch(function (response) {
        //handle error
        console.log('This is response eerroorrr :: ', response);
        setErrorMessage('Email already registered');
        setShowError(true);
        setLoading(false);
      });
  };

  const responseFacebook = (response) => {
    loginFacebookGoogle(response.name, response.email, '123456');
    // loginGoogle(response.email, "123456")
  };

  const responseGoogle = (response) => {
    loginFacebookGoogle(response.wt.Ad, response.wt.cu, '123456');
  };

  const responseGoogleError = (response) => {
    // console.log("This is google eror ::", response);
  };

  useEffect(() => {
    if (JSON.parse(localStorage.getItem('yellowTutor'))) {
      history.push('/dashboard');
    }
  }, []);

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id='simple-modal-title'>Email verification sent</h2>

      <p id='simple-modal-description'>
        We have send a confirmation email to you. Please verify your email
      </p>

      <Button
        variant='contained'
        style={{
          color: 'white',
          backgroundColor: 'green',
          marginRight: '2rem',
        }}
        onClick={() => history.push('/login')}
      >
        Go to login
      </Button>
    </div>
  );

  return (
    <div>
      <NavBar></NavBar>
      <Grid container style={{ marginTop: 10 }} direction='row'>
        <Grid item md={3} lg={3} xl={3} sm={1} xs={1}></Grid>
        <Grid item md={6} lg={6} xl={6} sm={10} xs={10}>
          <p className={classes.TextLabelLarge}>
            The world best teachers you can find here
          </p>
          <Grid container>
            <Grid item md={3} xs={1}></Grid>
            <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
              {showError ? (
                <p style={{ color: 'red' }}>{errorMessage}</p>
              ) : null}
              <form style={{ marginBottom: '1rem' }}>
                <p style={{ textAlign: 'left', marginBottom: '0px' }}>Name*</p>
                <input
                  style={{
                    width: '100%',
                    height: 45,
                    padding: 10,
                    marginBottom: '1vh',
                  }}
                  value={name}
                  name='name'
                  onChange={(val) => setName(val.target.value)}
                  type='text'
                ></input>
                <p style={{ textAlign: 'left' }}>
                  {/* Email address */}
                  {loginPage[0][language]}*
                </p>
                <input
                  style={{ width: '100%', height: 45, padding: 10 }}
                  onChange={(val) => setEmail(val.target.value)}
                  type='text'
                ></input>
                <p style={{ textAlign: 'left' }}>
                  {/* Password */}
                  {loginPage[1][language]}*
                </p>
                <input
                  style={{ width: '100%', height: 45, padding: 10 }}
                  onChange={(val) => setPassword(val.target.value)}
                  type='password'
                ></input>
                <p style={{ textAlign: 'left', marginBottom: '0px' }}>
                  Confirm Password*
                </p>
                <input
                  style={{
                    width: '100%',
                    height: 45,
                    padding: 10,
                    marginBottom: '1vh',
                  }}
                  name='password'
                  onChange={(val) => setConfirmPassword(val.target.value)}
                  type='password'
                ></input>
                <Grid
                  container
                  direction='row'
                  alignItems='center'
                  justify='center'
                  style={{ background: '#FFB600', marginTop: 15 }}
                >
                  <Button
                    onClick={() => login()}
                    disabled={loading}
                    style={{
                      height: '100%',
                      width: '100%',
                      padding: '10px',
                      background: 'none',
                      border: 'none',
                    }}
                  >
                    {loading ? <CircularProgress size={25} /> : 'SignUp'}
                  </Button>
                </Grid>
              </form>

              {/* <FacebookLogin
                appId="248422355780887"
                fields="name,email"
                autoLoad={false}
                callback={responseFacebook}
                render={(renderProps) => (
                  <Grid
                    onClick={() => renderProps.onClick()}
                    container
                    direction="row"
                    alignItems="center"
                    style={{
                      background: "#3D5188",
                      cursor: "pointer",
                      marginBottom: "1rem",
                    }}
                  >
                    <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <img style={{ margin: 10 }} src={FacebookSquare}></img>
                      </Grid>
                    </Grid>
                    <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <span
                          style={{
                            color: "white",
                          }}
                        >
                          Sign up with Facebook
                        </span>
                      </Grid>
                    </Grid>
                  </Grid>
                )}
              /> */}

              {/* <GoogleLogin
                clientId="989350622141-263iao24sq0qcppvstssnvp2j2qp3cb5.apps.googleusercontent.com"
                render={(renderProps) => (
                  <Grid
                    container
                    direction="row"
                    alignItems="center"
                    style={{ background: "#4285F4", cursor: "pointer" }}
                    onClick={renderProps.onClick}
                  >
                    <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <img
                          style={{ margin: "5px 10px" }}
                          src={GoogleSquare}
                        ></img>
                      </Grid>
                    </Grid>
                    <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                      <Grid
                        container
                        direction="column"
                        alignItems="flex-start"
                      >
                        <span
                          style={{
                            color: "white",
                            // marginLeft:"4.25vw"
                          }}
                        >
                          Sign up with Google
                        </span>
                      </Grid>
                    </Grid>
                  </Grid>
                )}
                buttonText="Login"
                onSuccess={responseGoogle}
                onFailure={responseGoogleError}
                cookiePolicy={"single_host_origin"}
              /> */}
            </Grid>
            <Grid item md={3} xs={1}></Grid>
          </Grid>
          <Grid container direction='column' alignItems='center'>
            <p style={{ fontSize: 13, width: '80%', marginTop: '4vh' }}>
              Please Download our app from PlayStore to start taking world's
              best teachers courses
            </p>
          </Grid>
          <Grid container direction='row'>
            <Grid item md={2} lg={2} xl={2} sm={1} xs={1}></Grid>
            <Grid item md={8} lg={8} xl={8} sm={10} xs={10}>
              <Grid container direction='row' alignItems='center' style={{}}>
                <Grid
                  item
                  md={5}
                  lg={5}
                  xl={5}
                  sm={5}
                  xs={5}
                  style={{ display: 'flex', justifyContent: 'center' }}
                >
                  <Link to='#'>
                    {/* <img style={{ margin: 10, width: "100%" }} src={logo}></img> */}
                  </Link>
                </Grid>
              </Grid>
            </Grid>
            <Grid item md={2} lg={2} xl={2} sm={1} xs={1}></Grid>
          </Grid>
        </Grid>
        <Grid item md={3} lg={3} xl={3} sm={1} xs={1}></Grid>
      </Grid>
      <Grid container style={{ marginTop: 10 }} direction='row'>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
        <Grid item md={10} lg={10} xl={10} sm={10} xs={10}>
          <img style={{ width: '100%' }} src={ManStanding}></img>
          <Grid container direction='row-reverse'>
            <div
              style={{
                background: '#FFF2D3',
                width: 230,
                height: 200,
                borderRadius: 5,
                marginTop: -225,
                marginRight: 25,
              }}
            >
              <img
                style={{ marginTop: -25, width: 50, height: 50 }}
                src={LightStrokeIcon}
              ></img>
              <Grid container direction='column' alignItems='center'>
                <span
                  style={{ width: '80%', fontSize: 12, fontWeight: 'bold' }}
                >
                  Weekly, most students after taking courses on Yellow School
                  earn up to:
                </span>
                <span
                  style={{
                    width: '80%',
                    fontSize: 42,
                    marginTop: 15,
                    fontWeight: 'bold',
                  }}
                >
                  $550
                </span>
              </Grid>
            </div>
          </Grid>
          <div style={{ marginTop: 100 }}></div>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>

      <Footer></Footer>
      <Modal
        open={open}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        {body}
      </Modal>
    </div>
  );
}

export default BecomeStudentPage;
