const monthNames = [
  "Jan",
  "Feb",
  "Mar",
  "Apr",
  "May",
  "June",
  "July",
  "Aug",
  "Sep",
  "Oct",
  "Nov",
  "Dec",
];
export const mapScheduleData = (data) => {
  console.log("data mapper", data);
  let filled = [];
  data.forEach((sch) => {
    filled.push([
      monthNames[new Date(sch.lessionDate).getMonth()] +
        " " +
        new Date(sch.lessionDate).getDay(),

      new Date(sch.startTime).getHours() + ":" + "00",
      sch.studentId?.name || "",
    ]);
  });
  console.log("time", filled);
  return filled;
};
