import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Grid, Box } from "@material-ui/core";
import Button from "@material-ui/core/Button";
import NavBar from "../../components/nav/topNav";
import axios from "axios";
import Footer from "../../components/footer/footer";
import GoogleSquare from "../../assets/images/googleSquare.png";
import { LOGIN } from "../../Constants/sagaActions";
import { GoogleLogin } from "react-google-login";
import FacebookSquare from "../../assets/images/facebookSquare.png";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import baseUrl from "../../baseurl";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

const ResetPassword = ({ history, match }) => {
  const classes = useStyles();
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);
  const dispatch = useDispatch();
  const [password, setPassword] = useState("");
  const [reEnterPassword, setReEnterPassword] = useState("");
  const [error, setError] = useState(null);
  const [showError, setShowError] = useState(false);
  const [loading, setLoading] = useState(false);
  const [showSuccessMessage, setShowSuccessMessage] = useState(false);
  const [successMessage, setSuccessMessage] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const loggingIn = useSelector((state) => state.auth.loggingIn);
  const user = useSelector((state) => state.auth.user);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id="simple-modal-title">Email verified</h2>

      <p id="simple-modal-description">Email verified successfully</p>

      <Button
        variant="contained"
        style={{
          color: "white",
          backgroundColor: "green",
          marginRight: "2rem",
        }}
        onClick={() => history.push("/login")}
      >
        Go to login
      </Button>
    </div>
  );

  // const handleResetPassword = () => {
  //   axios({
  //     method: "post",
  //     url: "https://localhost:8080/api/auth/emailforgotpassword",
  //     data: { email: match.params.id },
  //   })
  //   .then(da => console.log('The rset emailllll successs :: ', da))
  //   .catch(e => console.log('The rest emaillll errorrrr :: ', e))
  // }
  useEffect(() => {
    handleResetPassword();
  }, []);
  const handleResetPassword = () => {
    setShowError(false);
    setLoading(true);
    axios({
      method: "put",
      url: `${baseUrl}/api/auth/confirmemail`,
      data: { email: match.params.id },
    })
      .then((da) => {
        console.log("This is data :: ", da.data.data);
        setLoading(false);
        setShowError(false);
        setOpen(true);
      })
      .catch((e) => {
        setLoading(false);
        setShowError(true);
      });
  };

  return (
    <div>
      <NavBar />
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          marginTop: "3rem",
        }}
      >
        <h2>Please waith we are confirming your account</h2>
      </div>
      <div style={{ height: "100vh" }}>
        <div>
          {loading && <CircularProgress />}
          {showError && (
            <>
              <p style={{ color: "red" }}>
                Sorry we are unable to verify your email
              </p>
              <Button
                onClick={() => history.push("/login")}
                style={{
                  height: "100%",
                  width: "100%",
                  padding: "10px",
                  background: "none",
                  border: "none",
                }}
              >
                Go to login
              </Button>
            </>
          )}
        </div>
      </div>
      <div style={{}}>
        <Footer />
      </div>
      <Modal
        open={open}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
};

export default ResetPassword;
