import React from "react";
import { Grid } from "@material-ui/core";

const options1 = [
  "Toddlers (1-3)",
  "Preschoolers (4-6)",
  "Primary school (6-12)",
  "Secondary school (12-17)",
  "Adults (23-40)",
  "Adults (40+)",
];
const options2 = [
  "Beginner",
  "Pre Intermediate",
  "Intermediate",
  "Upper Intermediate",
  "Advanced",
  "Proficiency",
];

const SingleTutorAbout = (props) => {
  return (
    <div
      style={{
        alignItems: "left",
        textAlign: "left",
        marginRight: 30,
        padding: "40px 30px 20px 30px",
        boxShadow:
          "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
      }}
    >
      
      <h4>About this tutor</h4>

      <p>
        Hi! <br />
       {props.tutorDetail.description}
      </p>
      {/* <p style={{ color: "#FFB600", marginBottom: 50 }}>Read More..</p> */}
      <Grid container style={{ color: "gray" }}>
        <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
          <p>Preferred student age</p>
          {props.tutorDetail.preferredStudentAge.map((op, i) => (
            <span key={i} className="mb-2">
              {" "}
              - {op}
              <br />
            </span>
          )) }
        </Grid>
        <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
          <p style={{marginTop: '1rem'}}>Preferred Level of student</p>
          {props.tutorDetail.preferredStudentLevel.map((op, i) => (
            <span key={i} className="mb-2">
              {" "}
              - {op}
              <br />
            </span>
          ))}
        </Grid>
      </Grid>
    </div>
  );
};

export default SingleTutorAbout;
