import React, { useState } from "react";
import NavBar from "../components/nav/topNav";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Footer from "../components/footer/footer";
import SideButtons from "../components/Settings/SideButtons";
import RedGoogle from "../assets/images/redGoogle.png";

const useStyles = makeStyles((theme) => ({
  TextLabelLarge: {
    fontSize: 35,
  },
  card: {
    background: "white",
    marginBottom: 20,
    borderRadius: 5,
    minHeight: 300,
  },
  links: {
    marginBottom: 0,
    marginLeft: 10,
    color: "white",
    cursor: "pointer",
  },
  buttons: {
    border: "none",
    borderRadius: 2,
    padding: 10,
    width: 130,
    textAlign: "left",
  },
  buttonsFilled: {
    background: "#FFB600",
    border: "none",
    color: "white",
    borderRadius: 2,
    padding: 10,
    marginLeft: 20,
    marginTop: 10,
  },
}));

export default function Calendar() {
  const classes = useStyles();
  const [avatar, setAvatar] = useState(null);

  return (
    <div>
      <NavBar></NavBar>
      <div style={{ background: "#384047" }}>
        <Grid
          container
          direction='row'
          style={{ height: 50 }}
          alignItems='center'
          justify='flex-start'>
          <p className={classes.links}>My Requests</p>
          <p className={classes.links}>My Lessons</p>
          <p className={classes.links}>Settings</p>
        </Grid>
      </div>
      <div style={{ background: "#EDEFF0" }}>
        <Grid container>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
          <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
            <Grid container style={{ marginTop: 20, marginBottom: 20 }}>
              <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
                <SideButtons for='Calendar'></SideButtons>
              </Grid>
              <Grid item md={9} lg={9} xl={9} sm={9} xs={9}>
                <div className={classes.card}>
                  <Grid container style={{ borderBottom: "1px solid #F2F2F2" }}>
                    <p
                      style={{
                        textAlign: "left",
                        padding: 10,
                        fontSize: 22,
                      }}>
                      Google Calendar
                    </p>
                  </Grid>
                  <Grid container direction='row' alignItems='center'>
                    <img style={{ marginLeft: 20 }} src={RedGoogle}></img>
                    <p style={{ width: "45%" }}>
                      Connect your Google Calendar and synchronize all your
                      Yellow Schools Space lessons with your personal schedule
                    </p>
                    <p className={classes.buttonsFilled}>
                      Connect Google Calendar
                    </p>
                  </Grid>
                </div>
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
        </Grid>
      </div>
      <Footer></Footer>
    </div>
  );
}
