import React from "react";
import { Grid } from "@material-ui/core";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Heart from "../../assets/images/heart.png";
import HatIcon from "../../assets/images/hatIcon.png";
import UserIcon from "../../assets/images/user.png";
import Avatar from "@material-ui/core/Avatar";
import Badge from "@material-ui/core/Badge";
import StarIcon from "@material-ui/icons/Star";
import Button from "@material-ui/core/Button";
import { useSelector } from "react-redux";
import { findTutTrans } from "../../Pages/translation";

const StyledBadge = withStyles((theme) => ({
  badge: {
    backgroundColor: "#44b700",
    color: "#44b700",
    boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
    "&::after": {
      position: "absolute",
      top: 0,
      left: 0,
      width: "100%",
      height: "100%",
      borderRadius: "50%",
      animation: "$ripple 1.2s infinite ease-in-out",
      border: "1px solid currentColor",
      content: '""',
    },
  },
  "@keyframes ripple": {
    "0%": {
      transform: "scale(.8)",
      opacity: 1,
    },
    "100%": {
      transform: "scale(2.4)",
      opacity: 0,
    },
  },
}))(Badge);

const SmallAvatar = withStyles((theme) => ({
  root: {
    width: 22,
    height: 22,
    borderRadius: 0,
    border: `2px solid ${theme.palette.background.paper}`,
  },
}))(Avatar);

const useStyles = makeStyles((theme) => ({
  Card: {
    background: "white",
    borderRadius: 5,
    marginBottom: 20,
  },
  pic: {
    width: 100,
    height: 100,
    marginLeft: -20,
    marginBottom: 30,
    [theme.breakpoints.up("lg")]: {
      width: 120,
    },
  },
  online: {
    height: 15,
    width: 15,
    borderRadius: 20,
    border: "1px solid white",
    background: "green",
    position: "relative",
    left: 15,
    top: 20,

    [theme.breakpoints.up("lg")]: {
      left: 20,
    },
    [theme.breakpoints.up("xl")]: {
      left: 45,
    },
  },
  heart: {
    width: 20,
    position: "relative",
    left: 75,
    top: -50,
  },
}));

export default function TutorCard(props) {
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);
  return (
    <div style={{ background: "#EDEFF0" }}>
      <div className={classes.Card}>
        <Grid container>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}>
            {/* <div className={classes.online}></div>
            <img className={classes.heart} src={Heart}></img>
            <img className={classes.pic} src={props.pic}></img> */}
            <StyledBadge
              overlap="circle"
              anchorOrigin={{
                vertical: "top",
                horizontal: "left",
              }}
              variant="dot"
            >
              <Avatar variant="square" src={props.pic} />
            </StyledBadge>
          </Grid>
          <Grid item md={7} lg={7} xl={7} sm={7} xs={7}>
            <Grid container direction="column" alignItems="flex-start">
              <p style={{ marginTop: 15, fontSize: 17 }}>{props.tutorName}</p>
              <Grid container style={{ marginTop: -15 }}>
                <p style={{ fontSize: 11, fontWeight: "lighter" }}>
                  <img style={{ marginRight: 5 }} src={HatIcon}></img>
                  {props.Language} Language
                </p>
              </Grid>
              <Grid
                container
                direction="center"
                alignItems="center"
                style={{ height: 25, marginTop: -10 }}
              >
                <p style={{ fontSize: 11, fontWeight: "lighter" }}>
                  <img style={{ marginRight: 5 }} src={UserIcon}></img>
                  {props.activeStudents}
                  {/* active Students  */}
                  {findTutTrans[3][language]}
                  {props.lessons} lessons
                </p>
                <p
                  style={{
                    fontSize: 14,
                    fontWeight: "lighter",
                    marginLeft: 15,
                  }}
                >
                  Speaks: {props.Language}
                </p>
                <p
                  style={{
                    fontSize: 15,

                    marginLeft: 15,
                    padding: 5,
                    borderRadius: 2,
                    color: "#FFB600",
                    background: "rgb(255, 182, 0,0.1)",
                  }}
                >
                  {props.Native}
                </p>
              </Grid>
              <p style={{ fontSize: 13, marginTop: 10, fontWeight: "lighter" }}>
                {props.tutorDetail.headline}
              </p>
              <p
                style={{
                  fontSize: 12,
                  marginTop: -15,
                  textAlign: "left",
                  fontWeight: "lighter",
                }}
              >
                English pronunciation to students.{" "}
                <span style={{ color: "gray" }}>{props.experience}</span>
              </p>
            </Grid>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}
