import React, { useState } from "react";
import { fade, makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import SearchBar from "../../components/LandingPage/SearchbBar";
import MobileImage from "../../assets/images/mobile.png";
import { useSelector } from "react-redux";
import { landingPageTop } from "./translation";
const useStyles = makeStyles((theme) => ({
  sloganText: {
    color: "white",
    fontFamily: "Roboto",
    fontSize: 45,
    width: "50%",
    fontWeight: "bold",
  },
  simpleText: {
    color: "white",
  },
}));

export default function Cover() {
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);
  console.log("This is language ... ", landingPageTop);
  return (
    <div style={{ background: "#141414" }}>
      <Grid container style={{ paddingTop: 80 }}>
        <Grid item md={4} lg={4} xl={4} sm={1} xs={1}></Grid>
        <Grid item md={4} lg={4} xl={4} sm={12} xs={12}>
          <span className={classes.sloganText}>
            {/* BECOME FLUENT IN ANY LANGUAGE */}
            {landingPageTop[5][language]}
          </span>
          <p className={classes.simpleText}>
            {/* Choose the best tutor for 1  on  1 lessons based on your goals and
            interests */}
            {landingPageTop[6][language]}
          </p>
          <SearchBar></SearchBar>
          <img
            style={{ marginTop: 50, paddingBottom: 130 }}
            src={MobileImage}
          ></img>
        </Grid>
        <Grid item md={4} lg={4} xl={4} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
}
