import React from "react";
import { Grid } from "@material-ui/core";

import NavBar from "../../components/nav/topNav";
import SubNavBar from "../../components/nav/SubHeaderTutor";
import { LEARNIN_PLANS, LEARNIN_LESSONS } from "../../Data/Data";
import LearningPlanCard from "./learningPlanCard";

const LearningPlanLibarary = () => {
  return (
    <div>
      <NavBar></NavBar>
      <SubNavBar for="learningPlans" />
      <Grid
        container
        style={{ background: "#EDEFF0", textAlign: "left", paddingTop: 15 }}
      >
        <Grid item md={2} lg={3} xl={3} sm={1} xs={1}></Grid>
        <Grid item md={8} lg={6} xl={6} sm={9} xs={9}>
          <h4>
            <b>Learning Plans Library</b>
          </h4>
          <p>
            Preview all Yellow School learning plans anytime, in one place!
            Choose from the learning plans below based on your students level
            and learning needs.
          </p>

          <h4 className="mt-4 mb-3">Conversation Starters</h4>
          <div style={{ display: "flex", flexDirection: "row" }}>
            {LEARNIN_PLANS.map((plan, i) => (
              <LearningPlanCard plan={plan} key={i} />
            ))}
          </div>
          <h4 className="mt-4 mb-3">Lesson Topics</h4>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <Grid container lg={12} md={12} sm={6}>
              {LEARNIN_LESSONS.map((plan, i) => (
                <LearningPlanCard plan={plan} key={i} />
              ))}
            </Grid>
          </div>
        </Grid>

        <Grid item md={2} lg={3} xl={3} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
};

export default LearningPlanLibarary;
