import React, { useEffect } from 'react';
import { useHistory, Link } from 'react-router-dom';
import { fade, makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import InputBase from '@material-ui/core/InputBase';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import CardMedia from '@material-ui/core/CardMedia';
import Logo from '../../assets/logo/logo.png';
import BusLogo from '../../assets/logo/BusLogo.png';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MailIcon from '@material-ui/icons/Mail';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MoreIcon from '@material-ui/icons/MoreVert';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import DropDownMenu from '../DropdownIconMenu/dropdownIconMenu';
import { ListItem, Box, List, Button, Drawer } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  iconColor: {
    color: '#8A959E',
  },
  iconColor2: {
    color: '#E9E6E3',
  },
  allmenus: {
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
  logo2: {
    width: 30,
    height: 30,
  },
  list: {
    [theme.breakpoints.down('sm')]: {
      width: 250,
      height: '100%',
    },
  },

  showbutton: {
    display: 'none',
    marginTop: '1rem',
    [theme.breakpoints.down('sm')]: {
      display: 'flex',
    },
  },
}));

export default function PrimarySearchAppBar() {
  const classes = useStyles();
  const history = useHistory();
  const [open, setOpen] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [user, setUser] = React.useState({});
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  useEffect(() => {
    getUser();
  }, []);

  const getUser = async () => {
    let usr = await localStorage.getItem('yellowTutor');
    if (usr !== undefined) {
      let newUsr = JSON.parse(usr);
      setUser(newUsr);
    }
  };

  const logout = async () => {
    await localStorage.removeItem('yellowToken');
    await localStorage.removeItem('yellowTutor');
    await localStorage.removeItem('yellowStudent');
    await localStorage.removeItem('role');
    history.push('/login');
  };

  const list = (anchor) => (
    <div className={classes.list}>
      <List>
        <ListItem>
          {' '}
          <Box display='flex' style={{ width: '100%' }}>
            <Box flex={1}>
              <img src={BusLogo} alt='logo' className={classes.logo2} />
            </Box>
            <Box flex={1}>
              <p
                style={{
                  color: 'black',
                  fontFamily: 'Roboto',
                  fontSize: 15,
                  marginLeft: '-2rem',
                  marginTop: 4,
                }}
              >
                Yellow School
              </p>
            </Box>
          </Box>
        </ListItem>
        <ListItem>
          {' '}
          <Link
            class={classes.navLinks}
            style={{ width: '100%' }}
            to='/becomeStudent'
          >
            <Button
              style={{
                backgroundColor: '#ffb600',
                width: '100%',
                color: 'white',
              }}
            >
              Sign up
            </Button>
          </Link>
        </ListItem>

        <ListItem>
          {' '}
          <Link class={classes.navLinks} style={{ width: '100%' }} to='/login'>
            <Button
              style={{
                backgroundColor: '#ffb600',
                width: '100%',
                color: 'white',
              }}
            >
              Login
            </Button>
          </Link>
        </ListItem>

        <ListItem>
          {' '}
          <Link
            class={classes.navLinks}
            style={{ width: '100%' }}
            to='/requestTutorHome'
          >
            <Button
              style={{
                backgroundColor: '#ffb600',
                width: '100%',
                color: 'white',
              }}
            >
              {' '}
              Find a tutor
            </Button>
          </Link>
        </ListItem>

        <ListItem>
          {' '}
          <Link
            class={classes.navLinks}
            style={{ width: '100%' }}
            to='/becometutor'
          >
            <Button
              style={{
                backgroundColor: '#ffb600',
                width: '100%',
                color: 'white',
              }}
            >
              {' '}
              Become a teacher
            </Button>
          </Link>
        </ListItem>

        <ListItem>
          {' '}
          <Link class={classes.navLinks} style={{ width: '100%' }} to='/blog'>
            <Button
              style={{
                backgroundColor: '#ffb600',
                width: '100%',
                color: 'white',
              }}
            >
              {' '}
              Nazar's Blog
            </Button>
          </Link>
        </ListItem>
      </List>
    </div>
  );

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setOpen(open);
  };

  return (
    <div>
      <Drawer anchor={'left'} open={open} onClose={toggleDrawer('left', false)}>
        {list('left')}
      </Drawer>
      <AppBar position='static' color='transparent'>
        <div className={classes.showbutton}>
          <div style={{ flex: 1 }}>
            {' '}
            <MenuIcon onClick={toggleDrawer('left', true)} />{' '}
          </div>
          <div style={{ flex: 2 }}>
            <Box display='flex'>
              <Box flex={1}>
                <img src={BusLogo} alt='logo' className={classes.logo2} />
              </Box>
              <Box flex={1}>
                <p
                  style={{
                    color: 'black',
                    fontFamily: 'Roboto',
                    fontSize: 15,
                    marginLeft: '-2rem',
                    marginTop: 4,
                  }}
                >
                  Yellow School
                </p>
              </Box>
            </Box>
          </div>
          <div style={{ flex: 1 }}> </div>
        </div>
        <Toolbar className={classes.allmenus}>
          {/* <IconButton
            edge='start'
            className={classes.menuButton}
            color='inherit'
            aria-label='open drawer'>
            <MenuIcon />
          </IconButton> */}
          {/* <Typography className={classes.title} variant='h6' noWrap>
            LOGO
          </Typography> */}
          <Link to='/'>
            <img src={Logo} alt='logo' className={classes.logo} />
          </Link>

          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>
            {/* <Link to='/helpcenter'>
              <IconButton aria-label='show 4 new mails'>
                <Badge>
                  <HelpOutlineIcon className={classes.iconColor} />
                </Badge>
              </IconButton>
            </Link> */}

            {user && (
              <ExitToAppIcon style={{ cursor: 'pointer' }} onClick={logout} />
            )}

            <IconButton>
              {user && (
                <span
                  style={{
                    borderRadius: '50%',
                    height: '30px',
                    width: '30px',
                    textAlign: 'center',
                    background: '#FFB600',
                  }}
                >
                  {user.firstName ? user.firstName[0].toUpperCase() : 'U'}
                </span>
              )}
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
