import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";

import ChevronRightIcon from "@material-ui/icons/ChevronRight";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import { Grid } from "@material-ui/core";

import moment from "moment";

import React, { Component } from "react";
import Cards from "./cards";

class daysOfWeek extends Component {
  state = {
    startDate: moment(),
    nextDate: moment(),
  };

  componentDidMount() {
    this.setState({ nextDate: this.state.nextDate.add(6, "days") });
  }

  onChangeDateAdd = () => {
    this.setState({
      startDate: this.state.startDate.add(7, "days"),
      nextDate: this.state.nextDate.add(7, "days"),
    });
  };

  onChangeDateSubtract = () => {
    this.setState({
      startDate: this.state.startDate.subtract(7, "days"),
      nextDate: this.state.nextDate.subtract(7, "days"),
    });
  };

  render() {
    return (
      <Grid container style={{ marginTop: "30px" }}>
        <Grid container>
          <Grid item sm={2} xs={2} md={1}>
            <IconButton
              aria-label="ChevronLeftIcon"
              onClick={this.onChangeDateSubtract}
            >
              <ChevronLeftIcon fontSize="large" />
            </IconButton>
          </Grid>
          <Grid item sm={2} xs={2} md={1}>
            <IconButton
              aria-label="ChevronRightIcon"
              onClick={this.onChangeDateAdd}
            >
              <ChevronRightIcon fontSize="large" />
            </IconButton>
          </Grid>
          <Grid
            item
            container
            xs={8}
            sm={5}
            md={10}
            style={{ marginTop: "20px" }}
          >
            <Typography
              style={{ fontSize: "17px", fontWeight: "bold" }}
            >{`${this.state.startDate.format(
              "MMM DD"
            )} - ${this.state.nextDate.format("DD , YYYY")}`}</Typography>
          </Grid>
        </Grid>
        <Cards firstDay={this.state.startDate} />
      </Grid>
    );
  }
}

export default daysOfWeek;
