import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Container from '@material-ui/core/Container';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import NativeSelect from '@material-ui/core/NativeSelect';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import InputBase from '@material-ui/core/InputBase';
import { Box } from '@material-ui/core';
import axios from 'axios';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Checkbox from '@material-ui/core/Checkbox';
import baseUrl from '../../baseurl';

const BootstrapInput = withStyles((theme) => ({
  input: {
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '10px 26px 10px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
    },
  },
}))(InputBase);

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: '#FFFFFF',
    padding: '2rem',
    borderRadius: 5,
    marginTop: '3rem',
  },
  name: {
    padding: 0,
  },
  input1: {
    height: '0.3rem',
  },
  input2: {
    height: 30,
    fontSize: '3em',
  },
  headings: {
    color: '#6F757B',
  },
  headings1: {
    color: '#6F757B',
    marginTop: '2rem',
    marginBottom: '2rem',
  },
  formControl: {
    width: '100%',
  },
  button: {
    marginTop: theme.spacing(3),
    backgroundColor: '#FFB600',
    color: 'white',
  },
  formControl: {
    margin: theme.spacing(3),
  },
  root: {
    display: 'flex',
  },
  radio: {
    '&$checked': {
      color: '#ffcd02',
    },
  },
  checked: {},
}));
export default function About(props) {
  const classes = useStyles();
  const [firstName, setFirstName] = React.useState('');
  const [error, setError] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [lastName, setLastName] = React.useState('');
  const [countryOrigin, setCountryOrigin] = React.useState('');
  const [hourlyRate, setHourlyRate] = React.useState(null);
  const [subjectTaught, setSubjectTaught] = React.useState('');
  const [phoneNumber, setPhoneNumber] = React.useState('');
  const [experience, setExperience] = React.useState(null);
  const [education, setEducation] = React.useState({
    startDate: '',
    endDate: '',
    instituteName: '',
    degreeTitle: '',
  });
  const [certificate, setCertificate] = React.useState([
    {
      startDate: '',
      endDate: '',
      instituteName: '',
      certificationTitle: '',
    },
  ]);
  const [state, setState] = React.useState({
    gilad: true,
    jason: false,
    antoine: false,
  });

  const [level1, setLevel1] = React.useState(false);
  const [level2, setLevel2] = React.useState(false);
  const [level3, setLevel3] = React.useState(false);
  const [level4, setLevel4] = React.useState(false);
  const [level5, setLevel5] = React.useState(false);

  const [age1, setAge1] = React.useState(false);
  const [age2, setAge2] = React.useState(false);
  const [age3, setAge3] = React.useState(false);
  const [age4, setAge4] = React.useState(false);
  const handleChange = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };
  const { gilad, jason, antoine } = state;

  const [studentAge, setStudentAge] = React.useState('Preschoolers (4-6)');
  const [studentLevel, setStudentLevel] = React.useState('Beginner');

  const resumePost = async () => {
    let ages = [];
    if (age1) {
      ages.push('Preschoolers (4-6)');
    }
    if (age2) {
      ages.push('Primary School (6-12)');
    }
    if (age3) {
      ages.push('Primary School (12-17)');
    }
    if (age4) {
      ages.push(' Undergraduate (17-22)');
    }
    console.log('jhsdgfjhsdgf Tutor id is ', ages);

    let levels = [];
    if (level1) {
      levels.push('Beginner');
    }
    if (level2) {
      levels.push('Pre Intermediate');
    }
    if (level3) {
      levels.push('Intermediate');
    }
    if (level4) {
      levels.push('Advance');
    }
    if (level5) {
      levels.push('Profiency');
    }

    setLoading(true);
    setError(false);
    let dataObj = {
      experience: experience,
      education: education,
      certifications: certificate,
      preferredStudentAge: ages,
      preferredStudentLevel: levels,
    };

    await axios
      .put(baseUrl + '/api/tutors/resume/' + props.tutorId, dataObj)
      .then(async (res) => {
        //   let allLessons = [];

        console.log('This is response updated tutor resume  :: ', res);

        setLoading(false);
        props.handleNext();
      })
      .catch((error) => {
        setLoading(false);
        setError(true);
        console.log('Email already existed :: ', error);
      });
  };

  return (
    <div style={{ margin: '1rem' }}>
      <Container maxWidth='md' className={classes.container} boxShadow={3}>
        <Typography className={classes.headings} variant='h6' gutterBottom>
          Resume
        </Typography>
        {error ? (
          <Grid container alignItems='left'>
            <p style={{ fontSize: 13, color: 'red' }}>Failed to save</p>
          </Grid>
        ) : null}
        <Grid container spacing={4}>
          <Grid item xs={12}>
            <Typography
              variant='subtitle2'
              align='left'
              className={classes.headings}
            >
              Education
            </Typography>

            <Grid container spacing={3}>
              <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                <TextField
                  style={{ width: '100%' }}
                  id='outlined-read-only-input'
                  placeholder='From'
                  type='date'
                  onChange={(e) =>
                    setEducation({ ...education, startDate: e.target.value })
                  }
                  InputProps={{ classes: { input: classes.input1 } }}
                  variant='outlined'
                />
              </Grid>
              <Grid item md={3} lg={3} xl={3} sm={3} xs={6}>
                <TextField
                  style={{ width: '100%' }}
                  id='outlined-read-only-input'
                  placeholder='To'
                  type='date'
                  onChange={(e) =>
                    setEducation({ ...education, endDate: e.target.value })
                  }
                  InputProps={{ classes: { input: classes.input1 } }}
                  variant='outlined'
                />
              </Grid>

              <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                <TextField
                  style={{ width: '100%' }}
                  id='outlined-read-only-input'
                  placeholder='Institute Name'
                  onChange={(e) =>
                    setEducation({
                      ...education,
                      instituteName: e.target.value,
                    })
                  }
                  InputProps={{ classes: { input: classes.input1 } }}
                  variant='outlined'
                />
              </Grid>
              <Grid item md={3} lg={3} xl={3} sm={3} xs={6}>
                <TextField
                  style={{ width: '100%' }}
                  id='outlined-read-only-input'
                  placeholder='Degree Title'
                  onChange={(e) =>
                    setEducation({ ...education, degreeTitle: e.target.value })
                  }
                  InputProps={{ classes: { input: classes.input1 } }}
                  variant='outlined'
                />
              </Grid>
            </Grid>
          </Grid>

          <Grid item xs={12}>
            <Typography
              variant='subtitle2'
              align='left'
              className={classes.headings}
            >
              Certification
            </Typography>
            {certificate.map((data, index) => (
              <>
                <Grid container spacing={3}>
                  <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                    <TextField
                      style={{ width: '100%' }}
                      id='outlined-read-only-input'
                      placeholder='From'
                      type='date'
                      onChange={(e) => {
                        let allCers = certificate;
                        allCers[index].startDate = e.target.value;
                        setCertificate(allCers);
                      }}
                      InputProps={{ classes: { input: classes.input1 } }}
                      variant='outlined'
                    />
                  </Grid>
                  <Grid item md={3} lg={3} xl={3} sm={3} xs={6}>
                    <TextField
                      style={{ width: '100%' }}
                      id='outlined-read-only-input'
                      placeholder='To'
                      type='date'
                      onChange={(e) => {
                        let allCers = certificate;
                        allCers[index].endDate = e.target.value;
                        setCertificate(allCers);
                      }}
                      InputProps={{ classes: { input: classes.input1 } }}
                      variant='outlined'
                    />
                  </Grid>

                  <Grid item md={3} lg={3} xl={3} sm={6} xs={6}>
                    <TextField
                      style={{ width: '100%' }}
                      id='outlined-read-only-input'
                      placeholder='Institute Name'
                      onChange={(e) => {
                        let allCers = certificate;
                        allCers[index].instituteName = e.target.value;
                        setCertificate(allCers);
                      }}
                      InputProps={{ classes: { input: classes.input1 } }}
                      variant='outlined'
                    />
                  </Grid>
                  <Grid item md={3} lg={3} xl={3} sm={3} xs={6}>
                    <TextField
                      style={{ width: '100%' }}
                      id='outlined-read-only-input'
                      placeholder='Certificate Title'
                      onChange={(e) => {
                        let allCers = certificate;
                        allCers[index].certificationTitle = e.target.value;
                        setCertificate(allCers);
                      }}
                      InputProps={{ classes: { input: classes.input1 } }}
                      variant='outlined'
                    />
                  </Grid>
                </Grid>
              </>
            ))}
          </Grid>
          <Grid item xs={12} style={{ margin: 0, padding: 0, display: 'flex' }}>
            <Typography
              align='left'
              variant='caption'
              // display='block'
              style={{
                color: '#FFB600',
                marginLeft: '1rem',
                cursor: 'pointer',
              }}
              onClick={() => {
                setCertificate([
                  ...certificate,
                  {
                    startDate: '',
                    endDate: '',
                    instituteName: '',
                    certificationTitle: '',
                  },
                ]);
              }}
            >
              Add another certificate
            </Typography>
          </Grid>

          <Grid item xs={6} sm={3}>
            <Grid
              container
              direction='row'
              justify='center'
              alignItems='center'
            >
              <Grid item xs={10}>
                <Typography
                  variant='subtitle2'
                  align='left'
                  color='red'
                  className={classes.headings}
                >
                  Experience Years
                </Typography>
                <TextField
                  align='left'
                  type='number'
                  id='outlined-read-only-input'
                  value={experience}
                  onChange={(e) => {
                    if (e.target.value >= 0) {
                      setExperience(e.target.value);
                    } else {
                      setExperience(0);
                    }
                  }}
                  placeholder={0}
                  InputProps={{
                    classes: { input: classes.input1 },
                  }}
                  variant='outlined'
                />
              </Grid>
              <Grid item xs={2} style={{ marginTop: '1rem' }}>
                <Typography
                  variant='subtitle2'
                  color='red'
                  className={classes.headings}
                ></Typography>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Typography className={classes.headings1} variant='h6' gutterBottom>
          Students Information
        </Typography>

        <Grid container spacing={4}>
          <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
            <FormControl component='fieldset' className={classes.formControl}>
              <Typography
                variant='subtitle2'
                align='left'
                color='red'
                className={classes.headings}
              >
                Student Age
              </Typography>
              <RadioGroup value={studentAge}>
                <FormControlLabel
                  value='Preschoolers (4-6)'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setAge1(!age1)}
                      checked={age1}
                    />
                  }
                  label='Preschoolers (4-6)'
                />
                <FormControlLabel
                  value='Primary School (6-12)'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setAge2(!age2)}
                      checked={age2}
                    />
                  }
                  label='Primary School (6-12)'
                />
                <FormControlLabel
                  value='Primary School (12-17)'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setAge3(!age3)}
                      checked={age3}
                    />
                  }
                  label='Primary School (12-17)'
                />
                <FormControlLabel
                  value='Adults (17-22)'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setAge4(!age4)}
                      checked={age4}
                    />
                  }
                  label='Adults (17-22)'
                />
              </RadioGroup>
            </FormControl>
          </Grid>

          <Grid item md={6} lg={6} xl={6} sm={12} xs={12}>
            <FormControl component='fieldset' className={classes.formControl}>
              <Typography
                variant='subtitle2'
                align='left'
                color='red'
                className={classes.headings}
              >
                Level Of Students
              </Typography>
              <RadioGroup
                value={studentLevel}
                onChange={(e) => setStudentLevel(e.target.value)}
              >
                <FormControlLabel
                  value='Beginner'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setLevel1(!level1)}
                      checked={level1}
                    />
                  }
                  label='Beginner'
                />
                <FormControlLabel
                  value='Pre Intermediate'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setLevel2(!level2)}
                      checked={level2}
                    />
                  }
                  label='Pre Intermediate'
                />
                <FormControlLabel
                  value='Intermediate'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setLevel3(!level3)}
                      checked={level3}
                    />
                  }
                  label='Intermediate'
                />
                <FormControlLabel
                  value='Advance'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setLevel4(!level4)}
                      checked={level4}
                    />
                  }
                  label='Advance'
                />
                <FormControlLabel
                  value='Proficient'
                  control={
                    <Checkbox
                      classes={{
                        root: classes.radio,
                        checked: classes.checked,
                      }}
                      onClick={() => setLevel5(!level5)}
                      checked={level5}
                    />
                  }
                  label='Proficient'
                />
              </RadioGroup>
            </FormControl>
          </Grid>
        </Grid>

        <Button
          variant='contained'
          className={classes.button}
          onClick={() => resumePost()}
        >
          {loading === false ? 'Next' : <CircularProgress size={25} />}
        </Button>
      </Container>
    </div>
  );
}
