import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import Nav from '../components/nav/topNav';
import { Grid } from '@material-ui/core';
import SubHeaderTutor from '../components/nav/SubHeaderTutor';
import Footer from '../components/footer/footer';
import { makeStyles } from '@material-ui/core/styles';
import Interaction from '../assets/images/interaction.png';
import baseurl from '../baseurl';
import { Messages, Conversation } from '../Data/Data';
import LightStrokeYellow from '../assets/images/lightStrokeYellow.png';
import {
  ALL_CONVERSATIONS,
  ALL_MESSAGES,
  REPLY_TO_CONVERSATION,
  GET_TUTOR_PROFILE,
} from '../Constants/sagaActions';
import io from 'socket.io-client';
import CircularProgress from '@material-ui/core/CircularProgress';
import SendInput from '../components/SendInput/SendInput';
import { Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { findTutTrans } from './translation';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  messagesHeaderActive: {
    marginBottom: 0,
    color: '#FFB600',
    fontWeight: 'bold',
    marginLeft: 20,
    paddingTop: 10,
    width: 40,
    cursor: 'pointer',
    height: '100%',
    borderBottom: '4px solid #FFB600',
  },
  messagesHeader: {
    marginBottom: 0,
    cursor: 'pointer',
    marginLeft: 20,
    paddingTop: 10,
    // fontWeight: "bold",
    color: 'gray',
    width: 40,
    height: '100%',
  },
  messagePic: {
    width: 45,
    height: 45,
    borderRadius: 25,
  },
  yellowText: {
    marginBottom: 0,
    color: '#FFB600',
    textAlign: 'center',
    fontWeight: 'bold',
  },
  dateBox: {
    background: '#F2F2F2',
    color: 'gray',
    borderRadius: 15,
    marginTop: 10,
    padding: 5,
    width: 150,
  },
  MessageBox: {
    background: '#F2F2F2',
    color: 'gray',
    borderRadius: 15,
    marginTop: 10,
    padding: 5,
    marginBottom: 0,
    width: 250,
  },
  MessageBox2: {
    background: '#FFB600',
    color: 'black',
    borderRadius: 15,
    marginTop: 10,
    padding: 5,
    marginBottom: 0,
    width: 250,
  },
}));

let socket1;
let timerIntr = null;
export default function TutorMyMessages(props) {
  let history = useHistory();
  const classes = useStyles();
  const [allChats, setAllChats] = useState([]);
  const dispatch = useDispatch();
  const [socket, setSocket] = React.useState(null);
  const [endpoint, setEndpoint] = useState('https://yellowschool.world');
  const [loading, setLoading] = useState(false);
  const [studentInfo, setStudentInfo] = useState({});
  const [activeMessage, setActiveMessage] = React.useState('All');
  const language = useSelector((state) => state.languageReducer.language);
  const [showMessage, setShowMessage] = React.useState(
    '5fa46550ba1def37a927edb0'
  );
  const [recImg, setRecImg] = React.useState(
    'https://yellow-school.s3.us-east-2.amazonaws.com/9895ed2d4868eecf06603f8a4ed39b59.jpg'
  );
  const [allMessages, setAllMessages] = React.useState([]);
  const [allConversations, setAllConversations] = useState([]);
  const [chatId, setChatId] = useState('');
  const [user, setUser] = useState(null);
  const [tutorImage, setTutorImage] = useState('');
  const [tutorName, setTutorName] = useState('');

  // useEffect(() => {
  // if(history.location.state) {
  //   joinChat(history.location.state.chatId, history.location.state.tutorImage)
  // }
  // }, [])
  // useEffect(() => {
  //   socket1 = io(endpoint);
  //   setSocket(socket1);
  //   socket1.on('connect', () => {});
  //   return () => {
  //     socket1.on('disconnect');
  //     socket1.off();
  //   };
  // }, [endpoint]);

  useEffect(() => {
    timerIntr = setInterval(() => {
      if (chatId) {
        getMessagesOfChat(chatId);
      }
    }, 2000);

    return () => {
      if (timerIntr) {
        clearInterval(timerIntr);
      }
    };
  });
  const getData = async () => {
    let tutor = await JSON.parse(localStorage.getItem('yellowTutor'));
    setUser(tutor);

    axios
      .get(`${baseurl}/api/newconversation/gettutors/${tutor.userId}`)
      .then((data) => {
        // console.log("sssttuuddeennttt found:: ", data.data.success)
        if (data.data.success === false) {
          setAllChats([]);
          return;
        }

        setAllChats(data.data.data);
      })
      .catch((e) => {
        console.log('sssttuuddeennttt error:: ', e);
        setAllChats([]);
      });
  };

  const getMessagesOfChat = async (chatId) => {
    axios
      .get(`${baseurl}/api/newchat/chatbyconversationid/${chatId}`)
      .then((data) => {
        if (data.data.success === false) {
          setAllChats([]);
          return;
        }

        setAllMessages(data.data.data);
      })
      .catch((e) => {
        setAllMessages([]);
      });
  };

  const userInfo = async () => {
    let a = await localStorage.getItem('yellowTutor');
    let user = JSON.parse(a);
    console.log('asjgdfagshfdjahsgdasjhg :: ', user);
    setUser(user);
  };
  useEffect(() => {
    getData();
    userInfo();
  }, []);

  const joinChat = async (chatId, img, name) => {
    setChatId(chatId);
    setTutorImage(img);
    setTutorName(name);
    getMessagesOfChat(chatId);
  };

  const changeChat = (chat) => {
    let allMess = allMessages;
    allMess.push(chat);
    setAllMessages([...allMessages]);
  };

  const onMessageSend = async (message) => {
    let payload = {
      conversationId: chatId,
      message: message,
      isSendertutor: true,
      isSenderStudent: false,
    };
    changeChat(payload);
    await axios.post(`${baseurl}/api/newchat/sendmessage`, payload);
  };

  return (
    <div>
      <Nav></Nav>
      <SubHeaderTutor for='Messages'></SubHeaderTutor>
      <Grid container style={{ height: '90vh' }}>
        <Grid
          item
          md={3}
          lg={3}
          xl={3}
          sm={3}
          xs={3}
          style={{ borderRight: '1px solid #F2F2F2' }}
        >
          <Grid
            container
            direction='row'
            alignItems='center'
            style={{ borderBottom: '1px solid #F2F2F2', height: 50 }}
          >
            <p
              className={
                activeMessage === 'All'
                  ? classes.messagesHeaderActive
                  : classes.messagesHeader
              }
            >
              All
            </p>
            <p
              className={
                activeMessage === 'Unread'
                  ? classes.messagesHeaderActive
                  : classes.messagesHeader
              }
            >
              {/* Unread */}
            </p>
          </Grid>
          <Grid container direction='column'>
            {allChats.length > 0 ? (
              allChats.map((message) => {
                return (
                  <Grid
                    key={message._id}
                    container
                    direction='row'
                    onClick={() => {
                      joinChat(
                        message._id,
                        message.studentImage,
                        message.studentName
                      );
                    }}
                    style={{
                      borderBottom: '1px solid #F2F2F2',
                      cursor: 'pointer',
                      height: 60,
                    }}
                  >
                    <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
                      <Grid
                        container
                        direction='row'
                        justify='center'
                        alignItems='center'
                        style={{ height: '100%' }}
                      >
                        <img
                          className={classes.messagePic}
                          src={message.studentImage}
                        ></img>
                      </Grid>
                    </Grid>
                    <Grid item md={7} lg={7} xl={7} sm={7} xs={7}>
                      <Grid
                        container
                        direction='column'
                        justify='center'
                        style={{ height: '100%' }}
                      >
                        <p
                          style={{
                            fontWeight: 'bold',
                            textAlign: 'left',
                            fontSize: 13,
                            marginBottom: 0,
                          }}
                        >
                          {message.studentName}
                        </p>
                        <p
                          style={{
                            textAlign: 'left',
                            fontSize: 12,
                            marginBottom: 0,
                            color: '#6F757B',
                          }}
                        >
                          <p></p>
                        </p>
                      </Grid>
                    </Grid>
                    <Grid item md={2} lg={2} xl={2} sm={2} xs={2}>
                      <Grid
                        container
                        direction='column'
                        justify='center'
                        style={{ height: '100%' }}
                      >
                        <p
                          style={{
                            textAlign: 'left',
                            fontSize: 11,
                            marginBottom: 0,
                            marginRight: 5,
                          }}
                        >
                          {new Date(message.createdAt).toLocaleTimeString()}
                        </p>
                      </Grid>
                    </Grid>
                  </Grid>
                );
              })
            ) : (
              // <CircularProgress size={25} style={{ margin: "20px" }} />
              <p>No messages</p>
            )}
          </Grid>
        </Grid>

        {chatId ? (
          <Grid
            item
            md={9}
            lg={9}
            xl={9}
            sm={9}
            xs={9}
            style={{ height: '90vh', overflowX: 'scroll' }}
          >
            <Grid container direction='column' alignItems='center'>
              <Grid
                container
                direction='column'
                justify='center'
                alignItems='center'
                style={{ borderBottom: '1px solid #F2F2F2', height: 50 }}
              >
                <p className={classes.yellowText}>
                  {<img src={tutorImage} className={classes.messagePic}></img>}{' '}
                  {tutorName}
                </p>
              </Grid>
              <Grid
                container
                direction='column'
                justify='center'
                alignItems='center'
                style={{ height: 50 }}
              >
                <p className={classes.dateBox}>
                  {allConversations.length > 0 &&
                    new Date(
                      allConversations[
                        allConversations.length - 1
                      ].lastMessage.createdAt
                    ).toLocaleDateString()}
                </p>
              </Grid>

              {allMessages.length > 0 &&
                allMessages.map((conv) => {
                  console.log('mmeesssaaggeess :: ', conv);
                  if (!conv.isSenderStudent) {
                    return (
                      <Grid
                        key={conv._id}
                        container
                        direction='row'
                        justify='flex-end'
                        alignItems='flex-end'
                        style={{ height: 50 }}
                      >
                        <p className={classes.MessageBox2}>{conv.message}</p>
                        <img
                          className={classes.messagePic}
                          style={{ marginRight: 10, marginLeft: 10 }}
                          src={user.imageUrl}
                        ></img>
                      </Grid>
                    );
                  } else {
                    return (
                      <Grid
                        key={conv._id}
                        container
                        direction='row'
                        justify='flex-start'
                        alignItems='flex-end'
                        style={{ height: 50 }}
                      >
                        <img
                          className={classes.messagePic}
                          style={{ marginRight: 10, marginLeft: 10 }}
                          src={tutorImage}
                        ></img>
                        <p className={classes.MessageBox}>{conv.message}</p>
                      </Grid>
                    );
                  }
                })}
            </Grid>
            <div style={{ marginTop: '3rem', marginBottom: '2rem' }}>
              <SendInput onSend={onMessageSend} />
            </div>
          </Grid>
        ) : (
          <Grid item md={9} lg={9} xl={9} sm={9} xs={9}>
            <Grid
              container
              style={{ height: '100%' }}
              direction='column'
              alignItems='center'
              justify='center'
            >
              <img src={Interaction}></img>
              <p style={{ fontWeight: 'bold', fontSize: 14 }}>
                {/* Select a student to start conversation */}
                {findTutTrans[9][language]}
              </p>
              <p style={{ width: '30%', marginTop: -20, fontSize: 13 }}>
                {/* Once you select a student, you'll also see their learning
                details */}
                {findTutTrans[10][language]}
              </p>
            </Grid>
          </Grid>
        )}
      </Grid>

      <div>
        <Footer />
      </div>
    </div>
  );
}
