import { CardElement, useElements, useStripe } from '@stripe/react-stripe-js';
import axios from 'axios';
import { Button, CircularProgress } from '@material-ui/core';
import React, { useState } from 'react';
import baseUrl from '../../baseurl';
import { useAlert } from 'react-alert';
import WeChatPay from '../../Pages/StripeExample';
import './payment.css';

const CARD_OPTIONS = {
  iconStyle: 'solid',
  style: {
    base: {
      iconColor: '#c4f0ff',
      color: '#fff',
      fontWeight: 500,
      fontFamily: 'Roboto, Open Sans, Segoe UI, sans-serif',
      fontSize: '16px',
      fontSmoothing: 'antialiased',
      ':-webkit-autofill': { color: '#fce883' },
      '::placeholder': { color: '#87bbfd' },
    },
    invalid: {
      iconColor: '#ffc7ee',
      color: '#ffc7ee',
    },
  },
};

export default function PaymentForm({ bookLesson, rate }) {
  const [success, setSuccess] = useState(false);
  const [payError, setPayError] = useState(false);
  const [isPaying, setIsPaying] = useState(false);
  const stripe = useStripe();
  const elements = useElements();
  const alert = useAlert();
  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsPaying(true);
    const { error, paymentMethod } = await stripe.createPaymentMethod({
      type: 'card',
      card: elements.getElement(CardElement),
    });

    if (!error) {
      try {
        const { id } = paymentMethod;
        console.log('This is payment idd :: ', id);
        const response = await axios.post(`${baseUrl}/payment`, {
          amount: rate * 100,
          id,
        });
        console.log('Stripe response :: ', response);
        if (response.data.success) {
          console.log('Successful payment');
          bookLesson();
          setIsPaying(false);
          setSuccess(true);
        } else {
          alert.error(String(response.data.details));
          setIsPaying(false);
        }
      } catch (error) {
        console.log('Error', error);
        alert.error(String(error.message));
        setIsPaying(false);
        setPayError(true);
      }
    } else {
      alert.error(String(error.message));
      setIsPaying(false);
    }
  };

  return (
    <>
      {!success ? (
        <form onSubmit={handleSubmit}>
          <fieldset className='FormGroup'>
            <div className='FormRow'>
              <CardElement options={CARD_OPTIONS} />
            </div>
          </fieldset>
          <div style={{ display: 'flex', justifyContent: 'space-between' }}>
            <Button
              variant='contained'
              type='submit'
              style={{
                color: 'white',
                backgroundColor: '#ffcd02',
                margin: '2rem',
              }}
            >
              {isPaying ? (
                <CircularProgress size='1.5rem' color='white' />
              ) : (
                'Pay'
              )}
            </Button>
            {/* <WeChatPay bookLesson={bookLesson} /> */}
          </div>
        </form>
      ) : payError ? (
        <div>
          <h5 style={{ margin: '2rem', color: 'red' }}>Payment unsuccessful</h5>
        </div>
      ) : (
        <div>
          <h5 style={{ margin: '2rem', color: 'green' }}>Payment successful</h5>
        </div>
      )}
    </>
  );
}
