import React from "react";
import Header from "../components/nav/topNav";
import RainbowCard from "../components/AboutPage/RainbowBorderCard";
import { Grid } from "@material-ui/core";
import Medal from "../assets/images/medal.png";
import CheckList from "../assets/images/checklist.png";
import Youtube from "../assets/images/youtube.png";
import Patent from "../assets/images/patent.png";
import Footer from "../components/footer/footer";

export default function AboutPage() {
  return (
    <div>
      <Header></Header>
      <Grid container style={{ background: "#EDEFF0" }}>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
        <Grid item md={10} lg={10} xl={10} sm={10} xs={10}>
          <Grid container>
            <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
              <Grid container direction='row' justify='center'>
                <RainbowCard
                  icon={CheckList}
                  Heading='What is the Application Process?'
                  Detail='Before applying, kindly note the application process will be lengthy, selective and will require your active participation.'
                  buttonText='UNDERSTAND THE PROCESS'></RainbowCard>
              </Grid>
            </Grid>
            <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
              <Grid container direction='row' justify='center'>
                <RainbowCard
                  icon={Patent}
                  Heading='What is the Application Process?'
                  Detail='On yellow school you can become a Professional Teacher or a Community Tutor.
                  Discover a solution that suits you.'
                  buttonText='UNDERSTAND THE PROCESS'></RainbowCard>
              </Grid>
            </Grid>
            <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
              <Grid container direction='row' justify='center'>
                <RainbowCard
                  icon={Youtube}
                  Heading='Prepare a video introduction'
                  Detail='yellow school requires that you have a video introduction. This video introduction is how you can show off your teaching style and teaching personality to potential students. Video example'
                  buttonText='VIDEO RULES'></RainbowCard>
              </Grid>
            </Grid>
            <Grid item md={6} lg={6} xl={6} sm={6} xs={6}>
              <Grid container direction='row' justify='center'>
                <RainbowCard
                  icon={Medal}
                  Heading='Teaching certifications'
                  Detail='yellow school requires only Professional Teachers to upload their Teaching certificates.
                  Your diplomas and certificates are not published.'
                  buttonText='UNDERSTAND THE PROCESS'></RainbowCard>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
      <Footer></Footer>
    </div>
  );
}
