  const [countryOrigin, setCountryOrigin] = React.useState('');
  const [hourlyRate, setHourlyRate] = React.useState(null);
  const [subjectTaught, setSubjectTaught] = React.useState('');
  const [phoneNumber, setPhoneNumber] = React.useState('');
  const [currency, setCurrency] = React.useState('Dollar');
  const [nativeLanguage, setNativeLanguage] = React.useState('');
  const [userId, setUserId] = React.useState('');
  const [languageSpoken, setLanguageSpoken] = React.useState([
    {
      language: '',
      level: '',
    },
  ]);
  useEffect(() => {
    getUser();
  }, []);

  const getUser = async () => {
    setLoading(true);
    console.log('Token :: ');
    const token = await localStorage.getItem('yellowToken');
    console.log('Token :: ', token);
    await axios
      .get(`${baseUrl}/api/tutors/get-profile`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        console.log('This is uusserrraaxxiiooss resume :: ', data.data.data);
        setLoading(false);
        setFirstName(data.data.data.firstName);
        setLastName(data.data.data.lastName);
        setCountryOrigin(data.data.data.countryOrigin);
        setHourlyRate(data.data.data.hourlyRate);
        setSubjectTaught(data.data.data.subjectTaught);
        setPhoneNumber(data.data.data.phoneNumber);
        setNativeLanguage(data.data.data.nativeLanguage);
        setLanguageSpoken(data.data.data.languageSpoken);
        setUserId(data.data.data._id);
        setCurrency(data.data.data.currency);
      })
      .catch((e) => {
        console.log(e);
        setLoading(false);
      });
  };

  const changeLanguage = (value, index) => {
    let newLang = languageSpoken;
    newLang[index].language = value;
    setLanguageSpoken([...newLang]);
  };

  const changeLevel = (value, index) => {
    let newLang = languageSpoken;
    newLang[index].level = value;
    setLanguageSpoken([...newLang]);
  };

