import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  message: null,
  sendingMessage: false,
  allConversations: [],
  conversationsLoading: false,
  newConversationStarting: false,
  newConversation: null,
  replyToConversationLoading: false,
  repliedConversation: null,
  allMessagesLoading: false,
  reply: null,
  allMessages: [],
  error: null,
};

const conversationSlice = createSlice({
  name: "conversation",
  initialState,
  reducers: {
    sending(state) {
      state.sendingMessage = true;
      state.message = null;
      state.error = null;
    },
    sendMessageSuccesss(state, action) {
      state.sendingMessage = false;
      state.message = action.payload;
    },
    sendMessageError(state, action) {
      state.sendingMessage = false;
      state.error = action.payload;
    },

    //getting conversations
    allConversationsLoading(state) {
      state.conversationsLoading = true;
    },
    allConversationsSuccesss(state, action) {
      state.conversationsLoading = false;
      state.allConversations = action.payload.conversations;
    },
    allConversationsError(state, action) {
      state.conversationsLoading = false;
      state.error = action.payload;
    },

    //new conversations
    newConversationLoading(state) {
      state.newConversationStarting = true;
    },
    newConversationSuccesss(state, action) {
      state.newConversationStarting = false;
      state.newConversation = action.payload;
    },
    newConversationError(state, action) {
      state.newConversationStarting = false;
      state.error = action.payload;
    },

    //reply to conversation
    replyToConversationLoading(state) {
      state.replyToConversationLoading = true;
    },
    replyToConversationSuccesss(state, action) {
      state.replyToConversationLoading = false;
      state.repliedConversation = action.payload;
    },
    replyToConversationError(state, action) {
      state.replyToConversationLoading = false;
      state.error = action.payload;
    },

    //all messages
    allMessagesLoading(state) {
      state.allMessagesLoading = true;
    },
    allMessagesSuccesss(state, action) {
      const sortedMessages = action.payload.conversation.sort(function (a, b) {
        var dateA = new Date(a.createdAt),
          dateB = new Date(b.createdAt);
        return dateA - dateB;
      });
      state.allMessagesLoading = false;
      state.allMessages = sortedMessages;
    },
    allMessagesError(state, action) {
      state.allMessagesLoading = false;
      state.error = action.payload;
    },
  },
});

export const {
  sending,
  sendMessageSuccesss,
  sendMessageError,
  allConversationsLoading,
  allConversationsSuccesss,
  allConversationsError,
  newConversationLoading,
  newConversationSuccesss,
  newConversationError,
  replyToConversationLoading,
  replyToConversationSuccesss,
  replyToConversationError,
  allMessagesLoading,
  allMessagesSuccesss,
  allMessagesError,
} = conversationSlice.actions;

export default conversationSlice.reducer;
