import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Logo from "../../assets/logo/logo.png";
import { useHistory, Link } from "react-router-dom";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import Button from "@material-ui/core/Button";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
import AccountBalanceWalletOutlinedIcon from "@material-ui/icons/AccountBalanceWalletOutlined";
import TransformOutlinedIcon from "@material-ui/icons/TransformOutlined";
import Avatar from "@material-ui/core/Avatar";
import { Grid } from "@material-ui/core";
import { landingPageTop } from "../LandingPage/translation";
import { useSelector } from "react-redux";

import BuyHoursModal from "../Modal/modal";
import { getCountryCallingCode } from "react-phone-number-input";

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  iconColor: {
    color: "lightgray",
  },
  iconColor2: {
    color: "black",
  },
  leftText: {
    marginTop: 10,
    marginLeft: 20,
    fontSize: 14,
    cursor: "pointer",
    color: "black",
  },
  button: {
    padding: "5px 14px 5px 14px",
    borderRadius: 1,
    background: "#FFB600",
    fontSize: 14,
    color: "white",
    width: "10",
    marginLeft: 10,
    marginTop: 10,
    marginRight: 10,
    cursor: "pointer",
  },
  leftTextLabel: {
    fontSize: 12,
    marginLeft: 18,
    marginTop: 5,
    fontWeight: "lighter",
    textAlign: "left",
  },
  avatar: {
    background: "gray",
    borderRadius: "50%",
    border: "1px solid gray",
    height: "50px",
    width: "50px",
  },
}));

const TopToolbar = () => {
  const classes = useStyles();
  const history = useHistory();
  const [open, setOpen] = React.useState(false);
  const language = useSelector((state) => state.languageReducer.language);
  const logout = () => {
    localStorage.removeItem("yellowToken");
    localStorage.removeItem("yellowTutor");
    localStorage.removeItem("role");
    history.push("/login");
  };
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const modalBody = (
    <div>
      <h4 id='simple-modal-title'>Achieve More With Yellow School!</h4>
      <p
        id='simple-modal-description'
        style={{ fontSize: 14, forntWeight: "lighter" }}>
        You have already spent 0 hours learning on yellow school. <br /> Most
        people need at least 40 hours of subject studying to get noticeable
        results.
      </p>
      <p style={{ marginTop: 20, color: "#FFB600" }}>Transfer Credits</p>
      <Grid container>
        <Grid lg={4} md={4}>
          <div className={classes.avatar}></div>
        </Grid>
        <Grid lg={4} md={4}>
          {" "}
          $ 45.00 / hr
        </Grid>
        <Grid lg={4} md={4}>
          {" "}
          <p className={classes.button} style={{ marginTop: 10 }}>
            Book Lesson
          </p>{" "}
        </Grid>
        <Grid lg={4} md={4}>
          <div className={classes.avatar}></div>
        </Grid>
        <Grid lg={4} md={4}>
          {" "}
          $ 45.00 / hr
        </Grid>
        <Grid lg={4} md={4}>
          {" "}
          <p className={classes.button} style={{ marginTop: 10 }}>
            Book Lesson
          </p>{" "}
        </Grid>
      </Grid>
    </div>
  );

  return (
    <Toolbar>
      {/* <IconButton
            edge='start'
            className={classes.menuButton}
            color='inherit'
            aria-label='open drawer'>
            <MenuIcon />
          </IconButton> */}
      {/* <Typography className={classes.title} variant='h6' noWrap>
            LOGO
          </Typography> */}
      <Link to='/'>
        <img src={Logo} alt='logo' className={classes.logo} />
      </Link>
      <Link to='/studentMyLessons'>
        <p className={classes.leftText}>
          {/* Dashboard */}
          {landingPageTop[9][language]}
        </p>
      </Link>

      <div className={classes.grow} />
      {/* <AccountBalanceWalletOutlinedIcon className={classes.iconColor} />{" "}
      <p className={classes.leftText}>Balance; 0 hours </p>
      <p className={classes.button} onClick={handleOpen}>
        Buy Hours
      </p>
      <BuyHoursModal
        open={open}
        body={modalBody}
        handleClose={handleClose}
        handleOpen={handleOpen}
      />
      <p className={classes.leftText}>English US Dollar</p> */}
      <div className={classes.sectionDesktop}>
        {/* <IconButton aria-label="show 4 new mails">
          <Badge>
            <HelpOutlineIcon className={classes.iconColor} />
          </Badge>
        </IconButton> */}
        <IconButton
          edge='end'
          aria-label='account of current user'
          aria-haspopup='true'
          color='inherit'>
          <ExitToAppIcon className={classes.iconColor} />
          <p
            className={classes.leftText}
            style={{ fontSize: 14 }}
            onClick={() => logout()}>
            Logout
          </p>
        </IconButton>
      </div>
    </Toolbar>
  );
};

export default TopToolbar;
