import React, { useEffect } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import StepConnector from '@material-ui/core/StepConnector';
import { Link } from 'react-router-dom';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import About from './about';
import Photo from './photo';
import VideoPage from './video/index';
import Description from './profileDescription/index';
import Availability from './availability/index';
import Vaerification from './verification/index';
import Resume from './resume';
import Nav from '../nav/topNav';
import './signup.css';

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: '#EDEFF0',
    minHeight: '100vh',
    paddingBottom: '1rem',
  },
  root1: {},
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  stepper: {
    height: 59.89,
    overflowX: 'auto',
    overflowY: 'hidden',
    [theme.breakpoints.up('sm')]: {
      justifyContent: 'center',
      alignItems: 'center',
    },
  },
  stepper1: {
    height: 0,
    margin: 0,
    padding: 0,
    width: '70%',
  },
}));

function getSteps() {
  return [
    'About',
    'Photo',
    'Description',
    'Video',
    'Availability',
    'Resume',
    'Verification',
  ];
}

export default function HorizontalLinearStepper(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [tutorId, setTutorId] = React.useState('');
  const [avatar, setAvatar] = React.useState('');
  const [skipped, setSkipped] = React.useState(new Set());
  const steps = getSteps();

  useEffect(() => {
    // Update the document title using the browser API
    if (props.location.state) {
      setTutorId(props.location.state.tutorId);
    }
  });
  const isStepOptional = (step) => {
    return step === 1;
  };

  const isStepSkipped = (step) => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    setActiveStep((prevActiveStep) => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const setAvtarImage = (av) => {
    setAvatar(av);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  function getStepContent(step) {
    switch (step) {
      case 0:
        return <About handleNext={handleNext} tutorId={tutorId} />;
      case 1:
        return (
          <Photo
            handleNext={handleNext}
            handleBack={handleBack}
            setAvtarImage={setAvtarImage}
            tutorId={tutorId}
          />
        );
      case 2:
        return (
          <Description
            handleNext={handleNext}
            handleBack={handleBack}
            image={avatar}
            tutorId={tutorId}
          />
        );
      case 3:
        return (
          <VideoPage
            handleNext={handleNext}
            tutorId={tutorId}
            handleBack={handleBack}
          />
        );
      case 4:
        return (
          <Availability
            handleNext={handleNext}
            tutorId={tutorId}
            handleBack={handleBack}
          />
        );
      case 5:
        return (
          <Resume
            handleNext={handleNext}
            tutorId={tutorId}
            handleBack={handleBack}
          />
        );
      case 6:
        return (
          <Vaerification
            handleNext={handleNext}
            tutorId={tutorId}
            handleBack={handleBack}
          />
        );
      default:
        return 'Unknown step';
    }
  }

  return (
    <>
      <Nav />
      <div className={classes.root}>
        <Grid
          container
          style={{ backgroundColor: '#384047' }}
          className={classes.stepper}
        >
          <Grid item sm={2}></Grid>
          <Grid item sm={8}>
            <Stepper
              activeStep={activeStep}
              className={classes.stepper1}
              style={{ backgroundColor: '#384047' }}
            >
              {steps.map((label, index) => {
                const stepProps = {};
                const labelProps = {};

                return (
                  <Step key={label} {...stepProps}>
                    <StepLabel
                      {...labelProps}
                      StepIconProps={{
                        classes: {
                          root: classes.MuiStepIconRoot,
                          active: classes.MuiStepIconRoot,
                        },
                      }}
                    >
                      <p style={{ color: '#FFFFFF', marginTop: 15 }}>{label}</p>
                    </StepLabel>
                  </Step>
                );
              })}
            </Stepper>
          </Grid>
        </Grid>

        <div>
          {activeStep === steps.length ? (
            <div>
              <Typography className={classes.instructions}>
                You have successfully created your profile as a tutor.
              </Typography>
              <Typography className={classes.instructions}>
                Now students can see your profile on Yellow School app.
              </Typography>
              <Typography className={classes.instructions}>
                <Link to='/login'> Click to Login.</Link>
              </Typography>
            </div>
          ) : (
            <div>
              <Typography className={classes.instructions}>
                {getStepContent(activeStep)}
              </Typography>
              {activeStep === 0 ||
              activeStep === 1 ||
              activeStep === 2 ||
              activeStep === 3 ||
              activeStep === 4 ||
              activeStep === 5 ? null : (
                <div></div>
              )}
            </div>
          )}
        </div>
      </div>
    </>
  );
}
