import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import Nav from "../../components/nav/topNav";
import { Modal, Form } from "react-bootstrap";
import { Grid } from "@material-ui/core";
import SubHeaderTutor from "../../components/nav/SubHeaderTutor";
import AccountCircle from "@material-ui/icons/AccountCircle";
import CheckCircleIcon from "@material-ui/icons/CheckCircle";
import BusIcon from "../../assets/images/busCircle.png";
import ArchiveIcon from "@material-ui/icons/Archive";
import Button from "@material-ui/core/Button";
import MonetizationOnIcon from "@material-ui/icons/AttachMoney";
import { makeStyles } from "@material-ui/core/styles";
import MessageCircle from "../../assets/images/messageCircle.png";
import ListCircle from "../../assets/images/listCircle.png";
import LogoutCircle from "../../assets/images/logoutCircle.png";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import CircularProgress from "@material-ui/core/CircularProgress";
import Footer from "../../components/footer/footer";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import { GET_TUTOR_LESSON, SCHEDULE_LESSON } from "../../Constants/sagaActions";
import TutorLessonTable from "./tutorLessonTable";
import baseUrl from "../../baseurl";
import { useHistory } from "react-router-dom";
import { myLessTrans } from "./translation";

const useStyles = makeStyles((theme) => ({
  name: {
    fontSize: 14,
    marginLeft: 15,
  },
  tableHeader: {
    fontSize: 12,
  },
  button: {
    background: "#FFB600",
    border: "none",
    color: "black",
    padding: 5,
    paddingLeft: 10,
    marginLeft: 25,
    borderRadius: 2,
  },
  iconsImages: {
    marginLeft: "5%",
  },
}));

export default function TutorMyLessonsPage() {
  const classes = useStyles();
  const history = useHistory();
  const dispatch = useDispatch();
  const [tutorLessons, setTutorLessons] = useState([]);
  const [tutorData, setTutorData] = useState({});
  const [transactionUrl, setTransactionUrl] = useState("");
  const [tutorEarnings, setTutorEarnings] = useState(0);
  const [loading, setLoading] = useState(false);
  const [show, setShow] = useState(false);
  const [show1, setShow1] = useState(false);
  const [amount, setAmount] = useState(null);
  const [loading1, setLoading1] = useState(false);
  const [loading2, setLoading2] = useState(false);
  const [accountAmount, setAccountAmount] = useState(null);
  const language = useSelector((state) => state.languageReducer.language);
  const handleClose = () => setShow1(false);
  const handleShow = () => setShow(true);
  useEffect(() => {
    getAllLessons();
    getTutor();
    getAccountInfo();
  }, []);

  const getTutor = async () => {
    let token = await localStorage.getItem("yellowToken");

    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowTutor");
    let newStu = await JSON.parse(stu);
    console.log("tutor aaaa Idddd :: ", newStu._id);
    axios
      .get(`${baseUrl}/api/tutors/getbytutorid/${newStu._id}`, config)
      .then((data) => {
        setTutorEarnings(data.data.data.totalEarnings);
        setTutorData(data.data.data);
      })
      .catch((e) => {
        console.log("tutor aaaa erro get :: ", e);
      });
  };

  const getAccountInfo = async () => {
    setLoading1(true);
    let token = await localStorage.getItem("yellowToken");
    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowTutor");
    let newStu = await JSON.parse(stu);
    console.log("tutor aaaa Idddd :: ", newStu._id);
    axios
      .post(
        `${baseUrl}/api/instructors/checkaccountstatus`,
        { tutorId: newStu._id },
        config
      )
      .then((data) => {
        if (data.data.stripe_seller && !data.data.stripe_seller) {
          setAccountAmount(data.data);
        }
      })
      .catch((e) => {
        console.log("tutor aaaa erro get :: ", e);
      })
      .finally(() => setLoading1(false));

    axios
      .post(
        `${baseUrl}/api/instructors/paidenrollment`,
        { tutorId: newStu._id, price: 20 },
        config
      )
      .then((data) => {
        console.log("dataaaa sessionnn :: ", data);
      })
      .catch((e) => {
        console.log("erro sessionnn :: ", e);
      });
  };

  const connectAccount = async () => {
    setLoading1(true);
    let token = await localStorage.getItem("yellowToken");

    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowTutor");
    let newStu = await JSON.parse(stu);
    console.log("tutor aaaa Idddd :: ", newStu._id);
    axios
      .post(
        `${baseUrl}/api/instructors/link-tutor-account`,
        { tutorId: newStu._id },
        config
      )
      .then((data) => {
        console.log("tutor aaaa data:: ", data);
        setTransactionUrl(data.data);
        setShow(true);
      })
      .catch((e) => {
        console.log("tutor aaaa erro get :: ", e);
      })
      .finally(() => setLoading1(false));
  };

  const madeTransaction = async () => {
    setLoading2(true);
    let token = await localStorage.getItem("yellowToken");

    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowTutor");
    let newStu = await JSON.parse(stu);
    console.log("tutor aaaa Idddd :: ", newStu._id);
    axios
      .post(
        `${baseUrl}/api/tutors/withdrawl`,
        { tutorId: newStu._id, amount: amount },
        config
      )
      .then((data) => {
        console.log("tutor aaaa data amount:: ", data.data);
        getTutor();
        setShow1(false);
      })
      .catch((e) => {
        console.log("tutor aaaa erro get :: ", e);
      })
      .finally(() => setLoading2(false));
  };

  const getAllLessons = async () => {
    setLoading(true);
    let token = await localStorage.getItem("yellowToken");

    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowTutor");
    let newStu = await JSON.parse(stu);
    console.log("Lessss user tutt :; ", newStu);
    axios
      .get(`${baseUrl}/api/lessonRequest/lessonByTutorId/${newStu._id}`, config)
      .then((data) => {
        setTutorLessons(data.data.data);
        setLoading(false);
      })
      .catch((e) => {
        console.log("Error getting lessons:: ", e);
        setLoading(false);
      });
  };

  const [anchorEl, setAnchorEl] = React.useState(null);
  const isMenuOpen = Boolean(anchorEl);

  const gettingTutorLessons = useSelector(
    (state) => state.tutorLesson.gettingTutorLessons
  );
  const lessonScheduling = useSelector(
    (state) => state.tutorLesson.lessonScheduling
  );

  const handleMenuClose = () => {
    setAnchorEl(null);
  };
  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const scheduleLesson = (lessonId) => {
    dispatch({
      type: SCHEDULE_LESSON,
      payload: { status: "inprogress", lessonId },
    });
  };

  useEffect(() => {
    dispatch({ type: GET_TUTOR_LESSON });
  }, []);

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>Student</MenuItem>
      <MenuItem onClick={handleMenuClose}>Student</MenuItem>
    </Menu>
  );
  return (
    <div>
      <Modal show={show}>
        <Modal.Header closeButton>
          <Modal.Title>Account Connected</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <CheckCircleIcon style={{ color: "green", fontSize: "6rem" }} />
            <p>Please click below to connect with your bank.</p>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <a href={transactionUrl}>Continue</a>
        </Modal.Footer>
      </Modal>

      <Modal show={show1} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Withdraw your Funds</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Enter Amount</Form.Label>
              <Form.Control
                type="number"
                placeholder="0"
                onChange={(e) => {
                  let value = parseInt(e.target.value);
                  if (value <= tutorEarnings) {
                    setAmount(value);
                  }
                  if (!value) {
                    setAmount(null);
                  }
                  console.log("valueeee :: ", value);
                }}
                value={amount}
              />
              <Form.Text className="text-muted">
                Please enter amount less than {tutorEarnings}
              </Form.Text>
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <button onClick={() => setShow1(false)} className={classes.button}>
            Close
          </button>
          <button
            className={classes.button}
            onClick={madeTransaction}
            style={{ color: "white" }}
            disabled={loading2}
          >
            {loading2 ? (
              <CircularProgress
                size={25}
                style={{ marginTop: "10px", color: "white" }}
              />
            ) : (
              "Continue"
            )}
          </button>
        </Modal.Footer>
      </Modal>

      <Nav></Nav>
      <SubHeaderTutor for="My Lessons"></SubHeaderTutor>
      <Grid container style={{ background: "#EDEFF0", height: "90vh" }}>
        <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
          {/* <Grid
            container
            direction="row"
            alignItems="center"
            style={{
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
              height: 70,
              background: "white",
              borderRadius: 2,
              marginTop: 30,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            <Grid item md={7} lg={7} xl={7} sm={7} xs={7}>
              <Grid container direction="row" alignItems="flex-start">
                <span>Next lesson: Wednesday, 27 May at 11:00 with</span>
                <AccountCircle
                  style={{ color: "#E9E6E3", marginLeft: 15 }}
                ></AccountCircle>
                <span style={{ marginLeft: 10 }}>TNDRS A.</span>
              </Grid>
            </Grid>
            <Grid item md={5} lg={5} xl={5} sm={5} xs={5}>
              <Grid
                container
                direction="row"
                justify="flex-end"
                alignItems="center"
              >
                <img src={BusIcon} style={{ width: 45, height: 45 }}></img>
                <span style={{ color: "#FFB600" }}>Enter Classroom</span>
              </Grid>
            </Grid>
            <div style={{ width: "35%" }}> </div>
          </Grid> */}
          <div
            style={{
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
              background: "white",
            }}
          >
            <Grid
              container
              direction="row"
              alignItems="center"
              style={{
                height: 60,
                borderRadius: 2,
                marginTop: 30,
                paddingLeft: 20,
                borderBottom: "1px solid #AAB5BC",
                paddingRight: 20,
              }}
            >
              <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
                <Grid container direction="column" alignItems="flex-start">
                  <span style={{ fontSize: 18 }}>
                    {/* My lessons */}
                    {myLessTrans[0][language]}
                  </span>
                </Grid>
              </Grid>
              <Grid item md={9} lg={9} xl={9} sm={9} xs={9}>
                <Grid
                  container
                  direction="row"
                  alignItems="center"
                  justify="flex-end"
                >
                  {accountAmount ? (
                    <>
                      <span style={{ fontWeight: "bold", marginRight: 20 }}>
                        Account connected payment will transfer to this account
                        in 48hurs
                      </span>

                      {/* <MonetizationOnIcon
                        style={{ color: "gray" }}
                      ></MonetizationOnIcon>
                      <span style={{ fontWeight: "bold", fontSize: 20 }}>
                        {tutorEarnings}
                      </span> */}
                    </>
                  ) : (
                    <span style={{ fontWeight: "bold", marginRight: 20 }}>
                      Please connect your account
                    </span>
                  )}

                  {/* {!tutorData.stripe_account_id &&
                  !tutorData.stripe_connect_url ? (
                    <Button
                      className={classes.button}
                      onClick={connectAccount}
                      disabled={loading1}
                    >
                      {loading1 ? (
                        <CircularProgress
                          size={25}
                          style={{ marginTop: "10px", color: "white" }}
                        />
                      ) : (
                        "Connect Account"
                      )}
                    </Button>
                  ) : (
                    <Button
                      className={classes.button}
                      onClick={() => setShow1(true)}
                    >
                      Withdrawl Money
                    </Button>
                  )} */}
                  <Button
                    className={classes.button}
                    onClick={connectAccount}
                    disabled={loading1}
                  >
                    {loading1 ? (
                      <CircularProgress
                        size={25}
                        style={{ marginTop: "10px", color: "white" }}
                      />
                    ) : (
                      "Connect Account"
                    )}
                  </Button>
                </Grid>
              </Grid>
            </Grid>

            {!loading ? (
              <TutorLessonTable
                lessons={tutorLessons}
                scheduleLesson={scheduleLesson}
                lessonScheduling={lessonScheduling}
              />
            ) : (
              <CircularProgress
                size={25}
                style={{ marginTop: "10px", color: "red" }}
              />
            )}
            {/* {renderMenu} */}
          </div>
        </Grid>
        <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
      </Grid>

      <div>
        <Footer />
      </div>
    </div>
  );
}
