import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Modal from "@material-ui/core/Modal";
import Collapse from "@material-ui/core/Collapse";
import IconButton from "@material-ui/core/IconButton";
import Table from "@material-ui/core/Table";
import TextField from "@material-ui/core/TextField";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
import CircularProgress from "@material-ui/core/CircularProgress";
import dateFormat from "dateformat";
import MessageCircle from "../../assets/images/messageCircle.png";
import ListCircle from "../../assets/images/listCircle.png";
import LogoutCircle from "../../assets/images/logoutCircle.png";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import AccountCircle from "@material-ui/icons/AccountCircle";
import axios from "axios";
import { useHistory } from "react-router-dom";
import BaloonImage from "../../assets/popups/baloon.png";
import TeacherImage from "../../assets/popups/teacher.png";
import Button from "@material-ui/core/Button";
import ReactStars from "react-rating-stars-component";
import CloseIcon from "@material-ui/icons/Close";
import { Link } from "react-router-dom";
import baseUrl from "../../baseurl";
import { parseJSON } from "date-fns";
import { myLessTrans } from "./translation";
import { useSelector } from "react-redux";

const useRowStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      borderBottom: "unset",
    },
  },
  tableHeader: {
    fontSize: 12,
  },
  button: {
    background: "#FFB600",
    border: "none",
    color: "white",
    padding: 5,
    paddingLeft: 10,
    width: 150,

    borderRadius: 2,
  },
  iconsImages: {},
  paper: {
    position: "absolute",
    width: 700,

    backgroundColor: theme.palette.background.paper,
    top: "10%",
    left: "30%",
    border: "none",
    paddingLeft: "4rem",
    padding: theme.spacing(2, 4, 3),
  },
}));

function createData(name, calories, fat, carbs, protein, price) {
  return {
    name,
    calories,
    fat,
    carbs,
    protein,
    price,
    history: [
      { date: "2020-01-05", customerId: "11091700", amount: 3 },
      { date: "2020-01-02", customerId: "Anonymous", amount: 1 },
    ],
  };
}

function Row(props) {
  const { lesson } = props;
  const { tutorId, status, lessionDate, isCompleted } = lesson;
  const [open, setOpen] = React.useState(false);
  const language = useSelector((state) => state.languageReducer.language);

  const classes = useRowStyles();

  return (
    <React.Fragment>
      <>
        <TableRow
          className={classes.root}
          style={{
            borderBottom: "1px solid #AAB5BC",
            borderTop: "1px solid #AAB5BC",
          }}
        >
          <TableCell></TableCell>
          <TableCell>
            {/* <IconButton
            aria-label='expand row'
            size='small'
            onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton> */}
            <img
              src={tutorId.imageUrl}
              style={{ width: 30, borderRadius: 30, marginRight: 10 }}
            ></img>
          </TableCell>

          <TableCell align="left">{tutorId.firstName}</TableCell>
          <TableCell align="left">{tutorId.subjectTaught}</TableCell>
          <TableCell>
            {" "}
            <span style={{ background: "lightgray" }}>
              {" "}
              {status == "completed" ? myLessTrans[6][language] : status}{" "}
            </span>
          </TableCell>
          <TableCell align="left">
            {lessionDate !== ""
              ? dateFormat(lessionDate, "mmmm dS")
              : "Not define"}
          </TableCell>
          <TableCell align="left">
            {" "}
            <span style={{ background: "lightgray" }}>
              {" "}
              {isCompleted ? (
                <span
                  onClick={() => {
                    props.handleCompletePopup(
                      tutorId.imageUrl,
                      tutorId.firstName,
                      lessionDate,
                      lesson.startTime,
                      lesson._id,
                      tutorId._id
                    );
                  }}
                >
                  {/* Marked Completed */}
                  {myLessTrans[7][language]}
                </span>
              ) : (
                <Link
                  to="#"
                  onClick={() => {
                    props.handleCompletePopup(
                      tutorId.imageUrl,
                      tutorId.firstName,
                      lessionDate,
                      lesson.startTime,
                      lesson._id,
                      tutorId._id
                    );
                  }}
                >
                  {/* Mark Complete */}
                  {myLessTrans[6][language]}
                </Link>
              )}
            </span>
            {/* <Link to={{ pathname:'/tutorMyMessages', state: { studentName: studentId.name} }}><img src={MessageCircle} className={classes.iconsImages} /></Link> */}
          </TableCell>
        </TableRow>
        <TableRow>
          <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Box margin={1}>Further details goes here.</Box>
            </Collapse>
          </TableCell>
        </TableRow>
      </>
    </React.Fragment>
  );
}

export default function TutorLessonTable({ lessons, getData }) {
  const classes = useRowStyles();
  const history = useHistory();
  const [showCompleteLesson, setShowCompleteLesson] = useState(false);
  const [showHowLesson, setShowHowLesson] = useState(false);
  const [showRateLesson, setShowRateLesson] = useState(false);
  const [showReviewLesson, setShowReviewLesson] = useState(false);
  const [loading, setLoading] = useState(false);
  const [reviewLoading, setReviewLoading] = useState(false);
  const [tutorImage, setTutorImage] = useState("");
  const [tutorName, setTutorName] = useState("");
  const [lessonDate, setLessonDate] = useState("");
  const [lessonTime, setLessonTime] = useState("");
  const [lessonId, setLessonId] = useState("");
  const [commentText, setCommentText] = useState("");
  const [tutorId, setTutorId] = useState("");
  const [rating, setRating] = useState(0);
  const language = useSelector((state) => state.languageReducer.language);

  useEffect(() => {
    onRatingPredded();
  }, []);
  const onRatingPredded = async () => {
    setReviewLoading(true);
    let token = await localStorage.getItem("yellowToken");
    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowStudent");
    let student = await JSON.parse(stu);
    let reviewData = {
      publisher: {
        fullName: student.name,
        imageUrl: student.imageUrl,
        studentId: student._id,
      },
      stars: rating,
      comment: commentText,
      lessonId: lessonId,
    };
    axios
      .put(
        `${baseUrl}/api/tutors/updaterating/${tutorId}`,
        { rating: reviewData },
        config
      )
      .then((res) => {
        setReviewLoading(false);
        setShowRateLesson(true);
        setShowHowLesson(false);
        setShowReviewLesson(false);
        setShowCompleteLesson(false);
      })
      .catch((err) => {
        setReviewLoading(false);
      });
  };

  const updateLesson = async () => {
    setLoading(true);
    let token = await localStorage.getItem("yellowToken");
    const config = {
      headers: {
        Authorization: token,
      },
    };
    let stu = await localStorage.getItem("yellowStudent");
    let newStu = await JSON.parse(stu);
    axios
      .put(
        `${baseUrl}/api/lessonRequest/updatelessonstatus`,
        { lessonId: lessonId },
        config
      )
      .then((data) => {
        setShowRateLesson(false);
        setShowHowLesson(true);
        setLoading(false);
        getData();
      })
      .catch((e) => {
        setShowRateLesson(false);
        setShowHowLesson(false);
        setLoading(false);
      });
  };

  const handleCompletePopup = (tImage, tName, lDate, lTime, id, tutId) => {
    setTutorImage(tImage);
    setTutorName(tName);
    setLessonDate(lDate);
    setLessonTime(lTime);
    setShowCompleteLesson(true);
    setLessonId(id);
    setTutorId(tutId);
  };

  const TABLE_HEADER = [
    { label: myLessTrans[2][language] },
    { label: myLessTrans[3][language] },
    { label: myLessTrans[4][language] },
    { label: myLessTrans[5][language] },
    { label: "" },
    { label: "" },
    { label: "" },
  ];
  return (
    <>
      <TableContainer component={Paper}>
        <Table aria-label="collapsible table">
          <TableBody>
            <TableRow>
              <TableCell />
              <TableCell />
              {TABLE_HEADER.map(({ label }, i) => (
                <TableCell
                  className={classes.tableHeader}
                  style={{ textAlign: "left" }}
                  key={i}
                >
                  <b>{label}</b>
                </TableCell>
              ))}
            </TableRow>

            {lessons.map((lesson) => (
              <Row
                key={lesson._id}
                lesson={lesson}
                handleCompletePopup={handleCompletePopup}
              />
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modal open={showReviewLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-end",
            }}
          >
            <CloseIcon
              style={{ cursor: "pointer" }}
              onClick={() => setShowReviewLesson(false)}
            />
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-start",
              marginTop: "1rem",
            }}
          >
            <img
              src={tutorImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id="simple-modal-title"
            style={{ fontSize: 35, marginTop: "4rem" }}
          >
            Please, leave a review about your lesson with <b>{tutorName}</b>.
            Thank you !
          </h2>

          <div>
            <TextField
              id="outlined-multiline-static"
              label="Riew"
              multiline
              rows={4}
              placeholder="Write your review here"
              variant="outlined"
              focused
              style={{ width: "100%", marginTop: "1rem" }}
              onChange={(e) => setCommentText(e.target.value)}
            />
          </div>

          <Button
            onClick={() => {
              onRatingPredded();
            }}
            variant="contained"
            style={{
              background: "#FFB600",
              color: "white",
              fontSize: 22,
              marginTop: "4rem",
            }}
            disabled={loading}
          >
            {reviewLoading ? (
              <CircularProgress size={25} style={{ color: "white" }} />
            ) : (
              "Submit Review"
            )}
          </Button>
        </div>
      </Modal>

      <Modal open={showCompleteLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-end",
            }}
          >
            <CloseIcon
              style={{ cursor: "pointer" }}
              onClick={() => setShowCompleteLesson(false)}
            />
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-start",
              marginTop: "1rem",
            }}
          >
            <img
              src={tutorImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id="simple-modal-title"
            style={{ fontSize: 35, marginTop: "4rem" }}
          >
            Did you complete your lessons
            <br />
            with {tutorName}?
          </h2>
          <p
            id="simple-modal-description"
            style={{ fontSize: 25, marginTop: "4rem" }}
          >
            {dateFormat(
              lessonDate !== null && lessonDate !== ""
                ? lessonDate
                : Date.now(),
              "dddd, mmmm dS"
            )}{" "}
            at {lessonTime} 1 hour English language lesson
          </p>

          <Button
            onClick={() => {}}
            variant="contained"
            style={{
              background: "#FFB600",
              color: "white",
              fontSize: 22,
              marginTop: "4rem",
            }}
            disabled={loading}
            onClick={() => {
              updateLesson();
            }}
          >
            {loading ? (
              <CircularProgress size={25} style={{ color: "white" }} />
            ) : (
              "Yes, Confirm Lessons"
            )}
          </Button>
          <br />
          <Button
            variant="contained"
            onClick={() => setShowCompleteLesson(false)}
            style={{
              background: "#F8E0A4",
              color: "#DFA003",
              fontSize: 22,
              marginTop: "1rem",
            }}
          >
            Report issue
          </Button>
        </div>
      </Modal>

      <Modal open={showHowLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-end",
            }}
          >
            <CloseIcon
              style={{ cursor: "pointer" }}
              onClick={() => setShowHowLesson(false)}
            />
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-start",
              marginTop: "3rem",
            }}
          >
            <img
              src={tutorImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id="simple-modal-title"
            style={{ fontSize: 35, marginTop: "2rem" }}
          >
            How was your lesson
          </h2>
          <p
            id="simple-modal-description"
            style={{ fontSize: 25, marginTop: "3rem" }}
          >
            Rating is anonymous and helps you track <br /> you progress
          </p>

          <div style={{ marginTop: "2rem" }}>
            <ReactStars
              count={5}
              size={52}
              activeColor="#ffd700"
              isHalf={true}
              onChange={(newRating) => setRating(newRating)}
            />
          </div>

          <Button
            variant="contained"
            style={{
              background: "#FFB600",
              color: "white",
              fontSize: 22,
              marginTop: "3rem",
              width: 168,
              height: 60,
            }}
            onClick={() => {
              // onRatingPredded();
              setShowHowLesson(false);
              setShowRateLesson(false);
              setShowReviewLesson(true);
              setShowCompleteLesson(false);
            }}
          >
            Continue
          </Button>
        </div>
      </Modal>

      <Modal open={showRateLesson}>
        <div className={classes.paper}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-end",
            }}
          >
            <CloseIcon
              style={{ cursor: "pointer" }}
              onClick={() => setShowRateLesson(false)}
            />
          </div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "flex-start",
              marginTop: "1rem",
            }}
          >
            <img
              src={tutorImage}
              style={{ width: 150, height: 150, borderRadius: 100 }}
            />
          </div>
          <h2
            id="simple-modal-title"
            style={{ fontSize: 35, marginTop: "4rem" }}
          >
            Lesson rated! Ready to learn <br />
            from {tutorName}
          </h2>
          <p
            id="simple-modal-description"
            style={{ fontSize: 25, marginTop: "4rem" }}
          >
            Keep up the good work! Reserve your favorite <br />
            lesson times to continue your progress
          </p>

          <Button
            variant="contained"
            style={{
              background: "#FFB600",
              color: "white",
              fontSize: 22,
              marginTop: "4rem",
              width: 168,
              height: 60,
            }}
            onClick={() => {
              setShowHowLesson(false);
              setShowRateLesson(false);
              setShowCompleteLesson(false);
              setShowReviewLesson(false);
              history.push("/requestTutorHome");
            }}
          >
            Buy Hours
          </Button>
        </div>
      </Modal>
    </>
  );
}
