import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Logo from "../../assets/logo/logo.png";

import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";

const useStyles = makeStyles((theme) => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  iconColor: {
    color: "#8A959E",
  },
  iconColor2: {
    color: "black",
  },
  leftText: {
    marginTop: 10,
    marginLeft: 10,
    fontSize: 14,
    cursor: "pointer",
  },
  leftTextLabel: {
    fontSize: 12,
    marginLeft: 18,
    marginTop: 5,
    fontWeight: "lighter",
    textAlign: "left",
  },
}));

const TopToolbar = () => {
  const classes = useStyles();
  return (
    <Toolbar>
      {/* <IconButton
            edge='start'
            className={classes.menuButton}
            color='inherit'
            aria-label='open drawer'>
            <MenuIcon />
          </IconButton> */}
      {/* <Typography className={classes.title} variant='h6' noWrap>
            LOGO
          </Typography> */}
      <img src={Logo} alt="logo" className={classes.logo} />
      <p className={classes.leftText}>Find Tutors</p>
      <p className={classes.leftText}>Become a tutor</p>
      <div className={classes.grow} />

      <p className={classes.leftText}>English US Dollar</p>
      <div className={classes.sectionDesktop}>
        <IconButton aria-label="show 4 new mails">
          <Badge>
            <HelpOutlineIcon className={classes.iconColor} />
          </Badge>
        </IconButton>
        <IconButton
          edge="end"
          aria-label="account of current user"
          aria-haspopup="true"
          color="inherit"
        >
          <ExitToAppIcon className={classes.iconColor} />
          <p className={classes.leftText} style={{ fontSize: 14 }}>
            Login
          </p>
        </IconButton>
      </div>
    </Toolbar>
  );
};

export default TopToolbar;
