import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import Master from '../../assets/m2.png'
import American from '../../assets/a2.png'
import Visa from '../../assets/v2.png'
import Discover from '../../assets/d2.png'
import { Grid, Box, TextField, Button } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
    card: {
        background: "white",
        margin: '1rem',
        marginTop: 0,
        borderRadius: 5,
      },
      pass: {
          color: '#6F757B',
          fontSize: 26,
          textAlign: 'left',
          padding: '1rem',
          paddingLeft: '2rem',
          borderBottomStyle: 'solid',
          borderBottomWidth: 1,
          borderBottomColor: '#DADFE1'
      },
      stc: {
        color: '#384047',
        fontSize: 23,
        textAlign: 'left',
    },
    stcp: {
color: '#6F757B',
fontSize: 17,
textAlign: 'left',
    },
    logos: {
        textAlign: 'left',
    },
    button: {
        backgroundColor: '#FFB600',
        color: 'white',
       

    }
}))

export default function PaymentMethod() {
    const classes = useStyles();
    return (
        <div className={classes.card}>
            <div>
                <p className={classes.pass}>Payment methods</p>
            </div>
           <div  style={{ marginTop: '2rem', marginLeft: '2rem' }}>
               <p className={classes.stc}>Save a card</p>
           </div>

           <div  style={{ marginTop: '2rem', marginLeft: '2rem' }}>
               <p className={classes.stcp}>Make fast and easy payments. Save a card to use refills and keep weekly lessons reserved automatically.</p>
               <p className={classes.stcp}>Payment info is encrypted and stored securely by Braintree, a PayPal service.</p>
           </div>

           <div  style={{ marginTop: '2rem', marginLeft: '2rem' }}>
               <Box display='flex' >
                    <Box p={2}>
                    <img src={Visa} className={classes.logos}/>
                    </Box>
                    <Box p={2}>
                    <img src={Master} className={classes.logos}/>
                    </Box>
                    <Box p={2}>
                    <img src={Discover} className={classes.logos}/>
                    </Box>
                    <Box p={2}>
                    <img src={American} className={classes.logos}/>
                    </Box>
               </Box>
           </div>
          <div style={{ marginLeft: '2rem',  textAlign: 'left', }}>
          <Button variant="contained" className={classes.button} disableElevation>
      Save a card
    </Button>
          </div>
          <Box display='flex' style={{ marginTop: '2rem'}}></Box>
        </div>
    )
}
