import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { myLessTrans } from "./translation";
import { useSelector } from "react-redux";

const useStyles = makeStyles((theme) => ({
  links: {
    marginBottom: 0,
    marginLeft: 15,
    color: "white",
    fontSize: 13,
    color: "#AAB5BC",
    cursor: "pointer",
  },
  linkActive: {
    marginBottom: 0,
    marginLeft: 15,
    color: "white",
    fontSize: 13,
    fontWeight: "bold",
    color: "white",
    borderBottom: "1px solid white",
    cursor: "pointer",
  },
}));

export default function SubHeader(props) {
  const history = useHistory();
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);
  const onClick = (url) => {
    history.push(`/${url}`);
  };

  return (
    <div style={{ background: "#384047" }}>
      <Grid
        container
        direction="row"
        style={{ height: 50, paddingLeft: 20 }}
        alignItems="center"
        justify="flex-start"
      >
        <p
          className={
            props.for === "My Lessons" ? classes.linkActive : classes.links
          }
          onClick={() => onClick("tutorMyLessons")}
        >
          {/* My Lessons */}
          {myLessTrans[0][language]}
        </p>
        <p
          className={
            props.for === "Messages" ? classes.linkActive : classes.links
          }
          onClick={() => onClick("tutorMyMessages")}
        >
          {/* Messages */}
          {myLessTrans[1][language]}
        </p>
        <p
          className={
            props.for === "Calendar" ? classes.linkActive : classes.links
          }
          onClick={() => onClick("tutorCalendar")}
        >
          Calendar
        </p>
        <p
          className={
            props.for === "TutorAvailability"
              ? classes.linkActive
              : classes.links
          }
          onClick={() => onClick("tutorAvailability")}
        >
          Availability
        </p>
        <p
          className={
            props.for === "Settings" ? classes.linkActive : classes.links
          }
          onClick={() => onClick("settings")}
        >
          Settings
        </p>
      </Grid>
    </div>
  );
}
