import React from 'react';
import CompassIcon from '../../assets/images/compassIcon.png';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import OneIcon from '../../assets/images/oneIcon.png';
import TwoIcon from '../../assets/images/twoIcon.png';
import ThreeIcon from '../../assets/images/threeIcon.png';
import PerfectTutor from '../../assets/images/perfectTutor.png';
import Calender from '../../assets/images/calenders.png';
import Lessons from '../../assets/images/Lessons.png';
import GuranteeIcon from '../../assets/images/guaranteeIcon.png';
import { landingPageRequestPrivateTutor } from '../../Translation/index';
import { useSelector } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  centralCard: {
    marginTop: 50,
    background: 'white',
    borderRadius: 5,
  },
  CompassIcon: {
    width: 60,
    height: 60,
    marginTop: -30,
  },
  semiBoldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 500,
  },
  numberIcon: {
    marginLeft: 20,
    marginRight: 15,
  },
  sideImage: {
    width: '95%',
    [theme.breakpoints.down('sm')]: {
      width: '100%',
    },
  },
  textRowContainer: {
    height: 250,
  },
  leftAlignedText: {
    textAlign: 'left',
    [theme.breakpoints.down('sm')]: {
      textAlign: 'center',
    },
  },
  bigPictures: {
    width: '100%',
    height: 250,
    objectFit: 'contain',
    [theme.breakpoints.down('sm')]: {
      width: '100%',
    },
  },
}));

export default function HowYellowWork() {
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);

  return (
    <div style={{ background: '#EDEFF0' }}>
      <Grid container>
        <Grid item md={1} lg={1} xl={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={12}
          xs={12}
          style={{ margin: '1rem' }}
        >
          <div className={classes.centralCard}>
            <img className={classes.CompassIcon} src={CompassIcon}></img>
            <p className={classes.semiBoldText}>
              {/* How Yellow School works */}
              {landingPageRequestPrivateTutor[0][language]}
            </p>
            <span>
              {/* Learn languages online with the world's best tutors */}
              {landingPageRequestPrivateTutor[1][language]}
            </span>
            <Grid container style={{ marginTop: 50 }}>
              <Grid item md={6} xl={6} lg={6} sm={12} xs={12}>
                <Grid
                  container
                  className={classes.textRowContainer}
                  direction='row'
                  justify='flex-start'
                  alignItems='center'
                >
                  <Grid item md={2} lg={2} xl={2} sm={12} xs={12}>
                    <img className={classes.numberIcon} src={OneIcon}></img>
                  </Grid>

                  <Grid item md={4} lg={4} xl={4} sm={12} xs={12}>
                    <Grid
                      className={classes.leftAlignedText}
                      container
                      direction='column'
                    >
                      <span style={{ marginTop: 6 }}>
                        {/* Find the perfect tutor */}
                        {landingPageRequestPrivateTutor[2][language]}
                      </span>
                      <span style={{ fontSize: 12 }}>
                        {/* Search by language, price, nationality, or availability */}
                        {landingPageRequestPrivateTutor[3][language]}
                      </span>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item md={6} xl={6} lg={6} sm={12} xs={12}>
                <img className={classes.bigPictures} src={PerfectTutor}></img>
              </Grid>
              <Grid item md={6} xl={6} lg={6} sm={12} xs={12}>
                <Grid
                  container
                  className={classes.textRowContainer}
                  direction='row'
                  justify='flex-start'
                  alignItems='center'
                >
                  <Grid item md={2} lg={2} xl={2} sm={12} xs={12}>
                    <img className={classes.numberIcon} src={TwoIcon}></img>
                  </Grid>

                  <Grid item md={4} lg={4} xl={4} sm={12} xs={12}>
                    <Grid
                      className={classes.leftAlignedText}
                      container
                      direction='column'
                    >
                      <span style={{ marginTop: 6 }}>
                        {/* Schedule your lesson */}
                        {landingPageRequestPrivateTutor[4][language]}
                      </span>
                      <span style={{ fontSize: 12 }}>
                        {/* View your tutor's calendar and pick the perfect time for
                        your busy schedule */}
                        {landingPageRequestPrivateTutor[5][language]}
                      </span>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item md={6} xl={6} lg={6} sm={12} xs={12}>
                <img className={classes.sideImage} src={Calender}></img>
              </Grid>
              <Grid item md={6} xl={6} lg={6} sm={12} xs={12}>
                <Grid
                  container
                  className={classes.textRowContainer}
                  direction='row'
                  justify='flex-start'
                  alignItems='center'
                >
                  <Grid item md={2} lg={2} xl={2} sm={12} xs={12}>
                    <img className={classes.numberIcon} src={ThreeIcon}></img>
                  </Grid>

                  <Grid item md={4} lg={4} xl={4} sm={12} xs={12}>
                    <Grid
                      className={classes.leftAlignedText}
                      container
                      direction='column'
                    >
                      <span style={{ marginTop: 6 }}>
                        {/* Take your first lesson */}
                        {landingPageRequestPrivateTutor[6][language]}
                      </span>
                      <span style={{ fontSize: 12 }}>
                        {/* Talk with your tutor about the skills you need and get a
                        personalized plan to achieve your goals */}
                        {landingPageRequestPrivateTutor[7][language]}
                      </span>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item md={6} xl={6} lg={6} sm={12} xs={12}>
                <img className={classes.sideImage} src={Lessons}></img>
              </Grid>
            </Grid>
            <img style={{ width: 80, height: 80 }} src={GuranteeIcon}></img>
            <p className={classes.semiBoldText}>
              {/* 100% Satisfaction Guarantee */}
              {landingPageRequestPrivateTutor[8][language]}
            </p>
            <p>
              <span style={{ width: 250 }}>
                {/* If you are not satisfied with your trial lesson, we will give
                you a free replacement with a new tutor or a full refund */}
                {landingPageRequestPrivateTutor[9][language]}
              </span>
            </p>
            <p style={{ height: 50 }}></p>
            {/* <Grid
              container
              direction="row"
              justify="flex-start"
              alignItems="flex-start"
              md={6}
              xl={6}
              lg={6}
            >
              <img src={OneIcon}></img>
              <Grid item>
                <Grid container direction="column">
                  <span style={{ marginTop: 6 }}>Find the perfect tutor</span>
                  <span>Find the perfect tutor</span>
                </Grid>
              </Grid>
            </Grid>
            <Grid
              container
              direction="row"
              justify="flex-start"
              alignItems="flex-start"
              md={6}
              xl={6}
              lg={6}
            >
              <img src={OneIcon}></img>
              <Grid item>
                <Grid container direction="column">
                  <span style={{ marginTop: 6 }}>Find the perfect tutor</span>
                  <span>Find the perfect tutor</span>
                </Grid>
              </Grid>
            </Grid> */}
          </div>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
}
