import React, { useState } from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import FormControl from "@material-ui/core/FormControl";
import Grid from "@material-ui/core/Grid";
import NativeSelect from "@material-ui/core/NativeSelect";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Dropzone from "react-dropzone";
import axios from "axios";
import CircularProgress from "@material-ui/core/CircularProgress";
import Image1 from "../../assets/images/a.png";
import Image2 from "../../assets/images/b.png";
import Image3 from "../../assets/images/c.png";
import Tick from "../../assets/images/tick.png";
import baseUrl from "../../baseurl";

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: "#FFFFFF",
    padding: "2rem",
    borderRadius: 5,
    marginTop: "3rem",
  },
  headings: {
    color: "#6F757B",
    fontSize: 28,
  },
  button: {
    marginTop: theme.spacing(3),
    backgroundColor: "#FFB600",
    color: "white",
  },
  buttonUpload: {
    backgroundColor: "#FFB600",
    color: "white",
  },
  txt1: {
    color: "#6F757B",
    fontSize: 22,
    margin: 0,
    padding: 0,
  },
  txt2: {
    color: "#6F757B",
    fontSize: 15,
    margin: 0,
    padding: 0,
  },
  txt3: {
    color: "#6F757B",
    fontSize: 15,
    margin: 0,
    padding: 0,
    marginLeft: theme.spacing(3),
  },
  txt4: {
    color: "#6F757B",
    fontSize: 18,
    margin: 0,
    padding: 0,
    marginLeft: theme.spacing(10),
  },

  buttonNext: {
    marginTop: theme.spacing(5),
    backgroundColor: "#FFB600",
    color: "white",
  },
}));
export default function Photo(props) {
  const classes = useStyles();
  const [avatar, setAvatar] = useState(null);
  const [file, setFile] = useState(null);
  const [error, setError] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const handleFile = (e) => {
    var reader = new FileReader();
    var file = e.target.files[0];
    setFile(file);
    var tmppath = URL.createObjectURL(e.target.files[0]);
    props.setAvtarImage(tmppath);
    setAvatar(tmppath);
  };

  const onSubmit = async () => {
    setError(false);
    setLoading(true);
    if (avatar === null) {
      setError(true);
      setLoading(false);
    } else {
      var bodyFormData = new FormData();
      bodyFormData.set("image", file);
      let data = {
        image: file,
      };
      await axios
        .put(baseUrl + "/api/tutors/addImage/" + props.tutorId, bodyFormData)
        .then(async (res) => {
          //   let allLessons = [];

          console.log("Image uploaded suuuessss :: ", res);

          setLoading(false);
          props.handleNext();
        })
        .catch((error) => {
          setLoading(false);
          setError(true);
          console.log("Error uploading image :: ", error);
        });
    }
  };

  return (
    <div style={{ margin: "1rem" }}>
      <Container maxWidth='md' className={classes.container} boxShadow={3}>
        <Typography
          className={classes.headings}
          variant='subtitle1'
          align='left'
          gutterBottom>
          Profile photo
        </Typography>
        {error ? (
          <Grid container alignItems='left'>
            <p style={{ fontSize: 13, color: "red" }}>Please select photo</p>
          </Grid>
        ) : null}
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Grid container spacing={3}>
              <Grid item xs={12}>
                <Typography
                  className={classes.txt1}
                  variant='subtitle1'
                  align='left'
                  gutterBottom>
                  Make a great first impression
                </Typography>
                <Typography
                  className={classes.txt2}
                  variant='subtitle1'
                  align='left'
                  gutterBottom>
                  Tutors who look friendly and professional get most students
                </Typography>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex' flexDirection='row'>
                  <div class='upload-btn-wrapper'>
                    <button class='btnFilled'>Upload Photo</button>
                    <input type='file' onChange={handleFile} name='myfile' />
                  </div>
                  {/* <Button variant="contained" className={classes.buttonUpload}>
                    Upload Photo
                    <input
                      onChange={handleFile}
                      type="file"
                      style={{ display: "none" }}
                    />
                  </Button> */}
                  <Typography
                    className={classes.txt3}
                    align='left'
                    gutterBottom>
                    JPG or PNG format <br></br> Maximum 5MB
                  </Typography>
                </Box>
              </Grid>
              <Grid
                item
                xs={12}
                style={{
                  height: "28rem",
                  borderStyle: "dotted",
                  borderColor: "#707070",
                  borderWidth: 1.2,
                  marginLeft: "1rem",
                  marginRight: "2rem",
                }}>
                <Box
                  display='flex'
                  flexWrap='wrap'
                  alignContent='center'
                  bgcolor='background.paper'
                  css={{ maxWidth: "100%", height: "100%" }}>
                  <Dropzone
                    onDrop={(acceptedFiles) => {
                      var tmppath = URL.createObjectURL(acceptedFiles[0]);
                      props.setAvtarImage(tmppath);
                      console.log(tmppath);
                      let file = acceptedFiles[0];
                      setFile(file);
                      console.log("this is deaggg ile :: ", file);
                      setAvatar(tmppath);
                    }}>
                    {({ getRootProps, getInputProps }) => (
                      <section>
                        <div {...getRootProps()}>
                          <input {...getInputProps()} />
                          {avatar === null ? (
                            <Typography
                              className={classes.txt4}
                              variant='span'
                              gutterBottom>
                              Drag and drop your photo here
                            </Typography>
                          ) : (
                            <img style={{ width: "75%" }} src={avatar}></img>
                          )}
                        </div>
                      </section>
                    )}
                  </Dropzone>
                </Box>
              </Grid>
            </Grid>
          </Grid>
          <Grid item md={1}></Grid>
          <Grid item xs={12} md={5}>
            <Grid container>
              <Grid item xs={12}>
                <Typography
                  className={classes.txt1}
                  variant='subtitle1'
                  align='left'
                  gutterBottom>
                  Tips for an amazing photo
                </Typography>
              </Grid>
              <Grid item xs={4}>
                <img src={Image1} />
              </Grid>
              <Grid item xs={4}>
                <img src={Image2} />
              </Grid>
              <Grid item xs={4}>
                <img src={Image3} />
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{}}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{}}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      Smile and look at the camera
                    </p>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      Frame your head and shoulders
                    </p>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      Your photo is centered and upright
                    </p>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      Use neutral lighting and background
                    </p>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      Your face and eyes are fully visible <br></br>(except for
                      religious reasons)
                    </p>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      Avoid logos or contact information
                    </p>
                  </Box>
                </Box>
              </Grid>
              <Grid item xs={12}>
                <Box display='flex'>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <img src={Tick} />
                  </Box>
                  <Box p={1} style={{ paddingTop: 0 }}>
                    <p
                      style={{
                        fontSize: 17,
                        paddingTop: 0,
                        margin: 0,
                        color: "#6F757B",
                      }}>
                      You are the only person in the photo
                    </p>
                  </Box>
                </Box>
              </Grid>
            </Grid>
          </Grid>
        </Grid>

        <Box display='flex' justifyContent='flex-start'>
          <Button
            variant='contained'
            className={classes.buttonNext}
            onClick={() => onSubmit()}>
            {loading === false ? "Next" : <CircularProgress size={25} />}
          </Button>
        </Box>
      </Container>
    </div>
  );
}
