import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import { FormGroup } from '@material-ui/core';
import axios from 'axios';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import DirectionsIcon from '@material-ui/icons/Directions';
import BaseUrl from '../../baseurl';

import {
  Link,
  Switch,
  Route,
  BrowserRouter,
  Redirect,
  useHistory,
} from 'react-router-dom';
import { useSelector } from 'react-redux';
import { landingPageTop, landingPageLanguages } from './translation';
const useStyles = makeStyles((theme) => ({
  formControl: {
    marginTop: theme.spacing(2),
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

function getWindowDimensions() {
  const { innerWidth: width, innerHeight: height } = window;
  return {
    width,
    height,
  };
}

export default function CustomizedInputBase() {
  const history = useHistory();
  const classes = useStyles();
  const [warningText, setWarningText] = React.useState('');
  const [languages, setLanguages] = React.useState(['']);
  const language = useSelector((state) => state.languageReducer.language);
  const [selectedLanguage, setSelectedLanguage] = React.useState('');
  const [logedUser, setLoggedUser] = React.useState('');
  const [windowDimensions, setWindowDimensions] = React.useState(
    getWindowDimensions()
  );
  const [displayLanguages, setDisplayLanguages] = React.useState([]);
  const getUser = async () => {
    let user = await localStorage.getItem('role');

    setLoggedUser(user);
  };
  useEffect(() => {
    getUser();
  }, []);

  useEffect(() => {
    function handleResize() {
      setWindowDimensions(getWindowDimensions());
    }
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleChange = async (event, value) => {
    // console.log('aaaaa event: ', event);
    if (value) {
      console.log('aaaaa value: ', value);
      setSelectedLanguage(value.value);
      let name = value.title.split(' ')[0];
      if (logedUser === 'student') {
        await localStorage.setItem('language', name);
        history.push('/requestTutorHome', { language: name });
      } else if (logedUser != 'tutor') {
        history.push('/login');
      }
    }
  };

  const getCoursesData = async () => {
    axios
      .get('https://yellowschool.world/api/courses')
      .then((data) => {
        console.log(data.data.data);
        setLanguages(data.data.data);
      })
      .catch((e) => {
        setWarningText('No data');
        setLanguages(['']);
      });
  };
  useEffect(() => {
    getCoursesData();
  }, []);

  useEffect(() => {
    setLanguageValues();
  }, [language]);

  const setLanguageValues = () => {
    const newSelectedLanguages = landingPageLanguages.map((number) => {
      return {
        title: number[language],
        value: number['English'],
      };
    });
    setDisplayLanguages(newSelectedLanguages);
  };

  return (
    <div>
      <FormControl
        variant='outlined'
        className={classes.formControl}
        style={{
          height: '56px',
          width: '',
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
        }}
      >
        {/* <InputLabel id="demo-simple-select-outlined-label">Language</InputLabel> */}
        {/* <Select
          labelId='demo-simple-select-outlined-label'
          id='demo-simple-select-outlined'
          value={selectedLanguage}
          onChange={handleChange}
          displayEmpty
          style={{
            width: windowDimensions.width < 900 ? '' : '26vw',
            height: '56px',
            background: 'white',
            border: 'none',
          }}
        >
          <MenuItem value=''> */}
        {/* Choose Language */}
        {/* {landingPageTop[7][language]}
          </MenuItem>
          {landingPageLanguages.map((number) => (
            <MenuItem value={number['English']}>{number[language]}</MenuItem>
          ))}
        </Select> */}

        <Autocomplete
          id='combo-box-demo'
          options={displayLanguages}
          getOptionLabel={(option) => option.title}
          style={{
            width: windowDimensions.width < 900 ? '' : '20vw',
            height: '56px',
            background: 'white',
            border: 'none',
          }}
          value={selectedLanguage}
          onChange={handleChange}
          autoSelect={true}
          closeIcon={null}
          popupIcon={
            <IconButton
              type='submit'
              className={classes.iconButton}
              style={{
                background: '#FFB600',
                borderRadius: '5px',
                marginTop: '-0.7rem',
                marginRight: '-0.3rem',
                // display: 'inline',
                // height: '56px',
                // marginLeft: '-6px',
                // marginBottom: '3px',
              }}
            >
              <SearchIcon style={{ fill: 'white' }} />
            </IconButton>
          }
          renderInput={(params) => (
            <TextField
              {...params}
              label={landingPageTop[7][language]}
              variant='filled'
            />
          )}
        />
        {/* <IconButton
          type='submit'
          className={classes.iconButton}
          aria-label='search'
          style={{
            background: '#FFB600',
            borderRadius: '5px',
            display: 'inline',
            height: '56px',
            marginLeft: '-6px',
            marginBottom: '3px',
          }}
        >
          <SearchIcon style={{ fill: 'white' }} />
        </IconButton> */}
      </FormControl>
    </div>
  );
}
