function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  TextLabelLarge: {
    fontSize: 35,
  },
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  TextLabelMedium: {
    fontSize: 35,
  },
  threeRowsColOne: {
    fontSize: 20,
    width: "70%",
    height: "100%",
    textAlign: "left",
  },
  smallTextLeft: { fontSize: 13, width: "60%", textAlign: "left" },
  cardAdj: {
    background: "#FFF2D3",
    width: 230,
    height: 200,
    borderRadius: 5,
    marginTop: -208,
    marginRight: 25,
    [theme.breakpoints.down("sm")]: {
      marginTop: -50,
    },
  },
}));

const Accordion = withStyles({
  root: {
    border: "1px solid rgba(0, 0, 0, .125)",
    boxShadow: "none",
    "&:not(:last-child)": {
      borderBottom: 0,
    },
    "&:before": {
      display: "none",
    },
    "&$expanded": {
      margin: "auto",
    },
  },
  expanded: {},
})(MuiAccordion);

const AccordionSummary = withStyles({
  root: {
    backgroundColor: "white",
    borderBottom: "1px solid rgba(0, 0, 0, .125)",
    marginBottom: -1,
    minHeight: 26,
    "&$expanded": {
      minHeight: 26,
    },
  },
  content: {
    "&$expanded": {
      margin: "2px 0",
    },
    margin: "2px 0",
  },
  expanded: {},
})(MuiAccordionSummary);

const AccordionDetails = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiAccordionDetails);

