import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import Avatar from '@material-ui/core/Avatar';
import TimezonePicker from 'react-bootstrap-timezone-picker';
import 'react-bootstrap-timezone-picker/dist/react-bootstrap-timezone-picker.min.css';
import PhoneInput from 'react-phone-number-input';
import 'react-phone-number-input/style.css';
import FacebookLogo from '../assets/images/facebookLogo.png';
import GoogleLogo from '../assets/images/googleLogo.png';

import NavBar from '../components/nav/topNav';
import SubNavBar from '../components/nav/SubHeaderTutor';
import Footer from '../components/footer/footer';
import SideButtons from '../components/Settings/SideButtons';
import Password from '../components/Settings/password';
import PaymentMethod from '../components/Settings/paymentMethod';
import PaymentHistory from '../components/Settings/paymentHistory';
import AutoConformation from '../components/Settings/autoConformation';
import Calender from '../components/Settings/calender';
import ResumeUpdate from '../components/Settings/resume';
import Notification from '../components/Settings/notification';
import About from '../components/Settings/aboutUpdate';
import AlertSnackBar from '../components/AlertSnackBar/alertSnackBar';
import { UPDATE_ABOUT, UPDATE_IMAGE } from '../Constants/sagaActions';
import axios from 'axios';

const useStyles = makeStyles((theme) => ({
  TextLabelLarge: {
    fontSize: 35,
  },
  card: {
    background: 'white',
    marginBottom: 20,
    borderRadius: 5,
    padding: '2rem',
  },
  links: {
    marginBottom: 0,
    marginLeft: 10,
    color: 'white',
    cursor: 'pointer',
  },
  buttons: {
    border: 'none',
    borderRadius: 2,
    padding: 10,
    width: 130,
    textAlign: 'left',
  },
  buttonsFilled: {
    background: 'rgba(0,0,0,0.2)',
    border: 'none',
    borderRadius: 2,
    textAlign: 'left',
    padding: 10,
    width: 130,
  },
  saveButton: {
    background: '#FFB600',
    color: 'black',
    border: 'none',
    borderRadius: 2,
    width: 130,
  },
  deletButton: {
    border: '1px solid #F8463D',
    borderRadius: 2,
    color: '#F8463D',
    marginLeft: 20,
    cursor: 'pointer',
    width: 130,
  },

  textField: {
    width: '90%',
    borderRadius: 2,
    height: 40,
    border: '1px solid gray',
    marginRight: 20,
  },
  dataFieldContainer: {
    marginTop: 10,
  },
  textLeft: {
    textAlign: 'left',
    paddingLeft: 20,
  },
  buttonSelect: {
    color: '#384047',
    backgroundColor: '#DADFE1',
    width: '80%',
    textAlign: 'left',
    fontSize: 17,
    padding: 8,
    paddingLeft: 15,
  },
  buttonUnSelect: {
    color: '#6F757B',
    width: '80%',
    textAlign: 'left',
    fontSize: 17,

    paddingLeft: 15,
  },
}));

export default function Settings({ history }) {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.tutorInformation.tutorProfile) ||
    JSON.parse(localStorage.getItem('yellowTutor')) || {
      email: '',
      firstName: '',
      lastName: '',
      imageUrl: null,
    };

  const classes = useStyles();
  const [avatar, setAvatar] = useState('');
  const [isAccount, setIsAccount] = useState(false);
  const [file, setFile] = useState(null);
  const [value, setValue] = useState();
  const [userEmail, setUserEmail] = useState('');
  const [userFirstName, setUserFirstName] = useState('');
  const [userLastName, setUserLastName] = useState('');
  const [userId, setUserId] = useState({});
  const [loading, setLoading] = useState(false);
  const [showError, setShowError] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [fieldValues, setFieldValues] = useState({
    email: user['email'],
    firstName: user['firstName'],
    lastName: user['lastName'],
    timeZone: user['timeZone'] || 'Europe/Moscow',
  });
  const [openAlert, setOpenAlert] = useState(false);
  const [showPage, setShowPage] = useState('account');

  const updatingAbout = useSelector(
    (state) => state.tutorInformation.updatingAbout
  );
  const updatedAbout = useSelector(
    (state) => state.tutorInformation.updatedAbout
  );

  useEffect(() => {
    getUser();
  }, []);

  const getUser = async () => {
    setShowError(false);
    const token = await localStorage.getItem('yellowToken');
    setLoading(true);
    await axios
      .get('https://yellowschool.world/api/tutors/get-profile', {
        headers: {
          Authorization: token,
        },
      })
      .then((data) => {
        console.log('This is uusserrraaxxiiooss resume :: ', data.data.data);
        setLoading(false);
        setUserId(data.data.data._id);
        setUserFirstName(data.data.data.firstName);
        setUserLastName(data.data.data.lastName);
        setAvatar(data.data.data.imageUrl);
        setUserEmail(data.data.data.email);
      })
      .catch((e) => {
        setLoading(false);
      });
  };

  const onUpdateUserData = async () => {
    setLoading(true);
    setShowError(false);
    let dataObj = {
      firstName: userFirstName,
      lastName: userLastName,
    };
    await axios
      .put('https://yellowschool.world/api/tutors/about/' + userId, dataObj)
      .then(async (res) => {
        //   let allLessons = [];

        console.log('This is response updated tutor resume  :: ', res);

        setLoading(false);
      })
      .catch((error) => {
        console.log('This is response updated errorrr  :: ', error);
        setLoading(false);
      });

    if (file) {
      var bodyFormData = new FormData();
      bodyFormData.set('image', file);

      await axios
        .put(
          'https://yellowschool.world/api/tutors/addImage/' + userId,
          bodyFormData
        )
        .then((a) => {
          console.log('IImmmaaaggeee is uploaded');
        })
        .catch((e) => console.log('IImmmaaaggeee is errorr  :; ', e));
    }
  };

  const handleFile = (e) => {
    var reader = new FileReader();
    var file = e.target.files[0];
    var tmppath = URL.createObjectURL(e.target.files[0]);
    console.log(tmppath, 'file', file);
    setFile(file);
    setAvatar(tmppath);
  };

  const fileSelector = document.createElement('input');
  fileSelector.setAttribute('type', 'file');
  console.log('from file selector', fileSelector);

  const onChange = (e) => {
    console.log('e', e.target.value);
    const values = { ...fieldValues };
    values[e.target.name] = e.target.value;
    setFieldValues({ ...values });
  };

  const onTimeZoneChange = (value) => {
    const values = { ...fieldValues };
    values['timeZone'] = value;
    setFieldValues({ ...values });
  };

  const saveSettings = () => {
    if (fieldValues['firstName'] !== '' && fieldValues['lastName']) {
      dispatch({ type: UPDATE_ABOUT, payload: fieldValues });
    }
    if (file) {
      dispatch({ type: UPDATE_IMAGE, payload: file });
    }
  };

  const onClose = () => {
    setOpenAlert(false);
  };

  const onDeleteAccount = () => {
    setLoading(true);
    setShowError(false);
    let dataObj = { email: userEmail };
    axios
      .post('https://yellowschool.world/api/auth/deleteaccount', dataObj)
      .then(async (a) => {
        setLoading(false);
        await localStorage.removeItem('yellowToken');
        await localStorage.removeItem('yellowTutor');
        await localStorage.removeItem('yellowStudent');
        await localStorage.removeItem('role');
        history.push('/login');
      })
      .catch((e) => {
        console.log('IImmmaaaggeee is errorr  :; ', e);
        setLoading(true);
        setShowError(true);
        setErrorMessage('Account not deleted.');
      });
  };

  return (
    <div>
      <NavBar></NavBar>
      <SubNavBar for='Settings' />
      <div style={{ background: '#EDEFF0', paddingBottom: '4rem' }}>
        <Grid container>
          <Grid item md={2} lg={2} xl={2} sm={2} xs={2}></Grid>
          <Grid item md={8} lg={8} xl={8} sm={10} xs={12}>
            <Grid container style={{ marginTop: 20, marginBottom: 20 }}>
              <Grid item md={3} lg={3} xl={3} sm={3} xs={3}>
                <p
                  className={
                    showPage === 'account'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  onClick={() => setShowPage('account')}
                  style={{ cursor: 'pointer' }}
                >
                  Account
                </p>
                <p
                  className={
                    showPage === 'password'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  onClick={() => setShowPage('password')}
                  style={{ cursor: 'pointer' }}
                >
                  Password
                </p>
                <p
                  className={
                    showPage === 'paymentmethod'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  onClick={() => setShowPage('paymentmethod')}
                  style={{ cursor: 'pointer' }}
                >
                  Payment methods
                </p>
                <p
                  className={
                    showPage === 'paymenthistory'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  onClick={() => setShowPage('paymenthistory')}
                  style={{ cursor: 'pointer' }}
                >
                  Payment history
                </p>
                <p
                  className={
                    showPage === 'autoconfirmation'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  onClick={() => setShowPage('autoconfirmation')}
                  style={{ cursor: 'pointer' }}
                >
                  Autoconfirmation
                </p>
                <p
                  className={
                    showPage === 'calendar'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  style={{ cursor: 'pointer' }}
                  onClick={() => setShowPage('calendar')}
                >
                  Calendar
                </p>
                <p
                  className={
                    showPage === 'notification'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  style={{ cursor: 'pointer' }}
                  onClick={() => setShowPage('notification')}
                >
                  Notifications
                </p>
                <p
                  className={
                    showPage === 'resumeupdate'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  style={{ cursor: 'pointer' }}
                  onClick={() => setShowPage('resumeupdate')}
                >
                  Update Resume
                </p>
                <p
                  className={
                    showPage === 'about'
                      ? classes.buttonSelect
                      : classes.buttonUnSelect
                  }
                  style={{ cursor: 'pointer' }}
                  onClick={() => setShowPage('about')}
                >
                  Update About
                </p>
              </Grid>
              {showPage === 'account' ? (
                <Grid item md={9} lg={9} xl={9} sm={9} xs={9}>
                  <div className={classes.card}>
                    <p
                      style={{
                        textAlign: 'left',
                        padding: 10,
                        fontSize: 22,
                        borderBottom: '1px solid #F2F2F2',
                      }}
                    >
                      {' '}
                      Account Information
                    </p>
                    <form model={user}>
                      <Grid container>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>Profile Details</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid container>
                            {' '}
                            <Grid item md={4} lg={4} xl={4} sm={12} xs={12}>
                              <Avatar
                                src={avatar}
                                variant='square'
                                style={{ background: '#54BAC3', marginTop: 0 }}
                              >
                                H
                              </Avatar>
                            </Grid>
                            <Grid item md={8} lg={8} xl={8} sm={12} xs={12}>
                              <div className='upload-btn-wrapper'>
                                <button className='btn'>Upload a file</button>
                                <input
                                  type='file'
                                  onChange={handleFile}
                                  name='myfile'
                                />
                              </div>
                            </Grid>
                          </Grid>
                        </Grid>
                      </Grid>

                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>First name</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <input
                              className={classes.textField}
                              type='text'
                              name='firstName'
                              value={userFirstName}
                              onChange={(e) => setUserFirstName(e.target.value)}
                            ></input>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>Last name</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <input
                              className={classes.textField}
                              type='text'
                              name='lastName'
                              value={userLastName}
                              onChange={(e) => setUserLastName(e.target.value)}
                            ></input>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>Email</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <input
                              className={classes.textField}
                              name='email'
                              type='email'
                              value={fieldValues['email']}
                              onChange={onChange}
                              disabled
                            ></input>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>Phone number</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <PhoneInput
                              className={classes.textField}
                              placeholder='Enter phone number'
                              value={value}
                              onChange={setValue}
                            />
                            {/* <input
                         
                          type="text"
                          type="email"
                        ></input> */}
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>Skype ID</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <input
                              className={classes.textField}
                              type='text'
                            ></input>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}>
                          <p className={classes.textLeft}>Timezone</p>
                        </Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <TimezonePicker
                              absolute={false}
                              name='timeZone'
                              className={classes.textField}
                              value={fieldValues['timeZone']}
                              placeholder='Select timezone...'
                              onChange={onTimeZoneChange}
                            />
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}></Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <p>
                              {' '}
                              <img
                                style={{ paddingRight: 5 }}
                                src={FacebookLogo}
                              ></img>
                              Not connected to Facebook account
                            </p>
                            <p
                              style={{
                                cursor: 'pointer',
                                color: '#FFB600',
                                marginTop: -15,
                              }}
                            >
                              Connect
                            </p>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}></Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid
                            container
                            direction='column'
                            alignItems='flex-start'
                          >
                            <p>
                              <img
                                style={{ paddingRight: 5 }}
                                src={GoogleLogo}
                              ></img>
                              Not connected to Google account
                            </p>
                            <p
                              style={{
                                cursor: 'pointer',
                                color: '#FFB600',
                                marginTop: -15,
                              }}
                            >
                              Connect
                            </p>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid container className={classes.dataFieldContainer}>
                        <Grid item md={4} lg={4} xl={4} sm={4} xs={4}></Grid>
                        <Grid item md={8} lg={8} xl={8} sm={8} xs={8}>
                          <Grid container direction='row'>
                            <Button
                              className={classes.saveButton}
                              onClick={onUpdateUserData}
                            >
                              {loading ? (
                                <CircularProgress size={25} />
                              ) : (
                                'Save settings'
                              )}
                            </Button>
                            <Button
                              className={classes.deletButton}
                              onClick={onDeleteAccount}
                            >
                              Delete Account
                            </Button>
                          </Grid>
                        </Grid>
                      </Grid>
                    </form>
                  </div>
                </Grid>
              ) : null}
              <Grid item md={9} lg={9} xl={9} sm={12} xs={12}>
                {showPage === 'password' ? <Password /> : null}
                {showPage === 'paymentmethod' ? <PaymentMethod /> : null}
                {showPage === 'paymenthistory' ? <PaymentHistory /> : null}
                {showPage === 'autoconfirmation' ? <AutoConformation /> : null}
                {showPage === 'calendar' ? <Calender /> : null}
                {showPage === 'notification' ? <Notification /> : null}
                {showPage === 'resumeupdate' ? <ResumeUpdate /> : null}
                {showPage === 'about' ? <About /> : null}
              </Grid>
            </Grid>
          </Grid>
          <AlertSnackBar
            open={openAlert}
            message='Setting Updated..'
            onClose={onClose}
          />
          <Grid item md={2} lg={2} xl={2} sm={1} xs={1}></Grid>
        </Grid>
      </div>
      <div>
        <Footer></Footer>
      </div>
    </div>
  );
}
