import React from 'react';
import Slider from 'infinite-react-carousel';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Grid, Hidden } from '@material-ui/core';
import Girl from '../../assets/w1.png';
import SearchIcon from '../../assets/images/search.png';
import CalenderIcon from '../../assets/images/calendar.png';
import MoneyIcon from '../../assets/images/money.png';
import { Link, useHistory } from 'react-router-dom';
import InfoIcon from '../../assets/images/info.png';
import { landingPageRequestPrivateTutor } from '../../Translation/index';
import { useSelector } from 'react-redux';

const useStyles = makeStyles((theme) => ({
  centralCard: {
    marginTop: 50,
    background: 'white',
    borderRadius: 5,
    [theme.breakpoints.down('sm')]: {
      height: 800,
    },
  },
  showimg: {
    [theme.breakpoints.down('sm')]: {
      display: 'none',
    },
  },
  boldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 'bold',
  },
  semiBoldText: {
    fontFamily: 'Roboto',
    fontSize: 30,
    fontWeight: 500,
  },
  CommentPic: {
    height: '30vw',
    width: '41vw',
  },
  girlPicMobile: {
    [theme.breakpoints.down('sm')]: {
      width: '80%',
      height: 300,
      objectFit: 'contain',
    },
  },
  yellowButton: {
    padding: 10,
    color: 'white',
    borderRadius: 5,
    background: '#FFB600',
  },
  yellowText: {
    padding: 10,
    color: '#FFB600',
  },
  navLinks: {
    color: 'white',
    // marginLeft: 20,
    fontFamily: 'Roboto',
    cursor: 'pointer',
  },
  IconAndTextRow: { fontSize: 18, paddingTop: -1, marginLeft: '1rem' },
}));

export default function TutorWithYellow() {
  const classes = useStyles();
  const language = useSelector((state) => state.languageReducer.language);

  return (
    <div style={{ background: '#EDEFF0' }}>
      <div>
        <Grid container>
          <Grid item md={1} lg={1} xl={1}></Grid>
          <Grid
            item
            md={10}
            lg={10}
            xl={10}
            sm={12}
            xs={12}
            style={{ margin: '1rem' }}
          >
            <div className={classes.centralCard}>
              <Hidden only={['lg', 'md', 'xl']}>
                <img className={classes.girlPicMobile} src={Girl}></img>
              </Hidden>

              <Grid container direction='row'>
                <Grid
                  item
                  md={6}
                  lg={6}
                  xl={6}
                  style={{ textAlign: 'left', padding: 20 }}
                >
                  <p className={classes.semiBoldText}>
                    {/* Tutor with Yellow School */}
                    {landingPageRequestPrivateTutor[10][language]}
                  </p>
                  <p>
                    {/* Earn money sharing your expert knowledge with students. Sign
                    upto start tutoring online with Yellow school. */}
                    {landingPageRequestPrivateTutor[11][language]}
                  </p>
                  <p></p>

                  <Grid
                    container
                    direction='row'
                    style={{ height: 25 }}
                    alignItems='center'
                  >
                    <img src={SearchIcon} style={{ width: '1.4rem' }}></img>
                    <span className={classes.IconAndTextRow}>
                      {/* Find new students */}
                      {landingPageRequestPrivateTutor[12][language]}
                    </span>
                  </Grid>
                  <p></p>

                  <Grid
                    container
                    direction='row'
                    style={{ height: 25 }}
                    alignItems='center'
                  >
                    <img src={CalenderIcon} style={{ width: '1.4rem' }}></img>
                    <span className={classes.IconAndTextRow}>
                      {/* Grow your business */}
                      {landingPageRequestPrivateTutor[13][language]}
                    </span>
                  </Grid>
                  <p></p>
                  <Grid
                    container
                    direction='row'
                    style={{ height: 25 }}
                    alignItems='center'
                  >
                    <img src={MoneyIcon} style={{ width: '1.4rem' }}></img>
                    <span className={classes.IconAndTextRow}>
                      {/* Get paid securely */}
                      {landingPageRequestPrivateTutor[14][language]}
                    </span>
                  </Grid>
                  <Grid
                    container
                    direction='row'
                    style={{ height: 130 }}
                    alignItems='flex-end'
                  >
                    <p className={classes.yellowButton}>
                      <Link class={classes.navLinks} to='/becomeTutor'>
                        {/* Become a tutor */}
                        {landingPageRequestPrivateTutor[15][language]}
                      </Link>
                    </p>

                    {/* <p className={classes.yellowText}>
                      <img
                        style={{
                          marginBottom: -6,
                          marginRight: 5,
                          marginLeft: 5,
                        }}
                        src={InfoIcon}></img> */}
                    {/* How our platform works */}
                    {/* {landingPageRequestPrivateTutor[16][language]}
                    </p> */}
                  </Grid>
                </Grid>
                <Grid item md={6} lg={6} xl={6}>
                  <Box style={{ display: 'flex', justifyContent: 'flex-end' }}>
                    <img
                      className={classes.CommentPic}
                      src={Girl}
                      className={classes.showimg}
                    ></img>
                  </Box>
                </Grid>
              </Grid>
            </div>
          </Grid>
          <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
        </Grid>
      </div>
    </div>
  );
}
