import React, { useState, useEffect } from "react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import moment from "moment";
import "react-big-calendar/lib/css/react-big-calendar.css";
import Schedule from "./tutorSchedule";
import { Button, Box } from "@material-ui/core";
import FullCalendar from "@fullcalendar/react"; // must go before plugins
import dayGridPlugin from "@fullcalendar/daygrid"; // a plugin!
import CalenderUI from "./calenderDesign";

const localizer = momentLocalizer(moment);
const useStyles = makeStyles((theme) => ({
  modal: {},
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #ffcd02",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function TutorScheduleCalender(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  let [events, setEvents] = React.useState([]);

  const [ee, setEe] = useState([
    {
      title: "All Day Event very long title",
      allDay: true,
      start: new Date(2021, 6, 16),
      end: new Date(2021, 6, 16),
    },
    {
      title: "Long Event",
      start: new Date(2021, 6, 19),
      end: new Date(2021, 6, 19),
    },
  ]);
  const [dayName, setDayName] = React.useState("");
  const [lessonDate, setLessonDate] = React.useState("");
  const getDay = (e) => {
    const date = new Date(e);
    const day = date.getDay();
    var days = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday",
    ];
    setDayName(days[day]);
    setLessonDate(date);
    handleOpen();
  };
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  return (
    <div style={{ marginTop: "5rem" }}>
      <CalenderUI schedules={props.schedule} tutorDetail={props.tutorDetail} />
      {/* <Calendar
        events={ee}
        localizer={localizer}
        style={{ height: 500 }}
        getDrilldownView={(
          targetDate,
          currentViewName,
          configuredViewNames
        ) => {
          if (
            currentViewName === "month" &&
            configuredViewNames.includes("week")
          ) {
            return "month";
          }

          return null;
        }}
        onNavigate={(e) => getDay(e)}
      /> */}

      <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        className={classes.modal}
        open={open}
        style={{ width: "80%", height: "80%", margin: "10rem" }}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}>
        <Fade in={open}>
          <div className={classes.paper}>
            <h2 id='transition-modal-title'>Transition modal</h2>
            <div id='transition-modal-description'>
              <Schedule
                schedule={props.schedule}
                dayName={dayName}
                lessonDate={lessonDate}
                tutorDetail={props.tutorDetail}
              />
            </div>
            <Box display='flex' flexDirection='row'>
              <Box flex={1}></Box>
              <Box>
                <Button
                  style={{ color: "white", backgroundColor: "#ffcd02" }}
                  onClick={() => handleClose()}>
                  Close
                </Button>
              </Box>
            </Box>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
