import { callAPI } from "../Services/axios";

export const startConversationApi = async (data) => {
  const res = await callAPI({
    url: process.env.REACT_APP_START_CONVERSATION,
    method: "post",
    data,
  });
  return res;
};

export const allConversationsApi = async (data) => {
  const res = await callAPI({
    url: process.env.REACT_APP_ALL_CONVERSATIONS,
    method: "get",
    data,
  });
  return res;
};

export const newConversationApi = async (data) => {
  const res = await callAPI({
    url: process.env.REACT_APP_NEW_CONVERSATION,
    method: "post",
    data,
  });
  return res;
};

export const replyToConversationApi = async (data) => {
  const res = await callAPI({
    url: `${process.env.REACT_APP_REPLY_TO_CONVERSATION}/${data.conversationId}`,
    method: "post",
    data,
  });
  return res;
};

export const allMessagesApi = async (data) => {
  const res = await callAPI({
    url: `${process.env.REACT_APP_ALL_MESSAGES}/${data}`,
    method: "get",
    data,
  });
  return res;
};
