import React from "react";
import NavBar from "../components/nav/topNav";
import SubNavBar from "../components/nav/SubHeaderTutor";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Footer from "../components/footer/footer";
import { Invites } from "../Data/Data";

const useStyles = makeStyles((theme) => ({
  TextLabelLarge: {
    fontSize: 35,
  },
  card: {
    background: "white",
    marginTop: 20,
    marginBottom: 20,
    borderRadius: 5,
  },
  links: {
    marginBottom: 0,
    marginLeft: 10,
    color: "white",
    cursor: "pointer",
  },
}));

export default function BecomeTutorPage() {
  const classes = useStyles();
  return (
    <div>
      <NavBar></NavBar>
      <SubNavBar for="My Invites" />
      <div style={{ background: "#EDEFF0" }}>
        <Grid container direction="row" alignItems="center">
          <Grid item md={2} lg={2} xl={2} xs={2} sm={2}></Grid>
          <Grid item md={8} lg={8} xl={8} xs={8} sm={8}>
            <div className={classes.card}>
              {Invites.map((invite, index) => {
                return (
                  <Grid
                    container
                    direction="row"
                    alignItems="center"
                    style={{
                      borderBottom: "1px solid  #F2F2F2",
                      borderLeft:
                        index % 2 === 0 ? "4px solid #FFB600" : "none",
                      height: 60,
                    }}
                  >
                    <Grid item md={3} lg={3} xl={3} xs={3} sm={3}>
                      <img
                        style={{ width: 50, height: 50, borderRadius: 25 }}
                        src={invite.pic}
                      ></img>
                    </Grid>
                    <Grid item md={6} lg={6} xl={6} xs={6} sm={6}>
                      <p style={{ fontSize: 13 }}>{invite.message}</p>
                    </Grid>
                    <Grid item md={3} lg={3} xl={3} xs={3} sm={3}>
                      <p>{invite.date}</p>
                    </Grid>
                  </Grid>
                );
              })}
            </div>
          </Grid>
          <Grid item md={2} lg={2} xl={2} xs={2} sm={2}></Grid>
        </Grid>
      </div>
      <Footer></Footer>
    </div>
  );
}
