import React, {useState, useEffect} from "react";
import {useSelector, useDispatch} from "react-redux";
import Modal from "@material-ui/core/Modal";
import {makeStyles} from "@material-ui/core/styles";
import CircularProgress from "@material-ui/core/CircularProgress";
import {Grid, Box} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import NavBar from "../../components/nav/topNav";
import axios from "axios";
import Footer from "../../components/footer/footer";
import GoogleSquare from "../../assets/images/googleSquare.png";
import {LOGIN} from "../../Constants/sagaActions";
import {GoogleLogin} from "react-google-login";
import FacebookSquare from "../../assets/images/facebookSquare.png";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import baseUrl from "../../baseurl";

function rand() {
    return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
    const top = 50;
    const left = 50;

    return {
        top: `${top}%`,
        left: `${left}%`,
        transform: `translate(-${top}%, -${left}%)`,
    };
}

const useStyles = makeStyles((theme) => ({
    paper: {
        position: "absolute",
        width: 400,
        backgroundColor: theme.palette.background.paper,
        border: "2px solid #000",
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
    },
}));

const ResetPassword = ({history, match}) => {
    const classes = useStyles();
    const [modalStyle] = React.useState(getModalStyle);
    const [open, setOpen] = React.useState(false);
    const dispatch = useDispatch();
    const [password, setPassword] = useState("");
    const [reEnterPassword, setReEnterPassword] = useState("");
    const [error, setError] = useState(null);
    const [showError, setShowError] = useState(false);
    const [loading, setLoading] = useState(false);
    const [showSuccessMessage, setShowSuccessMessage] = useState(false);
    const [successMessage, setSuccessMessage] = useState("");
    const [errorMessage, setErrorMessage] = useState("");

    const loggingIn = useSelector((state) => state.auth.loggingIn);
    const user = useSelector((state) => state.auth.user);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    // const handleResetPassword = () => {
    //   axios({
    //     method: "post",
    //     url: "https://localhost:8080/api/auth/emailforgotpassword",
    //     data: { email: match.params.id },
    //   })
    //   .then(da => console.log('The rset emailllll successs :: ', da))
    //   .catch(e => console.log('The rest emaillll errorrrr :: ', e))
    // }
    const handleResetPassword = () => {
        setShowError(false);
        setLoading(true);
        if (password !== reEnterPassword) {
            setErrorMessage("Password not matched");
            setShowError(true);
            setLoading(false);
            return;
        }

        if (password.length <1 || reEnterPassword.length <1) {
            setErrorMessage("Fields cant be empty");
            setShowError(true);
            setLoading(false);
            return;
        }


        axios({
            method: "post",
            url: `${baseUrl}/api/auth/forgotpassword/${match.params.id}`,
            data: {resetPassword: password},
        })
            .then((da) => {
                setShowSuccessMessage(true);
                setSuccessMessage("Passowrd Updated");
                setLoading(false);
                history.push("/login");
            })
            .catch((e) => {
                setErrorMessage("Password not updated");
                showError(true);
                setLoading(false);
            });
    };

    return (
        <div>
            <NavBar/>
            <div
                style={{
                    display: "flex",
                    justifyContent: "center",
                    marginTop: "3rem",
                }}>
                <h2>Reset Password</h2>
            </div>
            <div style={{height: "100vh"}}>
                <Grid container style={{marginTop: "20px"}}>
                    <Grid item md={4} lg={4} xl={4} sm={2} xs={2}></Grid>
                    <Grid item md={4} lg={4} xl={4} sm={8} xs={8}>
                        {showError ? <p style={{color: "red"}}>{errorMessage}</p> : null}
                        <form style={{marginBottom: "1rem"}}>
                            <p style={{textAlign: "left"}}>New Password</p>
                            <input
                                style={{width: "100%", height: 45, padding: 10}}
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                type='password'></input>
                            <p style={{textAlign: "left", marginTop: "1rem"}}>
                                Confirm Password
                            </p>
                            <input
                                style={{width: "100%", height: 45, padding: 10}}
                                value={reEnterPassword}
                                onChange={(e) => setReEnterPassword(e.target.value)}
                                type='password'></input>
                            <Grid
                                container
                                direction='row'
                                alignItems='center'
                                justify='center'
                                style={{background: "#FFB600", marginTop: 15}}>
                                <Button
                                    onClick={handleResetPassword}
                                    style={{
                                        height: "100%",
                                        width: "100%",
                                        padding: "10px",
                                        background: "none",
                                        border: "none",
                                    }}>
                                    {loading ? <CircularProgress size={25}/> : "Reset"}
                                </Button>
                            </Grid>
                        </form>
                    </Grid>
                    <Grid item md={4} lg={4} xl={4} sm={2} xs={2}></Grid>
                </Grid>
            </div>
            <div style={{}}>
                <Footer/>
            </div>
        </div>
    );
};

export default ResetPassword;
