import React, { useState, useEffect } from 'react';
import { Grid, Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { LanguagesData } from '../../Data/Data';
import axios from 'axios';
import EnglishIcon from '../../assets/images/iconEnglish.png';
import CircularProgress from '@material-ui/core/CircularProgress';
import BaseUrl from '../../baseurl';

const useStyles = makeStyles((theme) => ({
  languagesCard: {
    marginTop: -60,
    background: 'white',
    borderRadius: 5,
  },
  languageName: {
    fontFamily: 'Roboto',
    fontSize: 25,
    fontWeight: 500,
    marginLeft: 15,
  },
  tutorNum: {
    fontFamily: 'Roboto',
    color: 'gray',
    marginTop: -1,
    fontSize: 13,
    width: '70%',
  },
}));

export default function LanguagesCard() {
  const classes = useStyles();
  const [allLanguages, setAllLanguages] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    axios
      .get(`${BaseUrl}/api/tutors/tutorCountByLanguage`)
      .then((data) => {
        let newArray = [];
        data.data.data.map((lang) => {
          let a = {
            language: lang.subjectTaught,
            icon: EnglishIcon,
            tutorNum: lang.count,
          };
          newArray.push(a);
        });
        setAllLanguages(newArray);
        setLoading(false);
      })
      .catch((e) => {
        console.log('This is dataaaaaaaaa errr :: ', e);
        setLoading(false);
      });
  }, []);
  return (
    <div style={{ background: '#EDEFF0', borderRadius: 5 }}>
      {loading ? <CircularProgress style={{ color: '#ffcd02' }} /> : null}
      <Grid container>
        <Grid item md={1} lg={1} xl={1}></Grid>
        <Grid
          item
          md={10}
          lg={10}
          xl={10}
          sm={12}
          xs={12}
          style={{ margin: '1rem' }}
        >
          <div className={classes.languagesCard}>
            <Grid container>
              {allLanguages.map((language) => {
                return (
                  <Grid
                    style={{
                      border: '1px solid #F2F2F2',
                      height: 150,
                      borderRadius: 5,
                    }}
                    item
                    md={4}
                    lg={4}
                    xl={4}
                    sm={12}
                    xs={12}
                  >
                    <Box
                      style={{
                        marginTop: 50,
                        width: '80%',
                      }}
                    >
                      <Box
                        style={{
                          display: 'flex',
                          justifyContent: 'flex-start',
                          marginLeft: '2rem',
                          alignItems: 'center',
                        }}
                      >
                        <img src={language.icon}></img>
                        <span className={classes.languageName}>
                          {language.language}{' '}
                          {language.language === 'Hindi' ? 'Language' : ''}{' '}
                          {language.language === 'Portuguese' ? 'Language' : ''}{' '}
                          {language.language === 'English' ? 'Language' : ''}
                        </span>
                      </Box>

                      <Box
                        style={{
                          display: 'flex',
                          marginLeft: '2rem',
                          marginTop: '1rem',
                        }}
                      >
                        <p>{language.tutorNum} tutors</p>
                      </Box>
                    </Box>
                  </Grid>
                );
              })}
            </Grid>
          </div>
        </Grid>
        <Grid item md={1} lg={1} xl={1} sm={1} xs={1}></Grid>
      </Grid>
    </div>
  );
}
