import { call, takeLatest, put, all, fork } from "redux-saga/effects";
import {
  sending,
  sendMessageSuccesss,
  sendMessageError,
  allConversationsLoading,
  allConversationsSuccesss,
  allConversationsError,
  newConversationLoading,
  newConversationSuccesss,
  newConversationError,
  replyToConversationLoading,
  replyToConversationSuccesss,
  replyToConversationError,
  allMessagesLoading,
  allMessagesSuccesss,
  allMessagesError,
} from "../slices/conversation.slice";
import {
  SEND_MESSAGE,
  ALL_CONVERSATIONS,
  NEW_CONVERSATION,
  REPLY_TO_CONVERSATION,
  ALL_MESSAGES,
} from "../../Constants/sagaActions";

import {
  startConversationApi,
  allConversationsApi,
  newConversationApi,
  replyToConversationApi,
  allMessagesApi,
} from "../../API/conversationApi";

export function* sendMessageSaga(action) {
  try {
    yield put(sending());
    const result = yield call(() => startConversationApi(action.payload));
    console.log("REsult of message", result);
    if (result.status === 200) {
      yield put(sendMessageSuccesss(result.data));
    } else {
      yield put(sendMessageError(result.data));
    }
  } catch (e) {
    yield put(sendMessageError(e));
  }
}

export function* watchSendMessageSaga() {
  yield takeLatest(SEND_MESSAGE, sendMessageSaga);
}

//all conversations
