export function* watchAllMessagesSaga() {
  yield takeLatest(ALL_MESSAGES, allMessagesSaga);
}

export default function* rootSaga() {
  yield all([
    fork(watchSendMessageSaga),
    fork(watchAllConversationsSaga),
    fork(watchNewConversationSaga),
    fork(watchReplyToConversationSaga),
    fork(watchAllMessagesSaga),
  ]);
}
